import React, { Component, Fragment } from 'react';
import { Helmet } from "react-helmet";
import { connect } from 'react-redux';
import QRCode from "react-qr-code";
import './assets/css/submit-pic.css'
import { submitPostData, showPopUP } from '../../actions/themeActions'
import { IS_EDITOR, IS_LIVE } from '../../actions/api'
import { checkEditorWithTags } from '../../utils'

const IMG_PATH = `https://test.taggbox.com/commerce/img/onsite-upload/`
const validateEmail = (email) => {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

class SubmitPic extends Component {
    state = {
        isOpen: false,
        step: 1,
        wall_id: null,
        name: null,
        email: null,
        upload_image: [
        ],
        receiveMail: false,
        tandc: true,
        nameError: false,
        emailError: false
    }
    componentDidMount() {
        const { wall } = this.props;
        if (checkEditorWithTags() && wall.UgcSettings.onsite_qr_code_status !== 0 && window.location.href && window.location.href.includes("onsite-upload")) {
            this.onLoadSubmitPopup()
        }
    }
    onShowCloseSubmitPicPopUp = event => {
        this.onLoadSubmitPopup()
    }
    onLoadSubmitPopup = () => {
        const { appendData, wall, onSite, startEmbed } = this.props;

        if (appendData.heightEvent != null) {
            var obj = {
                type: 'showPopUp',
                data: { type: 'submit-pic', card: {}, personalization: wall.Personalization, ThemeRule: wall.ThemeRule, UserRule: wall.UserRule, wall: wall, csrf_token: onSite.csrf_token, iframeId: this.props.startEmbed.heightEvent.data.id },
            }
            appendData.heightEvent.source.postMessage(obj, appendData.heightEvent.origin);
        }
        else {
            if (IS_EDITOR) {
                this.props.showPopUP({ type: 'submit-pic', card: {}, personalization: wall.Personalization, ThemeRule: wall.ThemeRule, UserRule: wall.UserRule, wall: wall, csrf_token: onSite.csrf_token })
            }
            // const { isOpen } = this.state;
            // this.setState({ isOpen: !isOpen })
        }
    }
    updateImageWithData = (data) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length < 5) {
            upload_image.push(data);
            this.setState({ upload_image })
        }
    }
    onUpdateImageAddCaption = (index) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0 && upload_image[index]) {
            upload_image[index].caption = event.target.value;
            upload_image[index].review = "";
            upload_image[index].rating = 0;
            this.setState({ upload_image })
        }
    }
    onUpdateImageAddReview = (index) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0 && upload_image[index]) {
            upload_image[index].review = event.target.value;
            upload_image[index].caption = "";
            this.setState({ upload_image })
        }
    }

    onUpdateImageAddRatings = (rating, index) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0 && upload_image[index]) {
            upload_image[index].caption = "";
            upload_image[index].rating = rating;
            this.setState({ upload_image })
        }
    }
    onUpdateStep = (step) => event => {
        this.setState({ step })
    }
    onTextName = event => {
        this.setState({ name: event.target.value, nameError: false }, () => {
            if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true })
        })
    }
    onTextEmail = event => {
        this.setState({ email: event.target.value, emailError: false }, () => {
            if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || this.state.email.includes("mailinator")) this.setState({ emailError: true })
        })
    }
    onSubmitData = event => {
        const { name, email, upload_image, tandc, receiveMail } = this.state;
        const { wallId } = this.props;
        if (tandc && (name && name.length > 0) && (email && email.length > 0 && validateEmail(email) && !email.includes("mailinator")) && (upload_image && upload_image.length > 0) && tandc) {
            submitPostData({ wall_id: wallId, name, email, upload_image, receive_mail: receiveMail }).then((response) => {
                this.setState({ step: 3 })
            })
                .catch((error) => {
                    console.error(error);
                });
        }
        else {
            if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true })
            if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || email.includes("mailinator")) this.setState({ emailError: true })
        }
    }
    onUpdatereceiveMail = event => {
        const { receiveMail } = this.state;
        this.setState({ receiveMail: !receiveMail })
    }
    onUpdatetandc = event => {
        const { tandc } = this.state;
        this.setState({ tandc: !tandc })
    }
    removeProductImage = (removeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0) {
            upload_image.splice(removeIndex, 1);
        }
        this.setState({ upload_image })
    }

    render() {

        const { isOpen, upload_image, step, name, email, receiveMail, tandc, nameError, emailError } = this.state;
        const { wall } = this.props;

        return <>
            <Helmet>
                {
                    wall.UgcSettings.onsite_status ? <link href={`https://fonts.googleapis.com/css?family=${wall.UgcSettings.onsite_link_font_btn}:${wall.UgcSettings.onsite_font_varient_btn}`} rel="stylesheet" /> : null
                }
                {
                    wall.UgcSettings.onsite_status ? <link href={`https://fonts.googleapis.com/css?family=${wall.UgcSettings.onsite_css_font_msg}:${wall.UgcSettings.onsite_font_varient_msg}`} rel="stylesheet" /> : null
                }
            </Helmet>
            {/* {checkEditorWithTags() && wall.UgcSettings.onsite_qr_code_status !== 0 ?
                <div id="tb_post_submit_container_button" className="tb_post_submit_container">
                    <div style={wall.UgcSettings.onsite_qr_code_status ? { backgroundColor: wall.UgcSettings.onsite_btn_color, borderColor: wall.UgcSettings.onsite_btn_color, color: wall.UgcSettings.onsite_btn_txt_color } : {}} className="tb_post_submit_btn" onClick={this.onShowCloseSubmitPicPopUp}>
                        {wall.UgcSettings.onsite_btn_icon || wall.UgcSettings.onsite_btn_icon != '' ? <div className={`tb-sp-font ${wall.UgcSettings.onsite_btn_icon}`}></div> : null}{wall.UgcSettings.onsite_qr_code_status ? wall.UgcSettings.onsite_btn_txt : 'Submit a Post'}
                    </div>
                </div> : null} */}

            {(checkEditorWithTags() && wall.UgcSettings.onsite_status == 1) ? <div id="tb_post_submit_container_button" className="tb_post_submit_container">
                {(wall.UgcSettings.onsite_qr_code_status == "1" || wall.UgcSettings.onsite_qr_code_btn_text != "") ? <div className='tb_post_submit_btn'>
                    <button style={wall.UgcSettings.onsite_qr_code_status ? { backgroundColor: wall.UgcSettings.onsite_btn_color, borderColor: wall.UgcSettings.onsite_btn_color, color: wall.UgcSettings.onsite_btn_txt_color } : {}} type='button' className='btn btn-scan' onClick={this.onShowCloseSubmitPicPopUp}>
                        {wall.UgcSettings.onsite_qr_code_status == 1 ? <> <span className='qr-image'>
                            <QRCode
                                level="L"
                                size={60}
                                value={`${IS_LIVE ? `https://widget.taggbox.com/${wall.Wall.id}?onsite-upload=true` : `https://test.taggbox.com/embed-widget/w?wall_id=${wall.Wall.id}&onsite-upload=true`}`}

                            />
                        </span>
                            {(wall.UgcSettings.onsite_qr_code_status && wall.UgcSettings.onsite_qr_code_btn_text != "") ? <span className='separator'></span> : null}</> : null}
                        {wall.UgcSettings.onsite_qr_code_status && wall.UgcSettings.onsite_qr_code_btn_text != "" ? <span className='qr-text'>

                            {wall.UgcSettings.onsite_btn_icon || wall.UgcSettings.onsite_btn_icon != '' ? <i className={`tb-sp-font ${wall.UgcSettings.onsite_btn_icon}`}></i> : null}
                            <span className={`sGFfonte-${wall.UgcSettings.onsite_font_varient_btn}`}
                                style={wall.UgcSettings || wall.UgcSettings.btn_fontSize || wall.UgcSettings.onsite_btn_icon != '' ? { fontSize: parseInt(wall.UgcSettings.btn_fontSize), fontFamily: wall.UgcSettings.onsite_css_font_btn } : {}}
                            >
                                {wall.UgcSettings.onsite_qr_code_status && wall.UgcSettings.onsite_qr_code_status ? wall.UgcSettings.onsite_qr_code_btn_text : wall.UgcSettings.onsite_qr_code_status ? wall.UgcSettings.onsite_qr_code_btn_text : wall.UgcSettings.onsite_qr_code_status ? wall.UgcSettings.onsite_btn_txt : 'Submit a Post'}
                            </span>
                        </span> : null}
                    </button>
                </div> : null}
            </div> : null}
        </>
    }
}


const mapDispatchToProps = dispatch => {
    return {
        showPopUP: (data) => dispatch(showPopUP(data)),
    }
}

const mapStateToProps = state => {
    return {
        wall: state.wall.wallData,
        postData: state.postData.postData,
        completeDataObject: state.postData.completeDataObject,
        appendData: state.postData.appendData,
        languageSetting: state.languageSetting,
        wallId: state.wallId.wallID,
        webFilters: state.webFilters.webFilters,
        onSite: state.onSite,
        startEmbed: state.startEmbed,
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(SubmitPic);