import React, { Component } from 'react';
import '../../css/loaders.css';
import { CUSTOM_SERVER_PATH, displayLoaderBackground } from '../../constants'
import Loader6 from './loader6'
import BrandLoader from './brandLoader'
const loaderBackgroundSettings = (BackgroundImagePath, ThemeRuleTransparent, error_code, Bg_Path, ThemeRuleBackgroundColor) => {
    return {
        backgroundImage: BackgroundImagePath !== null && ThemeRuleTransparent === 0 ? 'url(' + Bg_Path + ')' : null,
        backgroundColor: ThemeRuleTransparent === 1 ? 'transparent!important' : ThemeRuleBackgroundColor,
        backgroundRepeat: "repeat",
        backgroundSize: "100%",
        backgroundPosition: "center top",
        outline: "none",
        backgroundAttachment: 'fixed',
        display: displayLoaderBackground(error_code),
        scrollbarWidth: 'none'
    }

}
class LoaderComponent extends Component {
    render() {
        const { loader, isLoading, loaderTopStyle } = this.props;

        let backgroundStyle = loaderBackgroundSettings(loader.wall.BackgroundImage.path, loader.wall.ThemeRule.transparent, 0, `${CUSTOM_SERVER_PATH}${loader.wall.BackgroundImage.path}${loader.wall.BackgroundImage.newName}`, loader.wall.ThemeRule.backgroundColor)
        return loader.loader_type === 0 ? <BrandLoader isLoading={isLoading} loaderTopStyle={loaderTopStyle} backgroundStyle={backgroundStyle} /> : <div className={isLoading ? `loading` : ``} id="eloading" style={{ backgroundColor: '#fff!important', marginTop: loaderTopStyle != 0 ? `${loaderTopStyle}px` : 0 }}>

            <div className="loaderCenterlize">
                {loader.loader_type === 1 ? <div className="wrapLC loadercircle-side loader1c loaderno" style={{ borderColor: loader.loader_color1, borderTopColor: loader.loader_color2, borderRightColor: loader.loader_color2, borderBottomColor: loader.loader_color2 }}></div>

                    : loader.loader_type === 3 ? <div className="lds-double-ring">
                        <div id="dring1" style={{ borderTopColor: loader.loader_color1, borderBottomColor: loader.loader_color1 }}></div>
                        <div id="dring2" style={{ borderLeftColor: loader.loader_color2, borderRightColor: loader.loader_color2 }}></div>
                    </div>
                        : loader.loader_type === 2 ? <div className="lds-spinnerStar">
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                            <div style={{ background: loader.loader_color1 }}></div>
                        </div>
                            : loader.loader_type === 5 ? <div className="dotLoader">
                                <div className="dot-loader" style={{ backgroundColor: loader.loader_color1 }}></div>
                                <div className="dot-loader dot-loader--2" style={{ backgroundColor: loader.loader_color1 }}></div>
                                <div className="dot-loader dot-loader--3" style={{ backgroundColor: loader.loader_color1 }}></div>
                            </div>
                                : loader.loader_type === 6 ? <Loader6 />
                                    : loader.loader_type === 4 ? <span className="wrapLC loaderCube loader4c loaderno" style={{ borderColor: loader.loader_color2 }}><span className="loader-inner" style={{ backgroundColor: loader.loader_color1 }}></span></span> : null}
            </div>

        </div>

    }
}
export default LoaderComponent;