export const noPostErrorDisplayCss = (errorCode, err) => {
  return errorCode === 201
    ? ""
    : errorCode === 202
      ? ""
      : errorCode === 203
        ? ""
        : errorCode === 204
          ? ""
          : errorCode === 205
            ? ""
            : errorCode === 206
              ? ""
              : errorCode === 207
                ? ""
                : errorCode === 208
                  ? ""
                  : errorCode === 209
                    ? ""
                    : errorCode === 211
                      ? ""
                      : errorCode === 212
                        ? ""
                        : err === 404
                          ? ""
                          : "none";
};

export const displayLoaderBackground = (error_code) => (error_code === 203 || error_code === 207 || error_code === 201 || error_code === 202 || error_code === 204 || error_code === 205 || error_code === 206 || error_code === 208 || error_code === 209 || error_code === 211 || error_code === 212) ? 'none' : 'block'
export const renderScreenSize = (
  widgetTheme,
  highlight,
  isMobile,
  isTablet,
  adjustWidth,
  cardWidth,
  wall,
  numberOfCoumn
) => {
  let screen = isMobile && !isTablet ? "isMobile" : "default";

  switch (screen) {
    case "isMobile": {
      //if ((widgetTheme === 4 && wall.ThemeRule.numberOfCoumn > 0) || widgetTheme === 50) return highlight === 1 ? adjustWidth * 2 : adjustWidth >= cardWidth ? adjustWidth : adjustWidth;
      if (((widgetTheme === 4 || widgetTheme === 3) && numberOfCoumn > 0) || widgetTheme === 50) return highlight === 1 ? adjustWidth : adjustWidth >= cardWidth ? adjustWidth : adjustWidth;
      else return "100%";
    }
    // case 'isTablet': {
    //     return (adjustWidth >= cardWidth)?adjustWidth:cardWidth
    // }
    default:
      //return highlight === 1 ? adjustWidth * 2 : adjustWidth >= cardWidth ? adjustWidth : adjustWidth;
      return highlight === 1 ? adjustWidth * 2 : adjustWidth >= cardWidth ? adjustWidth : adjustWidth;

  }
};
export const IMG_DATA_FILTER = (data, link) => {
  if (data && data.length > 0) {
    return data.filter((item) => item.link == link)
  }
  else return [];
}
export const loaderTopHeightSetting = () => {
  let loaderTopStyle = 0;
  let wBannerImage = $("#wBannerImage").innerHeight();
  let networkfilter = $("#networkfilter").innerHeight();
  if (wBannerImage != undefined && parseInt(wBannerImage) != 0) loaderTopStyle = parseInt(wBannerImage);
  if (networkfilter != undefined && parseInt(wBannerImage) != 0) loaderTopStyle = loaderTopStyle + parseInt(networkfilter);
  return (loaderTopStyle > 0 && loaderTopStyle > 100) ? loaderTopStyle : 50;
};


