import React, { Component } from 'react';
import { isMobile, isTablet } from "react-device-detect";
import { Helmet } from "react-helmet";
import $ from 'jquery';
import { connect } from 'react-redux';
import '../../css/banner.css';
import BannerLogo from './bannerLogo'
import MiddleBanner from './middleBanner'
import BannerSocialAction from './bannerSocialAction'
import { CUSTOM_SERVER_PATH, defaultFinalPath } from '../../constants'
const defaultFinalLogo = "https://cloud.taggbox.com/widget/widget-embed/8651481539701.51210.png";
const MobileBanner = ({ CustomBanner, LogoImage, CustomBannerImage, wall }) => {
    return <div className="health-challenge-banner" style={{ minHeight: CustomBanner.banner_height, backgroundColor: CustomBanner.transparent != 0 ? '' : CustomBanner.background_color, backgroundImage: CustomBanner.transparent != 0 ? 'none' : (CustomBannerImage.newName === "8661481540843.08240" || CustomBannerImage.newName === 8661481540843.082) ? `url(${defaultFinalPath})` : `url(${CUSTOM_SERVER_PATH}${CustomBannerImage.path}${CustomBannerImage.newName})` }}>
        {LogoImage.newName != null ? <img src={(LogoImage.newName === 8651481539701.512 || LogoImage.newName === "8651481539701.51210") ? defaultFinalLogo : `${CUSTOM_SERVER_PATH}${LogoImage.path}${LogoImage.newName}`} alt="banner-logo" /> : null}
        <p className={`setFont sGFfonte-${CustomBanner.subtitle_font_varient}`} style={{ fontFamily: CustomBanner.subtitle_css_font, fontSize: CustomBanner.subTitle_font_size, color: CustomBanner.subTitle_font_color }}>{CustomBanner.subTitle}</p>
        <h1 className={`setFont sGFfonte-${CustomBanner.title_font_varient}`} style={{ fontFamily: CustomBanner.title_css_font, fontSize: CustomBanner.title_font_size, color: CustomBanner.title_font_color }}>{CustomBanner.title}</h1>
    </div>
}


class ThemeBanner extends Component {
    state = {
        windowWidth: $("#root").width()
    }
    componentDidMount() {
        window.addEventListener("resize", () => {
            this.setState({ windowWidth: $("#root").width() })
        });
    }

    render() {
        const { CustomBanner, CustomBannerImage, LogoImage, wall } = this.props;
        const { windowWidth } = this.state;
        return CustomBanner.status === 1 ? <React.Fragment>
            {CustomBanner.status === 1 && CustomBanner.bannerType == 5 ? <Helmet>

                <style type="text/css">{`#embedBannerImage .banner-5 .headerbannerRight:before, #wBannerImage .banner-5 .headerbannerRight:before, #wallBannerImage .banner-5 .headerbannerRight:before{ background:${CustomBanner.background_color_two}}`}</style>
            </Helmet> : null}
            {
                isMobile && !isTablet ? <MobileBanner CustomBanner={CustomBanner} LogoImage={LogoImage} CustomBannerImage={CustomBannerImage} wall={wall} /> : windowWidth < 767 ? <MobileBanner CustomBanner={CustomBanner} LogoImage={LogoImage} CustomBannerImage={CustomBannerImage} wall={wall} /> : (wall.Personalization.widgetTheme === 52 || wall.Personalization.widgetTheme === 49) ? <MobileBanner CustomBanner={CustomBanner} LogoImage={LogoImage} CustomBannerImage={CustomBannerImage} wall={wall} /> : <div className={CustomBanner.banner_position === 1 ? "bannerTop" : "bannerBottom"} id="wBannerImage">
                    <div className={`taggboxHeaderBanner banner-${CustomBanner.bannerType}`} style={{ height: CustomBanner.banner_height, backgroundColor: CustomBanner.transparent != 0 ? '' : CustomBanner.background_color, backgroundImage: CustomBanner.transparent != 0 ? 'none' : (CustomBannerImage.newName === "8661481540843.08240" || CustomBannerImage.newName === 8661481540843.082) ? `url(${defaultFinalPath})` : `url(${CUSTOM_SERVER_PATH}${CustomBannerImage.path}${CustomBannerImage.newName})` }}>
                        <div className="headBannerContent">
                            <div className="headerBannerLeft">
                                <div className="headBannerLogo">
                                    {LogoImage.newName != null ? <BannerLogo LogoImage={LogoImage} imagePath={`${CUSTOM_SERVER_PATH}${LogoImage.path}${LogoImage.newName}`} /> : null}
                                </div>
                            </div>
                            <MiddleBanner CustomBanner={CustomBanner} wall={wall} />
                            <div className={`headerbannerRight ${CustomBanner.transparent != 0 ? `customtransparent` : ``}`}>
                                <ul className="headbannerSocialIcon">
                                    <BannerSocialAction CustomBanner={CustomBanner} />
                                </ul>
                            </div>
                            <div className="clear"></div>
                        </div>
                    </div>
                </div>
            }
        </React.Fragment> : null

    }
}
const mapStateToProps = state => {
    return {
        wall: state.wall.wallData,
    }
}

export default connect(mapStateToProps)(ThemeBanner);
