import React, { Component } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import $ from "jquery";
import { ERROR_BACKGROUND_IMG, ERROR_ERROR, APPURL } from '../../../constants';

const styles = {
    addBtn: {
        border: '1px solid #2b2b2b',
        color: '#2b2b2b',
        fontSize: '14px',
        textTransform: 'uppercase',
        padding: '12px 20px',
        borderRadius: '5px', margin: 8
    },
    showDemoPost: {
        fontSize: '14px',
        textTransform: 'uppercase',
        padding: '12px 20px',
        borderRadius: '5px', margin: 8, color: '#fff',
        border: 'none'
    },
    demoWallErrorStyle: {
        maxWidth: '100%',
        width: '100%',
        left: 'auto',
        right: '0px',
        transform: 'none',
        bottom: 'auto',
        top: '0px',
        zIndex: 9998,
        position: 'relative',
        padding: '10px 15px',
        borderTop: '1px solid rgb(244, 244, 244)',
        borderRadius: '0px',
        textAlign: 'center',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: 'rgb(255, 255, 255)'
    },
    hideBtnStyle: {
        color: 'rgb(194, 14, 94)',
        textTransform: 'capitalize',
        background: 'transparent',
        borderWidth: '1px',
        borderStyle: 'solid',
        borderColor: 'rgb(194, 14, 94)',
        borderImage: 'initial',
        padding: '10px 15px',
        margin: '3px'
    },
    strongCss: {
        color: '#ff4051',
        fontWeight: 600,
        textTransform: 'capitalize'
    }
}
class Error extends Component {
    onClickToadd = event => {
        let data = [];
        data['type'] = 'create_feed';
        window.parent.postMessage(data, '*');
    }
    onClickToDemoPost = event => {
        const { wall } = this.props;
        let wallId = wall.Wall.id;
        let status = wall.Personalization.demoWall;
        $.ajax({
            url: `${APPURL}widget/wall/demoStatus/${wallId}/${status}`,
            cache: false,
            dataType: "json",
            timeout: (1000 * 60 * 7),
            success: function (response) {
                window.location.reload();
            }
        });

    }
    onClicktoUrl = (path) => event => window.top.location.href = path;
    render() {
        const { error, wall, innerHeight, errorWithMessage, startEmbed } = this.props;
        let editor = window.location.pathname.includes('/editor');

        return (errorWithMessage !== null ? <div className="col-md-12 text-center noPostSec" id="noPosts"
            style={{
                backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')'
                , height: innerHeight
            }}>
            <div className="noPostContent">
                <div className="noPostMsgBlock">
                    <a className="product-logo" target="_blank" href={`https://taggbox.com/widget/?utm_source=Not_active_logo&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=widget_not_active`}><img src="https://cloud.taggbox.com/widget/widget-embed/Taggbox_logo.svg" alt="logo" /> <span className="tb-pro-name">Widget</span></a>
                    {JSON.stringify(errorWithMessage)}
                    {(errorWithMessage.type === 'VISITOR_LIMIT_EXCEEDED') && <>
                        <h2>Page Views Limit Exceeded!</h2>
                        <p>Oops! Seems Page Views Limit for the plan exceeded. If you are the owner of this Taggbox account, please click below link to access your account.</p>
                        <a className="btn btn-lg btn-danger" target="_blank" href={`https://app.taggbox.com/widget/accounts/login?utm_source=Upgrade_now_button&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=page_views_exceeded`}>Upgrade Now</a>
                    </>}
                    {(errorWithMessage.type !== 'VISITOR_LIMIT_EXCEEDED') && <>
                        <h2>{errorWithMessage.message}</h2>
                        <p>Oops! Seems like something went wrong. If you are the owner of this Taggbox account, please click below link to access your account.</p>
                        <a className="btn btn-lg btn-danger" target="_blank" href={`https://app.taggbox.com/widget/accounts/login?utm_source=Access_now_button&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=widget_not_active`}>Access Now</a>
                    </>}
                </div>
            </div>
        </div>
            :
            <React.Fragment>
                {
                    error.error_code === 404 ? <React.Fragment>
                        <div className="col-md-12 text-center noPostSec" id="noPosts"
                            style={{
                                backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')'
                                , height: innerHeight,
                                minHeight: 400,
                            }}>
                            <div className="noPostContent">
                                <div className="noPostMsgBlock">
                                    <a className="product-logo" target="_blank" rel="noopener noreferrer" href={`https://taggbox.com/widget/?utm_source=Not_active_logo&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=widget_not_active`}><img src="https://cloud.taggbox.com/widget/widget-embed/Taggbox_logo.svg" alt="logo" /> <span className="tb-pro-name">Widget</span></a>
                                    <h2>{errorWithMessage.message}</h2>
                                    <p>Oops! Seems like something went wrong. If you are the owner of this Taggbox account, please click below link to access your account.</p>
                                    <a className="btn btn-lg btn-danger" target="_blank" href={`https://app.taggbox.com/widget/accounts/login?utm_source=Access_now_button&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=widget_not_active`}>Access Now</a>
                                </div>
                            </div>
                        </div>
                    </React.Fragment>
                        : error.error_code === 201 ? <React.Fragment>
                            <h2>No Posts!</h2>
                            <p>If Moderation is ON, then the posts might be in the moderation section and you need to approve them by going to MODERATION.</p>
                            <p>Else, there might not be any posts on feeds which you have added.</p>
                        </React.Fragment> : error.error_code === 202 ? <React.Fragment>
                            {wall.Personalization.demoWall === 1 && editor ? <div style={{ boxShadow: 'rgba(0, 0, 0, 0.3) 0px 3px 10px', ...styles.demoWallErrorStyle }}><h2 style={{ display: 'none' }}>No Feeds!</h2>
                                <p style={{
                                    fontFamily: 'Open Sans",sans-serif', fontSize: '18px',
                                    color: 'rgb(110, 112, 115)',
                                    fontWeight: 300,
                                    margin: '0px'
                                }}>It seems you have not added any Feed. Please Add Social Networks !</p><span style={{ padding: '0px 15px' }}><a href={() => false} className="loadDemoWall btn hideBtn" onClick={this.onClickToDemoPost}>HIDE DEMO POSTS</a></span></div> : <React.Fragment>
                                    <h2>No Feeds!</h2>
                                    <p style={{ fontFamily: 'Open Sans",sans-serif' }}>It seems you have not added any Feed. Please Add Social Networks !</p>
                                    {(!editor) && <a className="btn btn-lg btn-danger" target="_blank" href={`https://app.taggbox.com/widget/accounts/login?utm_source=Access_now_button&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=widget_not_active`}>Access Now</a>}
                                    {editor ? <div style={{ margin: '20px 0 0' }}>
                                        <a className="btn transBtn create-new-feed" style={styles.addBtn} onClick={this.onClickToadd} href={() => false}>
                                            Add Feed </a>
                                        <a href={() => false} className="loadDemoWall btn commanBtn" style={styles.showDemoPost} onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                    </div> : null}</React.Fragment>}



                        </React.Fragment> : error.error_code === 203 ? <React.Fragment>
                            {wall.Personalization.demoWall === 1 && editor ? <div style={{ boxShadow: 'rgba(0, 0, 0, 0.3) 0px 3px 10px', ...styles.demoWallErrorStyle }}><h2 style={{ display: 'none' }}>Feed Not Activated!</h2>
                                <p style={{
                                    fontFamily: 'Open Sans",sans-serif', fontSize: '18px',
                                    color: 'rgb(110, 112, 115)',
                                    fontWeight: 300,
                                    margin: '0px'
                                }}>It seems you have not actived any Feed. Please Activate feed from your <a className="manage-feed" onClick={this.onClicktoUrl(`${APPURL}widget/wall/index/${wall.Wall.id}`)} style={{ cursor: 'pointer' }} href={() => false}> wall editor</a></p><span style={{ padding: '0px 15px' }}><a href={() => false} className="loadDemoWall btn hideBtn" onClick={this.onClickToDemoPost}>HIDE DEMO POSTS</a></span></div> : <React.Fragment>
                                    <h2>Feed Not Activated!</h2>
                                    <p>It seems you have not actived any Feed. Please Activate feed from your
             <a onClick={this.onClicktoUrl(`${APPURL}widget/wall/index/${wall.Wall.id}`)} className="manage-feed" style={{ cursor: 'pointer' }} href={() => false}> wall editor</a>
                                    </p>
                                    {editor ? <div style={{ margin: '20px 0 0' }}>
                                        <a className="btn transBtn create-new-feed" style={styles.addBtn} onClick={this.onClickToadd} href={() => false}>
                                            Add Another Feed </a>
                                        <a href={() => false} className="loadDemoWall btn commanBtn" style={styles.showDemoPost} onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                    </div> : null}
                                </React.Fragment>}</React.Fragment> : error.error_code === 204 ?
                                        <React.Fragment>
                                            <h2>Feed Not Authorized!</h2>
                                            <p>It seems you have not authorized any Feed. Please <a target="_blank" href={`${APPURL}widget/wall/index/${wall.Wall.id}`} className="manage-feed" style={{ color: '#ff4051' }}>Click here</a> to authorize Feed !</p>
                                        </React.Fragment> : error.error_code === 205 ? <React.Fragment>
                                            {wall.Personalization.demoWall === 1 && editor ? <div style={{ boxShadow: 'rgba(0, 0, 0, 0.3) 0px 3px 10px', ...styles.demoWallErrorStyle }}><h2 style={{ display: 'none' }}>No {error.post_message}!</h2>
                                                <p style={{
                                                    fontFamily: 'Open Sans",sans-serif', fontSize: '18px',
                                                    color: 'rgb(110, 112, 115)',
                                                    fontWeight: 300,
                                                    margin: '0px'
                                                }}>It seems there are no {error.post_message} available on the entered <span style={styles.strongCss}>{ERROR_ERROR[`${error.older_post}`]}{error.older_days}</span>. All Upcoming posts will appear here.</p><span style={{ padding: '0px 15px' }}><a href={() => false} className="loadDemoWall btn hideBtn" onClick={this.onClickToDemoPost}>HIDE DEMO POSTS</a></span></div> :
                                                <React.Fragment>
                                                    <h2>No {error.post_message}!</h2>
                                                    <p>It seems there are no {error.post_message} available on the entered <span style={styles.strongCss}>{ERROR_ERROR[`${error.older_post}`]}{error.older_days}</span>. All Upcoming posts will appear here.</p>
                                                    <p className="tweets-or-section">OR</p>
                                                    <p>You can Click here to create another feed.</p>
                                                    {editor ? <div style={{ margin: '20px 0 0' }}>
                                                        <a className="btn transBtn create-new-feed" style={styles.addBtn} onClick={this.onClickToadd} href={() => false}>
                                                            Add Another Feed </a>
                                                        <a href={() => false} className="loadDemoWall btn commanBtn" style={styles.showDemoPost} onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                                    </div> : null}

                                                </React.Fragment>} </React.Fragment> : error.error_code === 206 ? <React.Fragment>
                                                    {wall.Personalization.demoWall === 1 && editor ? <div style={{ boxShadow: 'rgba(0, 0, 0, 0.3) 0px 3px 10px', ...styles.demoWallErrorStyle }}><h2 style={{ display: 'none' }}>No Tweets!</h2>
                                                        <p style={{
                                                            fontFamily: 'Open Sans",sans-serif', fontSize: '18px',
                                                            color: 'rgb(110, 112, 115)',
                                                            fontWeight: 300,
                                                            margin: '0px'
                                                        }}>It seems there are no tweets available on the entered <span style={styles.strongCss}>{ERROR_ERROR[`${error.older_post}`]}{error.older_days} </span>. All Upcoming tweets will appear here.</p><span style={{ padding: '0px 15px' }}><a href={() => false} className="loadDemoWall btn hideBtn" onClick={this.onClickToDemoPost}>HIDE DEMO POSTS</a></span></div> : <React.Fragment>
                                                            <h2>No Tweets!</h2>
                                                            <p>It seems there are no tweets available on the entered <span style={styles.strongCss}>{ERROR_ERROR[`${error.older_post}`]}{error.older_days}</span>. All Upcoming tweets will appear here.</p>
                                                            <p className="tweets-or-section">OR</p>
                                                            <p>Tweets are older than  days.</p>
                                                            {editor ? <div style={{ margin: '20px 0 0' }}>
                                                                <a className="btn transBtn create-new-feed" style={styles.addBtn} onClick={this.onClickToadd}>
                                                                    Add Another Feed </a>
                                                                <a href={() => false} className="loadDemoWall btn commanBtn" style={styles.showDemoPost} onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                                            </div> : null}
                                                        </React.Fragment>}</React.Fragment> : error.error_code === 207 ?
                                                    <React.Fragment>
                                                        <h2>No Posts!</h2>
                                                        <p>It seems your all posts are private. Please go to <a href={`https://app.taggbox.com/moderation/index/${wall.Wall.id}`} target="_blank" rel="noopener noreferrer">Moderation</a> and make some posts public.</p>
                                                    </React.Fragment> : error.error_code === 208 ?
                                                        <React.Fragment>
                                                            <h2>No Posts!</h2>
                                                            <p>It seems available posts are older than {error.older_days !== 8 ? `${error.older_days} Days` : moment(new Date(error.older_post * 1000)).format('LL')}
                                                                {'. '} All new posts will appear here.</p>
                                                        </React.Fragment> : error.error_code === 209 ?
                                                            <React.Fragment>
                                                                <h2>No Posts!</h2>
                                                                <p>It seems there are no posts with image and video.</p>
                                                            </React.Fragment> : error.error_code === 211 ?
                                                                <React.Fragment>
                                                                    <h2>No Posts!</h2>
                                                                    <p>It seems there are no posts available on the entered {ERROR_ERROR[`${error.older_post}`]}{error.older_days}. All Upcoming posts will appear here.</p>
                                                                    <p className="tweets-or-section">OR</p>
                                                                    <p>Post are older than 24 hours.</p>
                                                                </React.Fragment> : error.error_code === 212 ?
                                                                    <React.Fragment>
                                                                        <h2>No Posts!</h2>
                                                                        <p>Only newly posted Instagram {error.older_days} will be displayed. Unlike other feed sources, {error.older_days}  posted in the past will not be retrieved.</p>
                                                                    </React.Fragment> : null}
            </React.Fragment>)
    }

}
const mapStateToProps = state => {
    return {
        wall: state.wall.wallData,
        error: state.error.themeError,
        errorWithMessage: state.error.errorWithMessage,
        startEmbed: state.startEmbed
    }
}
export default connect(mapStateToProps)(Error);


