import React, { Component, Fragment } from 'react';
import moment from 'moment';
import ReactHtmlParser from 'react-html-parser';
import ErrorBoundary from '../../errorBoundary'
import MultiImageIcon from '../../cardPost/multiImageIcon'
import SocialNetworkIcon from '../../common/SocialNetworkIcon'
import ContentPostConversion from '../../../components/cardPost/contentPostConversion'
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, CAMERA_ICON_UPDATE_WITH_STYLE, IS_SCREEN_SHOT } from '../../../utils'
import { themePostTracking } from '../../../actions/themeActions'

class PostItems extends Component {
    state = {
        postFileNew: ""
    }
    componentWillMount() {
        const { item } = this.props;
        this.setState({ postFileNew: item.postFileNew })
        if (window.twttr) window.twttr.ready((twttr) => {
            window.twttr.events.bind('tweet', this.onTwitterClick);
        });
    }
    onUpdate = (e) => {
        this.setState({ postFileNew: e.target.src })
    }
    onShareFacebook = event => {
        const { item, wallId } = this.props;
        if (window.FB) window.FB.ui({
            display: 'popup',
            method: 'share',
            href: item.shareDetail.shareUrl,
        }, (response) => {
            if (response && !response.error_message) {
                themePostTracking({
                    type: 2,
                    action: 2,
                    wall: wallId,
                    feed: item.feedId,
                    post: item.referenceId,
                })
            }
        });
    }
    onTwitterClick = event => {
        const { item, wallId } = this.props;
        if (event) {
            themePostTracking({
                type: 2,
                action: 2,
                wall: wallId,
                feed: item.feedId,
                post: item.referenceId,
            })
        }
    }
    linkedinShareCount = event => {
        const { item, wallId } = this.props;
        themePostTracking({
            type: 2,
            action: 2,
            wall: wallId,
            feed: item.feedId,
            post: item.referenceId,
        })

    }
    render() {
        const { wallId, languageSetting, isDefaultFont, item, wall, fonrSize, reduceFontSize, onLoadPopup, videoIconSize, adjustWidth, index, fontSizeTrim } = this.props;
        const { postFileNew } = this.state;
        return <div className="test_img" style={{ backgroundImage: `url(${postFileNew})`, height: adjustWidth.height }}>
            <img className="blur-bg"
                data-src={item.postFileNew} src={IS_SCREEN_SHOT ? item.optmizedImg : item.postFileNew} data-is-optmized="0"
                data-filter-id={item.filterId}
                data-link={item.link} data-load="0" data-network={item.network.id} data-wall-id={wallId} data-item-id={item.id}
                onLoad={(e) => {
                    CAMERA_ICON_UPDATE_WITH_STYLE(e);
                    this.onUpdate(e);
                }}
                data-filter-id={item.filterId}
                onError={(item.stories == 0) ? (e) => {
                    IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)
                } : null} style={{ display: 'none' }} />

            <div className={`${item.network.id === 7 ? 'tb_youtube_icon_00' : ''} ${(item.type == 3 || item.type == 5) && (item.imageList && item.imageList.length > 0) ? 'tb_multiple_media_01' : 'tb_multiple_media_0'}`}>
                {(item.imageList && item.imageList.length > 0) ? <MultiImageIcon widgetTheme={wall.Personalization.widgetTheme} /> : null}
                {(item.type === 3 || item.type === 5) ? item.network.id === 7 ?
                    <div class="tb_youtube_post_ico">
                        <img className="tb_youtube_ico_svg" src={`https://cloud.taggbox.com/widget/widget-embed/youtube.png`} alt="youTube" />
                    </div> :
                    <div className="tb_video_post_ico">
                        <svg xmlns="http://www.w3.org/2000/svg" width="23.872" height="13.05" viewBox="0 0 23.872 13.05">
                            <g id="Group_29" data-name="Group 29" transform="translate(0 0)">
                                <path id="Path_35" data-name="Path 35" d="M25.492,40.55H12.378A1.881,1.881,0,0,1,10.5,38.672V29.378A1.881,1.881,0,0,1,12.378,27.5H25.46a1.881,1.881,0,0,1,1.878,1.878V38.64A1.862,1.862,0,0,1,25.492,40.55Z" transform="translate(-10.5 -27.5)" fill="#fff"></path>
                                <path id="Path_36" data-name="Path 36" d="M67.5,32.692v5.666l5.252,2.96a.317.317,0,0,0,.477-.286V30.019a.327.327,0,0,0-.477-.286Z" transform="translate(-49.357 -29)" fill="#fff"></path>
                            </g>
                        </svg>
                    </div>
                    : null
                }
            </div>

            <div className="testimonial_detail" onClick={onLoadPopup(index, item)}>

                <div className="author-details" style={reduceFontSize && reduceFontSize < 500 ? { fontSize: `${fonrSize}` } : {}}>
                    <div>
                        <img src={item.author.picture} alt="profile" style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block' }} onError={(e) => {
                            e.target.onerror = null; e.target.src = item.author.errorPic;
                        }} />
                        <div>

                            <h4 className={reduceFontSize && reduceFontSize < 500 ? `author-font-size` : ``} style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }}> {item.author.name}</h4>
                            <p style={{ color: item.font.authorColor, display: item.author.isInstaUser ? 'none' : 'block' }}>{(item.postAuthor === 1) && `@${item.author.username}`} <span className="timePost" style={{ display: (item.timePost === 0) ? 'none' : '', color: item.font.authorColor }} >{moment(new Date(item.createdAt * 1000)).fromNow()}</span></p>
                        </div>
                    </div>
                    <SocialNetworkIcon network={item.network.name != null ? item.network.name.toLowerCase() : ``} size="fa-2x" iconColor={item.font.iconColor} />
                </div>
                <div className="testimonial_detail--footer">
                    <blockquote style={{ fontFamily: wall.ThemeRule.css_font, fontSize: fonrSize, color: item.font.fontColor, display: (item.hideContent === 1) ? 'none' : '' }} className={`${wall.Personalization.textDecorate == 1 ? item.textDecoClass : ``} setFont ${parseInt(fontSizeTrim) <= 20 ? `text_trim` : `gallery_text_trim`} sGFfonte-${wall.ThemeRule.font_varient}`} > <ErrorBoundary><ContentPostConversion item={item} network={item.network} /></ErrorBoundary></blockquote>

                    {
                        (wall.Personalization.ctaStatus === 1 && item.cta && item.cta != null) && ReactHtmlParser(item.cta.replace("cardCTA", "cardCTA-post-theme"))
                    }

                    <div className={reduceFontSize && reduceFontSize < 500 ? `share-icons-post-theme author-font-size-${isDefaultFont}` : `share-icons-post-theme`}>
                        <div className="shareWrapperPostTheme" >
                            <a className="share-icon" style={{ display: (item.share.status === 0 || item.network.id === 7) ? 'none' : 'block' }}>
                                <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" aria-hidden="true" focusable="false" width="1em" height="1em" style={{ 'msTransform': 'rotate(360deg)', 'WebkitTransform': 'rotate(360deg)', transform: 'rotate(360deg)' }} preserveAspectRatio="xMidYMid meet" viewBox="0 0 512 512"><path d="M503.691 189.836L327.687 37.851C312.281 24.546 288 35.347 288 56.015v80.053C127.371 137.907 0 170.1 0 322.326c0 61.441 39.581 122.309 83.333 154.132c13.653 9.931 33.111-2.533 28.077-18.631C66.066 312.814 132.917 274.316 288 272.085V360c0 20.7 24.3 31.453 39.687 18.164l176.004-152c11.071-9.562 11.086-26.753 0-36.328z" fill="#ffffff" /></svg> {languageSetting.shareText}
                            </a>
                            <div className="popover-content">
                                <div className="popover-content-inner">
                                    {(item.network.id != 1 && item.type != 1) ? <a onClick={this.onShareFacebook} className="fbShare" target="_blank"> <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" aria-hidden="true" focusable="false" width="1em" height="1em" style={{ 'msTransform': 'rotate(360deg)', 'WebkitTransform': 'rotate(360deg)', transform: 'rotate(360deg)' }} preserveAspectRatio="xMidYMid meet" viewBox="0 0 512 512"><path d="M503.691 189.836L327.687 37.851C312.281 24.546 288 35.347 288 56.015v80.053C127.371 137.907 0 170.1 0 322.326c0 61.441 39.581 122.309 83.333 154.132c13.653 9.931 33.111-2.533 28.077-18.631C66.066 312.814 132.917 274.316 288 272.085V360c0 20.7 24.3 31.453 39.687 18.164l176.004-152c11.071-9.562 11.086-26.753 0-36.328z" fill="#ffffff" /></svg> Share on Facebook </a> : null}
                                    <a className="twShare" href={item.share.twitter} target="_blank">
                                        <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" aria-hidden="true" focusable="false" width="1.23em" height="1em" style={{ 'msTransform': 'rotate(360deg)', 'WebkitTransform': 'rotate(360deg)', transform: 'rotate(360deg)' }} preserveAspectRatio="xMidYMid meet" viewBox="0 0 256 209"><path d="M256 25.45c-9.42 4.177-19.542 7-30.166 8.27c10.845-6.5 19.172-16.793 23.093-29.057a105.183 105.183 0 0 1-33.351 12.745C205.995 7.201 192.346.822 177.239.822c-29.006 0-52.523 23.516-52.523 52.52c0 4.117.465 8.125 1.36 11.97c-43.65-2.191-82.35-23.1-108.255-54.876c-4.52 7.757-7.11 16.78-7.11 26.404c0 18.222 9.273 34.297 23.365 43.716a52.312 52.312 0 0 1-23.79-6.57c-.003.22-.003.44-.003.661c0 25.447 18.104 46.675 42.13 51.5a52.592 52.592 0 0 1-23.718.9c6.683 20.866 26.08 36.05 49.062 36.475c-17.975 14.086-40.622 22.483-65.228 22.483c-4.24 0-8.42-.249-12.529-.734c23.243 14.902 50.85 23.597 80.51 23.597c96.607 0 149.434-80.031 149.434-149.435c0-2.278-.05-4.543-.152-6.795A106.748 106.748 0 0 0 256 25.45" fill="#ffffff" /></svg> Share on Twiiter
                                            </a>
                                    <a onClick={this.linkedinShareCount} href={item.share.linkedin} className="liShare" target="_blank">
                                        <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" aria-hidden="true" focusable="false" width="1em" height="1em" style={{ 'msTransform': 'rotate(360deg)', 'WebkitTransform': 'rotate(360deg)', transform: 'rotate(360deg)' }} preserveAspectRatio="xMidYMid meet" viewBox="0 0 32 32"><path d="M8.268 28H2.463V9.306h5.805zM5.362 6.756C3.506 6.756 2 5.218 2 3.362a3.362 3.362 0 0 1 6.724 0c0 1.856-1.506 3.394-3.362 3.394zM29.994 28h-5.792v-9.1c0-2.169-.044-4.95-3.018-4.95c-3.018 0-3.481 2.356-3.481 4.794V28h-5.799V9.306h5.567v2.55h.081c.775-1.469 2.668-3.019 5.492-3.019c5.875 0 6.955 3.869 6.955 8.894V28z" fill="#ffffff" /></svg> Share on Linkedin
                                            </a>
                                </div>
                            </div>
                        </div>
                        {item.socialAction != 0 && item.socialAction && <div className="social-action">
                            <a target="_blank" href={`https://twitter.com/intent/retweet?tweet_id=${atob(item.postId)}`}><svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" aria-hidden="true" focusable="false" width="1em" height="1em" style={{ 'msTransform': 'rotate(360deg)', 'WebkitTransform': 'rotate(360deg)', transform: 'rotate(360deg)' }} preserveAspectRatio="xMidYMid meet" viewBox="0 0 1024 1024"><path d="M136 552h63.6c4.4 0 8-3.6 8-8V288.7h528.6v72.6c0 1.9.6 3.7 1.8 5.2a8.3 8.3 0 0 0 11.7 1.4L893 255.4c4.3-5 3.6-10.3 0-13.2L749.7 129.8a8.22 8.22 0 0 0-5.2-1.8c-4.6 0-8.4 3.8-8.4 8.4V209H199.7c-39.5 0-71.7 32.2-71.7 71.8V544c0 4.4 3.6 8 8 8zm752-80h-63.6c-4.4 0-8 3.6-8 8v255.3H287.8v-72.6c0-1.9-.6-3.7-1.8-5.2a8.3 8.3 0 0 0-11.7-1.4L131 768.6c-4.3 5-3.6 10.3 0 13.2l143.3 112.4c1.5 1.2 3.3 1.8 5.2 1.8c4.6 0 8.4-3.8 8.4-8.4V815h536.6c39.5 0 71.7-32.2 71.7-71.8V480c-.2-4.4-3.8-8-8.2-8z" fill="#00aced" /></svg></a>
                            <a target="_blank" href={`https://twitter.com/intent/favorite?tweet_id=${atob(item.postId)}`} ><svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" aria-hidden="true" focusable="false" width="1em" height="1em" style={{ 'msTransform': 'rotate(360deg)', 'WebkitTransform': 'rotate(360deg)', transform: 'rotate(360deg)' }} preserveAspectRatio="xMidYMid meet" viewBox="0 0 26 26"><path d="M17.869 3.889c-2.096 0-3.887 1.494-4.871 2.524c-.984-1.03-2.771-2.524-4.866-2.524C4.521 3.889 2 6.406 2 10.009c0 3.97 3.131 6.536 6.16 9.018c1.43 1.173 2.91 2.385 4.045 3.729c.191.225.471.355.765.355h.058c.295 0 .574-.131.764-.355c1.137-1.344 2.616-2.557 4.047-3.729C20.867 16.546 24 13.98 24 10.009c0-3.603-2.521-6.12-6.131-6.12z" fill="#626262" /></svg></a>
                        </div>}
                    </div>
                </div>
            </div>
        </div>
    }

}
export default PostItems;