import ReactHtmlParser from 'react-html-parser';
import $ from 'jquery';
import { isMobile, isTablet } from "react-device-detect";
export const scrlBarComan = (BackgroundImagePath, ThemeRuleTransparent, widgetTheme, isMobile, height, error_code, Bg_Path, ThemeRuleBackgroundColor, fixedHeightStyle) => {
    return {
        backgroundImage: BackgroundImagePath !== null && ThemeRuleTransparent === 0 ? 'url(' + Bg_Path + ')' : null,
        backgroundColor: ThemeRuleTransparent === 1 ? 'transparent!important' : ThemeRuleBackgroundColor,
        backgroundRepeat: "repeat",//"no-repeat",
        backgroundSize: "100%",//"cover",
        backgroundPosition: "center top",
        overflow: widgetTheme !== 49 && fixedHeightStyle == null ? 'hidden' : 'auto',
        outline: "none",
        marginTop: 0,
        // marginTop: widgetTheme === 49 && isMobile === false ? (height - 500) / 2: widgetTheme === 49 && isMobile === true ? 0: 0,
        backgroundAttachment: 'fixed',
        display: (error_code === 203 || error_code === 207 || error_code === 201 || error_code === 202 || error_code === 204 || error_code === 205 || error_code === 206 || error_code === 208 || error_code === 209 || error_code === 211 || error_code === 212) ? 'none' : 'block',
        scrollbarWidth: 'none'
    }
}

export const checkStringInNotNull = (string) => string != null && string !== "" && string !== 0 ? true : false;
export const convertHtmlStringToRender = (html) => ReactHtmlParser(html)
export const themeLeft = () => {
    let platform = window.navigator.platform;
    let windowWidth = $("#root").width();
    if (platform === 'Win32') return windowWidth < 767 ? 0 : 6;
    else return windowWidth < 767 ? 0 : 2;
}

