import React, { Component } from 'react';
import { connect } from 'react-redux';
import { isMobile } from "react-device-detect";
import '../css/inter-fonts.css'

const powered_byStyle = {
    padding: '5px 12px 5px 12px', 
    borderRadius: '4px',
    fontWeight: 700,
    border: '1px solid #ccc',
    display: 'inline-block',
    backgroundColor: '#ffffff',
    color: '#545454',
    fontSize:14
}
const classicPhotoCardStyle = {
    padding: '5px 12px 5px 12px',
    borderRadius: '4px',
    fontWeight: 700,
    border: '1px solid white',
    display: 'inline-block',
    backgroundColor: '#ffffff',
    color: '#545454',
    fontSize:14
}

const squareCardStyle = {
    padding: '5px 12px 5px 12px',
    borderRadius: '4px',
    fontWeight: 700,
    border: '1px solid white',
    display: 'inline-block',
    backgroundColor: 'rgba(21, 21, 21, 0.7411764705882353)',
    color: '#ffffff',
    fontSize:14
}
const horizontalStyle = {
    ...squareCardStyle,
    marginBottom: 4
}


class PoweredBY extends Component {
    render() {
        const { widgetTheme, startEmbed, numberOfCoumn } = this.props;
        const mResponsiveCss = (numberOfCoumn && numberOfCoumn > 3 && isMobile) ? `m-powered` : ``
        return <a className={`theme${widgetTheme}_poweredBy poweredBy ${mResponsiveCss}`} href={`https://taggbox.com/widget/?utm_source=lite_poweredby&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=lite_embed_poweredby`} target="_blank">
            <div className="poweredbywrapper" style={widgetTheme === 4 ? squareCardStyle : widgetTheme === 3 ? classicPhotoCardStyle : widgetTheme === 16 ? horizontalStyle : powered_byStyle}>
                <img src="https://cloud.taggbox.com/widget/widget-embed/taggbox-icon-PoweredBy.png" width="28px" />
                <span>Powered by Taggbox</span>
            </div>
        </a>
    }
}

const mapStateToProps = ({ startEmbed }) => {
    return {
        startEmbed: startEmbed
    }
}
export default connect(mapStateToProps)(PoweredBY);
