import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux';
import { wallThemeUpdate } from '../../actions/themeActions'
import LoaderComponent from '../loaderComponent';
import Loader6 from '../loaderComponent/loader6';
import ModerCardWidgetTheme from './modernCard';
import WidgetCardWidgetTheme from './widgetThemeCard';
import NewsCardWidgetTheme from './newsCard';
import ClassicCardTheme from './classicCard'
import ClassicPhotoTheme from './classicPhotoCard'
import SquarePhotoTheme from './squarePhoto'
import GalleryPhotoTheme from './galleryPhoto'
import ErrorExtends from './error'
import HorizontalSliderTheme from './horizontalSlider'
import HorizontalColumnSliderTheme from './horizontalColumnSlider'
import StoryThemeSliderTheme from './StoryThemeSlider'
import StoryTheme from './storyTheme'
// import NewStoryTheme from './newStroryTheme';
import PostTheme from './postTheme'
import { loaderTopHeightSetting, OG_IMAGE_PATH } from '../../constants'
import ModalPopUp from '../modalPopUp'
import PageHeadSection from '../pageHeadSection';

const ThemeLoadWithThemeID = (wall, loadMoreRequest) => {
    const widgetTheme = wall.Personalization.widgetTheme;
    return <Fragment>
        {wall.hasOwnProperty("Wall") && <PageHeadSection PageStyle={wall.Personalization.cssStatus === 1 ? wall.Personalization.css : null} PageTitle={`${wall.Wall.name}`} PageMetaData={null} wallId={wall.Wall.id} widgetName={wall.Wall.name} widgetOGImage={wall.Wall.ogImage ? wall.Wall.ogImage : OG_IMAGE_PATH} wallData={wall} />}

        {
            widgetTheme === 20 ? <ModerCardWidgetTheme loadMoreRequest={loadMoreRequest} /> :
                widgetTheme === 5 ? <ClassicCardTheme loadMoreRequest={loadMoreRequest} /> :
                    widgetTheme === 3 ? <ClassicPhotoTheme loadMoreRequest={loadMoreRequest} /> :
                        widgetTheme === 4 ? <SquarePhotoTheme loadMoreRequest={loadMoreRequest} /> :
                            widgetTheme === 19 ? <NewsCardWidgetTheme loadMoreRequest={loadMoreRequest} /> :
                                widgetTheme === 50 ? <GalleryPhotoTheme loadMoreRequest={loadMoreRequest} /> :
                                    widgetTheme === 52 ? <PostTheme /> :
                                        widgetTheme === 16 ? <HorizontalSliderTheme /> :
                                            widgetTheme === 55 ? <StoryTheme /> :
                                                widgetTheme === 49 ? <WidgetCardWidgetTheme loadMoreRequest={loadMoreRequest} /> :
                                                    widgetTheme === 47 ? <HorizontalColumnSliderTheme /> :
                                                        widgetTheme === 60 ? <StoryThemeSliderTheme /> : null

        }

    </Fragment>
}

class Themes extends PureComponent {
    state = { loaderTopStyle: 0 }
    componentDidMount = () => {
        this.onDataWallThemeUpdate();
        const { wall } = this.props;
        if (wall && wall != null && wall.Personalization.widgetTheme !== 49) this.setState({ loaderTopStyle: loaderTopHeightSetting() })
    }
    onDataWallThemeUpdate = () => {
        const { startEmbed, loader } = this.props;
        if (startEmbed.heightEvent && startEmbed.heightEvent.data && startEmbed.heightEvent && startEmbed.heightEvent.data.theme != null && parseInt(startEmbed.heightEvent.data.theme)) {
            this.props.wallThemeUpdate(parseInt(startEmbed.heightEvent.data.theme), loader.loader.wall)
        }
    }
    render() {
        const { loader, wall, error, loadMoreRequest, modalPop } = this.props;
        const { loaderTopStyle } = this.state;
        return loader.themeLoader ? loader.isLoaderEnable ? <LoaderComponent loader={loader.loader} isLoading={true} wall={wall} loaderTopStyle={loaderTopStyle} /> : null :
            loader.tagsLoader ? <Loader6 loader={loader.loader} isLoading={true} wall={wall} loaderTopStyle={loaderTopStyle} /> : error.error_code !== 0 ? <ErrorExtends innerHeight={window.innerHeight} /> : (wall && wall.Personalization) ? <Fragment> {ThemeLoadWithThemeID(wall, loadMoreRequest)}
                {modalPop.isShowPopUp && <ModalPopUp data={modalPop.data} />}
            </Fragment> : null
    }
}
const mapStateToProps = state => {
    return {
        loader: state.loader,
        wall: state.wall.wallData,
        error: state.error.themeError,
        startEmbed: state.startEmbed,
        modalPop: state.modalPop
    }
}
const mapDispatchToProps = dispatch => {
    return {
        wallThemeUpdate: (themeID, wallData) => dispatch(wallThemeUpdate(themeID, wallData))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Themes);

