const SampleNextArrow = (props) => {
  const { className, style, onClick, count, hasMoreData } = props;
  if (onClick == null && hasMoreData) props.onUpdateData();
  return (
    <div
      className={className}
      style={{ ...style, display: "block", color: "rgb(0, 0, 0)" }}
      onClick={onClick}
    />
  );
};

const SamplePrevArrow = (props) => {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{ ...style, display: "block", color: "rgb(0, 0, 0)" }}
      onClick={onClick}
    />
  );
};

