            initialSlide: 0,
            slidesToShow: numberOfCoumn == 0 ? 1 : numberOfCoumn,
            slidesToScroll: numberOfCoumn == 0 ? 1 : numberOfCoumn,
            autoplay: wall.ThemeRule.slidePost == 1 ? true : false,
            speed: wall.ThemeRule.slidePost == 1 ? (1000) : 500,
