import React, { Component } from 'react';
import { checkStringInNotNull } from '../../themes/customFunction'
import { convertHtmlStringToRender } from '../../themes/customFunction'
import ContentPostConversion from '../contentPostConversion'
import { CLOUD_URL } from '../../../constants'
import { themePostTracking } from '../../../actions/themeActions'
import ErrorBoundary from '../../errorBoundary'
class WidgetThemeCardContentPost extends Component {
    onCtaClick = event => {
        const { wall, item } = this.props;
        themePostTracking({
            type: 1,
            action: 2,
            wall: wall.Wall.id,
            feed: item.feedId,
            post: item.referenceId,
        })
    }
    render() {
        const { wall, item, isRating, rating, network, isCta, cta, clickLoadPopUp, personalization } = this.props;
        return <div className="postContentCard">
            {isRating ? <div className="rating">
                <img src={`${CLOUD_URL}/widget-embed/rating/${network.id}/${rating}.png`} alt="rating" />
            </div> : null}
            {isCta ? <span onClick={this.onCtaClick}> {convertHtmlStringToRender(cta)}</span> : null}
            <div onClick={clickLoadPopUp} className={`postedTxt ${personalization.textDecorate == 1 ? item.textDecoClass : ``} setFont  sGFfonte-${wall.ThemeRule.font_varient}`} style={{
                fontFamily: wall.ThemeRule.css_font, display: (item.hideContent === 1 && item.type !== 1) ? 'none' : 'inline-grid', fontSize: item.font.fontsize,

                color: item.font.cardColor === "#000000" && item.font.fontColor === "#000000" ? "#ffffff" :
                    item.font.cardColor === "#ffffff" && item.font.fontColor === "#ffffff" ? "#000000"
                        : item.font.fontColor

            }}>
                {checkStringInNotNull(item.contentTitle) ? <h1>{convertHtmlStringToRender(item.contentTitle)}</h1> : null}
                <ErrorBoundary><ContentPostConversion item={item} network={network} /></ErrorBoundary>
            </div>

        </div>
    }
}

export default WidgetThemeCardContentPost;