import React, { Component } from 'react';
import { connect } from 'react-redux';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';

import { reactStringReplace, THMEME_ID_TRIM_CONTENT } from '../../constants'
import { STRING_TO_URL_CONVERT, isFullContentEnable } from './../../utils'

const trimContent = {
    overflow: 'hidden',
    textOverflow: 'ellipsis'
}
const textContentStyleSheetName = (widgetTheme, imgWidth, trimcontent) => {
    let common = `${trimcontent == 1 ? `chtrLimits` : ``} emojiApplied`
    switch (widgetTheme) {
        case 4:
            const trimClass = imgWidth != "100%" && imgWidth < 250 ? `gallery_text_trim` : `text_trim`
            return trimcontent == 1 ? `${trimClass} ${common}` : common
            break;
        case 16:
            return trimcontent == 1 ? `horizontal_text_trim ${common}` : common
            break;
        case 50:
            return trimcontent == 1 ? `gallery_text_trim ${common}` : common
            break;
        default:
            return `${common}`
    }

}
const convertCss = (hashtag_color, match, isHash, hashtag_background_color) => {
    let style = `<span className="gridhashtag" style="color:${hashtag_color};font-weight:bold;background-color:${hashtag_background_color};">${isHash ? `#` : ``}${match}</span>`
    return style;
}
class ContentPostConversion extends Component {
    unicodeyEmojiConvert = (data) => {
        // if (window.EmojiConvertor) {

        //     var emoji = new window.EmojiConvertor();
        //     emoji.use_sheet = true;
        //    // emoji.replace_mode = 'unified';
        //     //emoji.allow_native = true;
        //     var output = emoji.replace_unified(data);
        //     output = emoji.replace_colons(output);
        //     return ReactHtmlParser(output);
        // }
        // else {
        return <>{ReactHtmlParser(data)}</>
        //}

    }
    textEmojiConvert = (data) => {
        return <>{data}</>

    }
    render() {

        const { item, network, widgetTheme, imgWidth, wall } = this.props;
        var contentData = wall.Wall.owner == 23390 ? item.content : STRING_TO_URL_CONVERT(item.content);
        const slackData = item.content;
        const isFullContentVisible = isFullContentEnable(wall.Wall.id)
        const trimContent = THMEME_ID_TRIM_CONTENT(wall.Personalization.widgetTheme) ? '1' : wall.Personalization.trimcontent;

        return item.network.id === 20 ?
            <p className={`${textContentStyleSheetName(wall.Personalization.widgetTheme, imgWidth, trimContent)} sGFfonte-${wall.ThemeRule.font_varient}`} style={{ fontFamily: wall.ThemeRule.css_font, ...isFullContentVisible }}> {
                wall.Personalization.hashtag_highlight !== 0 ?
                    wall.Personalization.hashtag_all === 1 ? this.unicodeyEmojiConvert(window.slackdown.parse(reactStringReplace(slackData, /#(\w+)/g, (match, i) => convertCss(wall.Personalization.hashtag_color, match, true, wall.Personalization.hashtag_background_status == 1 ? wall.Personalization.hashtag_background_color : 'transparent')), item.slackMember))
                        : wall.Personalization.hashtag_feed === 1 && item.hash.hashString != "" ? this.unicodeyEmojiConvert(window.slackdown.parse(reactStringReplace(slackData, item.hash.hashString, (match, i) => convertCss(wall.Personalization.hashtag_color, match, false, wall.Personalization.hashtag_background_status == 1 ? wall.Personalization.hashtag_background_color : 'transparent')
                        ), item.slackMember)) : this.unicodeyEmojiConvert(window.slackdown.parse(slackData, item.slackMember))
                    : this.unicodeyEmojiConvert(window.slackdown.parse(slackData, item.slackMember))
            }</p>
            : <p className={`${textContentStyleSheetName(wall.Personalization.widgetTheme, imgWidth, trimContent)} sGFfonte-${wall.ThemeRule.font_varient}`} style={{ fontFamily: wall.ThemeRule.css_font, ...isFullContentVisible }}>{
                wall.Personalization.hashtag_highlight !== 0 ?
                    wall.Personalization.hashtag_all === 1 ? this.textEmojiConvert(reactStringReplace(ReactHtmlParser(contentData), /#(\w+)/g, (match, i) => (
                        <span key={match + i} style={{ color: wall.Personalization.hashtag_color, fontWeight: "bold", backgroundColor: wall.Personalization.hashtag_background_status == 1 ? wall.Personalization.hashtag_background_color : 'transparent' }}>#{match}</span>
                    )))
                        : wall.Personalization.hashtag_feed === 1 && item.hash.hashString != "" ? this.textEmojiConvert(reactStringReplace(ReactHtmlParser(contentData), item.hash.hashString, (match, i) => (
                            <span key={match + i} style={{ color: wall.Personalization.hashtag_color, fontWeight: "bold", backgroundColor: wall.Personalization.hashtag_background_status == 1 ? wall.Personalization.hashtag_background_color : 'transparent' }}>{match}</span>
                        ))) : this.textEmojiConvert(ReactHtmlParser(contentData))
                    : this.textEmojiConvert(ReactHtmlParser(contentData))
            }</p>


    }
}

const mapStateToProps = state => {
    const { wall } = state
    return {
        wall: wall.wallData
    }
}
export default connect(mapStateToProps)(ContentPostConversion);