import React, { Component } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import { checkStringInNotNull } from '../../themes/customFunction'
import { convertHtmlStringToRender } from '../../themes/customFunction'
import ContentPostConversion from '../contentPostConversion'
import { CLOUD_URL } from '../../../constants'
import AuthorAvatar from '../../common/AuthorAvatar';
import ErrorBoundary from '../../errorBoundary'
import { isValidAuthorImage, IS_YOU_TUBE_POST_HOVER_CONTENT } from '../../../utils';
const isMinimumPost = { transform: 'translate(-50%, -50%)', top: '50%', left: '50%' }
const cardStyleEnable = (screenSize) => {
    if (screenSize < 100) return 5;
    if (screenSize > 100 && screenSize < 190) return 1;
    else if (screenSize > 190 && screenSize < 200) return 2;
    else return 3;
}
const centerSocialIcon = () => {
    return {
        top: '50%',
        transform: 'translatey(-50%)',
        textAlign: 'center',
        width: '100%',
        right: 0,
        bottom: 'auto'
    }
}
class SquarePhotoContentPost extends Component {
    state = { isAuthorImageValid: true }
    componentDidMount() {
        const { item } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({
            isAuthorImagevalid: isAuthorImagevalid
        });
    }
    render() {
        const { item, isRating, rating, network, isCta, cta, widgetTheme, imgWidth, wall, newCardWidth, clickLoadPopUp, numberOfCoumn, personalization } = this.props;

        const isStyleEnable = cardStyleEnable(newCardWidth)
        const top = newCardWidth != "100%" && newCardWidth < 290 ? { top: '42%' } : {}
        return <React.Fragment>
            <span onClick={clickLoadPopUp}>
                {(isStyleEnable == 2 || isStyleEnable == 3) && <div className="postNetwork" data-network={network.name} data-network-color={network.color}>
                    <i className={`fa ${network.icon}`} style={{ color: item.font.iconColor }} >
                    </i>
                </div>}

                {IS_YOU_TUBE_POST_HOVER_CONTENT(widgetTheme, network.id) && <div className="postContentCard" >
                    {(isStyleEnable == 1) && <div className="postNetwork" data-network={network.name} data-network-color={network.color} style={(isStyleEnable == 1) ? centerSocialIcon() : {}}>
                        <i className={`fa ${network.icon}`} style={{ color: item.font.iconColor }} >
                        </i>
                    </div>}
                    {isRating ? <div className="rating">
                        <img src={`${CLOUD_URL}/widget-embed/rating/${network.id}/${rating}.png`} alt="rating" />
                    </div> : null}


                    <div className={`postedTxt ${personalization.textDecorate == 1 ? item.textDecoClass : ""} setFont  sGFfonte-${wall.ThemeRule.font_varient}`} style={{ fontFamily: wall.ThemeRule.css_font, display: (item.hideContent === 1 && item.type !== 1) ? 'none' : 'inline-grid', fontSize: item.font.fontsize, color: item.font.fontColor, ...top }}>
                        {checkStringInNotNull(item.contentTitle) ? <h1>{convertHtmlStringToRender(item.contentTitle)}</h1> : null}
                        {parseInt(numberOfCoumn) == 0 && parseInt(wall.Personalization.minimumPostWidth) > 201 ? <ErrorBoundary><ContentPostConversion item={item} network={network} widgetTheme={widgetTheme} imgWidth={newCardWidth} /></ErrorBoundary> : parseInt(numberOfCoumn) > 0 && (isStyleEnable == 3) ? <ErrorBoundary><ContentPostConversion item={item} network={network} widgetTheme={widgetTheme} imgWidth={newCardWidth} /> </ErrorBoundary> : null}
                    </div>


                    {/* <div onClick={clickLoadPopUp} className={`postedTxt ${personalization.textDecorate == 1 ? item.textDecoClass : ``} setFont`} style={{ display: (item.hideContent === 1 && item.type !== 1) ? 'none' : 'inline-grid', fontSize: item.font.fontsize, color: item.font.fontColor, ...top }}>
                    {checkStringInNotNull(item.contentTitle) ? <h1>{convertHtmlStringToRender(item.contentTitle)}</h1> : null}
                    {parseInt(numberOfCoumn) == 0 && parseInt(wall.Personalization.minimumPostWidth) > 201  ? <ContentPostConversion item={item} network={network} widgetTheme={widgetTheme} imgWidth={newCardWidth} /> : parseInt(numberOfCoumn) > 0 && (isStyleEnable == 3) ? (numberOfCoumn < 3) ? <ContentPostConversion item={item} network={network} widgetTheme={widgetTheme} imgWidth={newCardWidth} /> : null : null}
                </div> */}


                    {(isStyleEnable == 2 || isStyleEnable == 3) && <h4>
                        <div className="authorDetails" style={parseInt(wall.Personalization.minimumPostWidth) < 201 ? { ...isMinimumPost, display: (item.instaHash === 0) ? 'none' : 'block' } : { display: (item.instaHash === 0) ? 'none' : 'block' }}>
                            {/* <img style={{ display: (item.postAuthor === 0) ? 'none' : 'block' }} src={item.author.picture}
                            onError={(e) => {
                                e.target.onerror = null; e.target.src = item.author.errorPic;
                            }}
                            alt="postAuthor" /> */}
                            {this.state.isAuthorImagevalid && <img style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block' }} src={item.author.picture}
                                onError={(e) => {
                                    this.setState({
                                        isAuthorImagevalid: false
                                    });
                                    e.target.onerror = null;
                                    e.target.src = item.author.errorPic
                                    console.clear()
                                }} alt="postAuthor" />}
                            {!this.state.isAuthorImagevalid && <AuthorAvatar style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'flex' }} username={item.author.name} color={item.network.color} />}
                            <div className="postAuthorName" style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }}>
                                {item.author.name}
                                {/* <br/>{item.id} */}
                            </div>

                            <div className="authrHandleTime">
                                <span className="authrHandle" style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor, textOverflow: 'ellipsis', whiteSpace: 'nowrap', maxWidth: parseInt(wall.Personalization.minimumPostWidth) < 180 ? '60.5px' : '100px' }} target="_blank">
                                    {/* {item.author.username.length>0?`@`:``}{item.author.username} */}
                                    {item.author.username ? item.author.username.length > 0 ? `@` : `` : ''}{item.author.username}
                                </span>
                                <div className="sepratedot" style={{ display: (item.postAuthor === 0 || item.timePost === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }}></div>
                                <span className="timePost" style={{ display: (item.timePost === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }} data-livestamp={item.createdAt}>
                                    {moment(new Date(item.createdAt * 1000)).fromNow()}
                                </span>
                            </div>
                            <div className="clearfix"></div>
                        </div>
                    </h4>}

                    {/* {isStyleEnable == 3 && <div className="shareWrapper" data-color="" style={{ display: (item.share.status === 0 || network.id === 7) ? 'none' : 'block' }}>
                    <a target="_blank">
                        <i className="fa fa-share icon" aria-hidden="true" style={{ lineHeight: '30px' }}>
                        </i>
                    </a>

                    <a href={item.share.facebook} className="fbShare" target="_blank">
                        <i className="fa fa-facebook-f icon" aria-hidden="true" style={{ color: 'rgb(58, 87, 149)' }}>
                        </i>
                    </a>
                    <a href={item.share.twitter} className="twShare" target="_blank">
                        <i className="fa fa-twitter" aria-hidden="true">
                        </i>
                    </a>

                    <a href={item.share.linkedin} className="liShare" target="_blank">
                        <i className="fa fa-linkedin" aria-hidden="true">
                        </i>
                    </a>
                </div>
                } */}

                </div>}
            </span>

        </React.Fragment>
    }
}

const mapStateToProps = state => {
    return {
        wall: state.wall.wallData
    }
}

export default connect(mapStateToProps)(SquarePhotoContentPost);