import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import $ from 'jquery';
import { isMobile, isTablet } from "react-device-detect";
import WebFilters from '../../webFilters'
import { scrlBarComan } from '../customFunction'
import CustomPosts from '../../customPost'
import OwnBanner from '../../ownBanner'
import ThemeBanner from '../../themeBanner'
import PoweredBY from '../../poweredByComponent'
import { CUSTOM_SERVER_PATH } from '../../../constants'
import HorizontalSliderExtend from './slider'
import { IS_EMBED } from '../../../actions/api';
import { THEME_WIDTH_ADJUSTMENT_HORIZONTAL_SLIDER, customCenterHeight } from '../../../utils'
import SubmitPic from '../../submitPic'
class ModernCardTheme extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        windowWidth: $("#root").width(),
        windowHeight: window.innerHeight,
        adjustWidth: 0,
    }
    componentDidMount() {
        this.onResponsiveTheme();
        window.addEventListener("resize", () => {
            this.setState({
                windowWidth: $("#root").width(),
                windowHeight: window.innerHeight,
            }, () => this.onResponsiveTheme());
        });
    }
    onResponsiveTheme = () => {
        const { wall } = this.props;
        const { windowWidth } = this.state;
        const { adjustWidth } = THEME_WIDTH_ADJUSTMENT_HORIZONTAL_SLIDER(windowWidth, wall);
        this.setState({ adjustWidth })
    }
    render() {

        const { wall, appendData, webFilters, startEmbed, wallId } = this.props;
        const { windowHeight, adjustWidth, windowWidth } = this.state;
        let fixedHeightStyle = null;
        if (appendData && appendData.heightEvent && appendData.heightEvent.data.fixedHeight == "0") {
            fixedHeightStyle = { overflow: 'auto', height: `${appendData.heightEvent.data.iframeHeight}px` }
        }
        let numberOfCoumn = (isMobile || windowWidth < 768) ? parseInt(wall.ThemeRule.mobileColumn) : parseInt(wall.ThemeRule.numberOfCoumn)

        let isCenter = false;
        let centerTheme = customCenterHeight(windowHeight, wall.ThemeRule.multiRow == 2 ? 600 : 300);
        if (startEmbed && startEmbed.heightEvent && startEmbed.heightEvent.data) isCenter = false;
        else isCenter = true;

        return <Fragment>

            <div id="controllerContent" >
                {
                    wall.UserRule.on_site_upload && !(window.wallId) ? <SubmitPic wallId={wallId} wall={wall} /> : null
                }
                <div id="wPostData" style={isCenter && wall.ThemeRule.multiRow == 1 ? { marginTop: `${wall.ThemeRule.multiRow == 1 ? centerTheme : (centerTheme / 3)}px` } : {}}>
                    {wall.CustomBanner.status === 1 && wall.CustomBanner.banner_position === 1 ? <ThemeBanner CustomBanner={wall.CustomBanner}
                        CustomBannerImage={wall.CustomBannerImage}
                        LogoImage={wall.LogoImage}
                    /> : null}
                    <div id="scrlBarComanW" className={`scrlBarComan themesWrapper themeStart${wall.Personalization.widgetTheme} themeType${wall.Personalization.themeType} `}
                        style={scrlBarComan(wall.BackgroundImage.path, wall.ThemeRule.transparent, wall.Personalization.widgetTheme, false, window.innerHeight, 0, `${CUSTOM_SERVER_PATH}${wall.BackgroundImage.path}${wall.BackgroundImage.newName}`, wall.ThemeRule.backgroundColor, fixedHeightStyle)} >
                        {
                            wall.BannerImage.status === 1 ? <OwnBanner BannerImage={wall.BannerImage} /> : null}
                        <WebFilters appendData={appendData} />
                        <div className="themeWrap" style={(IS_EMBED || (startEmbed.heightEvent && startEmbed.heightEvent.hasOwnProperty('data'))) ? {} : {}}>


                            <div id="ePosts"
                                className={`themeStart${wall.Personalization.widgetTheme} theme${wall.Personalization.widgetTheme} owl-carousel owl-theme owl-loaded owl-drag owl-carousel owl-theme owl-loaded owl-drag`}
                                style={
                                    { height: 'inherit' }} >
                                <HorizontalSliderExtend adjustWidth={adjustWidth} numberOfCoumn={numberOfCoumn} />
                            </div>

                            <CustomPosts />
                        </div>  {
                            wall.CustomBanner.status === 1 && wall.CustomBanner.banner_position === 2 ? < ThemeBanner CustomBanner={wall.CustomBanner}
                                CustomBannerImage={wall.CustomBannerImage}
                                LogoImage={wall.LogoImage}
                            /> : null}

                    </div>

                </div>
            </div>
        </Fragment >

    }
}
const mapStateToProps = state => {
    const { startEmbed, wallId, webFilters, wall, postData, appendData, loader } = state
    return {
        wallId: wallId.wallID,
        webFilters: webFilters.webFilters,
        wall: wall.wallData,
        appendData: postData.appendData,
        loader: loader,
        postData: postData.postData,
        completeDataObject: postData.completeDataObject,
        startEmbed: startEmbed
    }
}
export default connect(mapStateToProps)(ModernCardTheme);