import React, { Component } from 'react';
import { connect } from 'react-redux';
import { v4 as uuidv4 } from 'uuid';
// import './css/feature-popup.css'
// import './App.css'
import WidgetPopUP from './component/WidgetPopUP'
import StoryPopup from '../storyPopup/StoryPopup';
import { closePopUP } from '../../actions/themeActions'
// import SubmitPic from './component/submitPic'
import OnsiteUpload from './component/onsiteupload'

class ModalPopUp extends Component {
  state = {
    isEnable: 0,
    eventData: {},
    shover: false,
    shareText: "SHARE",
    viewOnText: "View on",
    currentIndex: null,
    completeData: {},
    personalization: {},
    url: null,
    platform: null,
    imgData: [],
    type: ""
  }
  /* FEATURED POP UP CLOSE AND SEND TO CLOSE EVENT TO PARENT */
  closeWidgetPopUP = event => {
    const { windowParent } = this.state;
    this.setState({ isEnable: 0, currentIndex: null, type: "" });
    this.props.closePopUP()
  };
  /* GET FEATURED POP UP FROM RECEIVED EVENTS FROM POST */
  getWidgetPopUPEvent = (data) => {
    let event = 0;
    if (data !== undefined && data != "" && data.card) {
      if (data.type == "post") {
        this.setState({
          platform: this.checkBrowser(),
          url: "",//event.origin,
          isEnable: 1,
          eventData: data,
          currentIndex: data.index,
          completeData: data.idArray,
          viewOnText: data.viewOnText,
          shareText: data.shareText,
          windowParent: null,
          imgData: data.imgData,
          personalization: data.personalization,
          type: data.type
        }, () => this.WidgetPopUPSlideWithArrowKey())
      }
      else if (data.type == "submit-pic") {
        this.setState({
          isEnable: 1,
          type: data.type,
          windowParent: data,
        })
      }
    }

  };
  checkBrowser = () => {
    let userAgentString = window.navigator.userAgent;
    if (userAgentString.indexOf("Chrome") > -1) return "tb-chrome modal-open";
    else if (userAgentString.indexOf("Safari") > -1) return "tb-safari modal-open";
    else return "modal-open";
  }
  /* POP SLIDER CHANGE INDEX WITH SLIDE */
  featuredPopSliderChangeIndex = (currentIndex) => event => {
    const { eventData } = this.state;
    if (currentIndex === -1) this.setState({ currentIndex: eventData.idArray.length - 1 });
    else this.setState({ currentIndex: eventData.idArray.length == currentIndex ? 0 : currentIndex });
  };
  SliderWithKey = (currentIndex) => {
    const { eventData } = this.state;
    if (currentIndex === -1) this.setState({ currentIndex: eventData.idArray.length - 1 });
    else this.setState({ currentIndex: eventData.idArray.length == currentIndex ? 0 : currentIndex });
  };
  componentDidMount() {
    document.body.className = this.checkBrowser();
    const { data } = this.props;
    this.getWidgetPopUPEvent(data)
  }
  /* POP SLIDER WITH ARROW KEYS EVENTS */
  WidgetPopUPSlideWithArrowKey = e => {
    const { personalization } = this.state;

    if (personalization && personalization.popupSlideShow === 1) {
      e = e || window.event;
      let self = this;
      if (e && e.keyCode && e.keyCode != undefined) {
        if (e.keyCode == 37) self.SliderWithKey(self.state.currentIndex - 1)
        else if (e.keyCode == 39) self.SliderWithKey(self.state.currentIndex + 1)
      }
    }


  }

  render() {
    const { wall } = this.props;
    const { viewOnText, isEnable, currentIndex, personalization, completeData, eventData, url, platform, imgData, type } = this.state;
    let data = null;
    if (currentIndex === -1) data = completeData[eventData.idArray.length - 1];
    else data = completeData[currentIndex];

    return (isEnable === 1 && wall && type == "submit-pic") ? <OnsiteUpload wall={wall} wallId={wall.Wall.id} onClosePopUp={this.closeWidgetPopUP} /> : isEnable === 1 && type == "post" ? (data.stories && data.stories == 1) ? <StoryPopup key={uuidv4()} completeData={completeData} eventData={eventData} wall={wall} item={data} onClosePopUp={this.closeWidgetPopUP} currentIndex={currentIndex} personalization={personalization} viewOnText={viewOnText} slideWithArrowKeys={this.WidgetPopUPSlideWithArrowKey} featuredPopSliderChangeIndex={this.featuredPopSliderChangeIndex} hostUrl={url} platform={platform} imgData={imgData} />
      : <WidgetPopUP completeData={completeData} eventData={eventData} wall={wall} item={data} onClosePopUp={this.closeWidgetPopUP} currentIndex={currentIndex} personalization={personalization} viewOnText={viewOnText} slideWithArrowKeys={this.WidgetPopUPSlideWithArrowKey} featuredPopSliderChangeIndex={this.featuredPopSliderChangeIndex} hostUrl={url} platform={platform} imgData={imgData} />
      : null
  }
}

const mapStateToProps = state => {

  return {
    wall: state.wall.wallData,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    closePopUP: (data) => dispatch(closePopUP(data)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ModalPopUp);

