
import {
  GET_POST_DATA_SUCCESS,
  GET_PRE_RENDER_POST_DATA_SUCCESS,
  GET_POST_DATA_ERROR,
  GET_HORIZONTAL_POST_DATA_SUCCESS,
  GET_FILTERED_COMPLETE_POST_DATA_SUCCESS,
  GET_CURRENT_FILTER_ID,
  GET_APPEND_DATA_SUCCESS,
  GET_APPEND_DATA__ERROR,
  GET_NETWORK_ID_APPEND_DATA_SUCCESS,
  GET_IFRAME_EVENT_SUCCESS,
  SET_FILTER_ALL,
  SET_HAS_MORE_DATA,
  SET_URLS_ACCESSED_FOR_DATA,
  GET_CUSTOM_POST_DATA_SUCCESS,
  WALL_ID_FETCHED,
  EMBED_URL_FETCHED,
  GET_WALL_SUCCESS,
  UPDATE_FREE_ADS_STATUS
} from "./reducersKeys";
import { SHARE_ROOT_URL } from "../actions/api";
import { randomNumber, URL_DESTRUCTURE } from '../utils'

const initialState = {
  postData: [],
  completeData: [],
  preRender: {},
  appendData: { after: null, networkID: 0, heightEvent: null },
  completeDataObject: {},
  filteredDataObject: {},
  hasMoreData: {},
  urlsAccessed: [],
  customPostData: {},
  wallId: null,
  originUrl: null,
  afterUsedList: [],
  userId: null,
  isFreeAdsStatus: false,
  freeAdsPosition: [3,]
};

export default function reducer(state = initialState, action) {
  const { type, payload, filterNetworkId, isPreRenderData, table_name } = action;
  switch (type) {
    case GET_POST_DATA_SUCCESS:
    case GET_FILTERED_COMPLETE_POST_DATA_SUCCESS: {
      let filterId = filterNetworkId
        ? filterNetworkId
        : state.currentFilterNetworkId;
      if (!filterId) {
        filterId = 0;
      }


      let completeDataObject = state.completeDataObject;
      let filteredDataObject = state.filteredDataObject;
      let completeData = state.completeData;
      let customPostData = state.customPostData;
      let freeAdsPosition = state.freeAdsPosition;

      let newData = payload;
      if (isPreRenderData) {
        newData = state.preRender;
      }

      if (Object.keys(newData).length) {
        Object.keys(newData).forEach((key) => {
          var item = newData[key];
          //if (item.id && item.post_id) item.id = item.post_id;
          if (!completeDataObject[item.id]) {
            // if (state.wallId == 40726 || state.wallId == 43286) {
            // $fbSHareUrl = 'https://www.facebook.com/sharer.php?u=' . urlencode($post['Post']['link']) . '&description=' . urlencode($post['Post']['content'] . ' via #taggbox');
            // $twitterShareUrl = 'https://twitter.com/intent/tweet?text=' . urlencode($post['Post']['content'] . ' via #taggbox');
            // if ($post['Post']['type'] != 1) {
            //     $twitterShareUrl .= '&url=' . urlencode($post['Post']['link']);
            // }
            // $plusGoogleShareUrl = 'https://plus.google.com/share?url=' . urlencode($post['Post']['link']);
            // $linkedinShareUrl = 'https://www.linkedin.com/shareArticle?mini=true&url=' . urlencode($post['Post']['link']).'&summary='. urlencode($post['Post']['content'] . ' via #taggbox');
            // if (state.originUrl != null) {
            //const parsed = queryString.parse(window.location.search);
            const facebookParams = URL_DESTRUCTURE(item.share.facebook);
            const linkedinParams = URL_DESTRUCTURE(item.share.linkedin);
            const twitterParams = URL_DESTRUCTURE(item.share.twitter);
            //console.log("URL_DESTRUCTURE", linkedinParams)

            var faceBooktextContent = item.content;
            var twitterTextContent = item.content;
            var linkedinTextContent = item.content;

            if (facebookParams && facebookParams.description) faceBooktextContent = facebookParams.description;
            else faceBooktextContent = item.content;

            if (twitterParams && twitterParams["https://twitter.com/intent/tweet?text"]) twitterTextContent = twitterParams["https://twitter.com/intent/tweet?text"];
            else twitterTextContent = item.content;

            if (linkedinParams && linkedinParams.summary) linkedinTextContent = linkedinParams.summary;
            else linkedinTextContent = item.content;

            const refererUrl = state.originUrl;
            const description = encodeURIComponent(
              item.content + (state.userId !== 30879 ? " via #taggbox" : '')
            );
            const twitterDescription = encodeURIComponent(twitterTextContent);
            const linkdlnDescription = encodeURIComponent(linkedinTextContent);
            const facebookDescription = encodeURIComponent(faceBooktextContent);

            const shareUrl = encodeURIComponent(
              SHARE_ROOT_URL +
              window.btoa(
                item.id + "---" + state.wallId + "---" + refererUrl + "---" + table_name
              )
            );
            const facebookShareUrl = `https://www.facebook.com/sharer.php?u=${shareUrl}&description=${facebookDescription}`;
            let twitterShareUrl = `https://twitter.com/intent/tweet?text=${twitterDescription}`;
            if (item.type != 1) {
              twitterShareUrl = twitterShareUrl + "&url=" + shareUrl;
            }
            const plusGoogleShareUrl = `https://plus.google.com/share?url=${shareUrl}`;
            const linkedinShareUrl = `https://www.linkedin.com/shareArticle?mini=true&url=${shareUrl}&summary=${linkdlnDescription}`;
            item.shareDetail = {
              shareUrl: SHARE_ROOT_URL +
                window.btoa(
                  item.id + "---" + state.wallId + "---" + refererUrl + "---" + table_name
                ), description: facebookDescription
            }

            item.share = {
              ...item.share,
              facebook: facebookShareUrl,
              twitter: twitterShareUrl,
              linkedin: linkedinShareUrl,
              google: plusGoogleShareUrl,
            };
            // }
            // }
            completeDataObject[item.id] = item;
          }

          if (filteredDataObject[item.network.id]) {
            //if(isPreRenderData){
            if (!filteredDataObject[item.network.id].includes(item.id)) {
              filteredDataObject[item.network.id].push(item.id);
            }
            // } else {
            //filteredDataObject[item.network.id].push(item.id);
            //}
          } else {
            filteredDataObject[item.network.id] = [item.id];
          }
          if (!completeData.includes(item.id)) completeData.push(item.id);
        });
      }

      let postData = filterId && filteredDataObject[filterId]
        ? filteredDataObject[filterId]
        : filterId == 0
          ? completeData
          : [];


      /* ===================== START FOR Instagram and Instagram Business FILTER =====================*/
      /**
       * TODO: REMOVE THIS CODE AND GET API FIXED FOR INSTAGRAM 
       */
      // if (filterId != 0 && [2, 18].includes(filterId)) {
      //   if (filterId == 2) {
      //     postData = filterId && filteredDataObject[18]
      //       ? filteredDataObject[2] ? [...filteredDataObject[18], ...filteredDataObject[2]] : filteredDataObject[18]
      //       : filterId == 0
      //         ? completeData
      //         : [];
      //   }
      //   else if (filterId == 18) {
      //     postData = filterId && filteredDataObject[2]
      //       ? filteredDataObject[18] ? [...filteredDataObject[18], ...filteredDataObject[2]] : filteredDataObject[2]
      //       : filterId == 0
      //         ? completeData
      //         : [];
      //   }
      // }
      /* ===================== END =========================================*/
      //Custom Data
      if (state.isFreeAdsStatus) {
        freeAdsPosition = randomNumber(3, completeData.length, freeAdsPosition);
        //console.log("freeAdsPosition", freeAdsPosition)
        if (freeAdsPosition && freeAdsPosition.length > 0) {
          freeAdsPosition.map((itemIndex) => {
            let customPostIndex = parseInt(itemIndex);
            const item = {
              isPost: true,
              id: `free_add_${itemIndex}`, share: {
                status: 1, facebook: "", google: "", linkedin: "", twitter: ""
              },
              network: {
                class: "", color: "", icon: "", id: 0, name: ""
              },
              font: {
                authorColor: "",
                cardColor: "",
                font: 0,
                fontColor: "",
                fontsize: "",
                iconColor: "",
              }
            }
            if (completeData.length > customPostIndex) {
              if (completeData[customPostIndex] != item.id)
                completeData.splice(customPostIndex, 0, item.id);
              if (!completeDataObject[item.id]) {
                completeDataObject[item.id] = item;
              }
            }
          })
        }
      }



      if (Object.keys(customPostData).length) {
        Object.keys(customPostData).forEach((key) => {
          let item = customPostData[key];
          const { status, after, every } = item;
          if (status === 1) {
            let customPostIndex = parseInt(after);
            for (
              customPostIndex;
              customPostIndex <= completeData.length;
              customPostIndex = customPostIndex + parseInt(every)
            ) {
              if (completeData.length > customPostIndex) {
                if (completeData[customPostIndex] != item.id)
                  completeData.splice(customPostIndex, 0, item.id);
                if (!completeDataObject[item.id]) {
                  completeDataObject[item.id] = item;
                }
              }
            }
          }
        });
      }

      return {
        ...state,
        completeDataObject,
        completeData,
        postData: postData,
        filteredDataObject: filteredDataObject,
        preRender: isPreRenderData ? [] : state.preRender,
        freeAdsPosition
      };
    }
    case GET_CUSTOM_POST_DATA_SUCCESS: {
      return { ...state, customPostData: action.payload };
    }
    case UPDATE_FREE_ADS_STATUS: {
      return { ...state, isFreeAdsStatus: action.payload };
    }

    case GET_PRE_RENDER_POST_DATA_SUCCESS: {
      return { ...state, preRender: { ...state.preRender, ...action.payload } };
    }
    case GET_POST_DATA_ERROR: {
      return { ...state, postData: [], completeData: [] };
    }
    case GET_HORIZONTAL_POST_DATA_SUCCESS: {
      return {
        ...state,
        postData: action.payload,
        completeData: action.payload,
      };
    }
    // case GET_FILTERED_POST_DATA_SUCCESS: {
    //     return { ...state, postData: action.payload}
    // }
    // case GET_FILTERED_COMPLETE_POST_DATA_SUCCESS: {
    //     const filterId = filterNetworkId ? filterNetworkId : state.currentFilterNetworkId;
    //     return {
    //         ...state,
    //         postData: filterId ? [...state.completeData, ...state.preRender, ...payload].filter(item => { return item.network.id === filterId }) : [...state.completeData, ...payload],
    //         completeData: [...state.completeData, ...payload]
    //     }
    // }
    case GET_CURRENT_FILTER_ID: {
      return {
        ...state,
        currentFilterNetworkId: action.payload,
        appendData: { ...state.appendData, networkID: action.payload },
      };
    }
    case GET_APPEND_DATA_SUCCESS: {
      let appendData = {
        after: action.payload.after,
        networkID: action.payload.networkID,
        heightEvent: action.payload.heightEvent
          ? action.payload.heightEvent
          : state.appendData.heightEvent,
      };
      return { ...state, appendData };
    }
    case GET_NETWORK_ID_APPEND_DATA_SUCCESS: {
      let appendData = {
        after: state.appendData.after,
        networkID: action.payload,
      };
      return { ...state, appendData };
    }
    case GET_APPEND_DATA__ERROR: {
      return { ...state, appendData: { after: null, networkID: 0 } };
    }
    case GET_IFRAME_EVENT_SUCCESS: {
      let appendData = {
        after: state.appendData.after,
        networkID: state.appendData.networkID,
        heightEvent: action.payload,
      };
      return { ...state, appendData };
    }
    case SET_FILTER_ALL: {
      let appendData = {
        after: state.appendData.after,
        networkID: 0,
        heightEvent: state.appendData.heightEvent,
      };
      return {
        ...state,
        postData: state.completeData,
        networkID: 0,
        appendData,
      };
    }
    case SET_HAS_MORE_DATA: {
      return {
        ...state,
        hasMoreData: { ...state.hasMoreData, ...payload },
      };
    }
    case SET_URLS_ACCESSED_FOR_DATA: {
      return {
        ...state,
        urlsAccessed: [...state.urlsAccessed, payload],
        afterUsedList: [...state.afterUsedList, state.appendData.after]
      };
    }
    case WALL_ID_FETCHED: {
      return { ...state, wallId: action.payload };
    }
    case GET_WALL_SUCCESS: {
      return { ...state, userId: payload.Wall.owner };
    }
    case EMBED_URL_FETCHED: {
      return { ...state, originUrl: action.payload.data.url };
    }
    default:
      return { ...state };
  }
}
