import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
// import $ from 'jquery';
import { isMobile, isTablet } from "react-device-detect";
import WebFilters from '../../webFilters'
import { scrlBarComan } from '../customFunction'
import CustomPosts from '../../customPost'
import OwnBanner from '../../ownBanner'
import ThemeBanner from '../../themeBanner'
import PoweredBY from '../../poweredByComponent'
import { CUSTOM_SERVER_PATH } from '../../../constants'
import StoryThemeSlider from './slider'
import { IS_EMBED } from '../../../actions/api';
import { THEME_WIDTH_ADJUSTMENT_NEW_LAYOUT, customCenterHeight } from '../../../utils'
import SubmitPic from '../../submitPic'
class StoryTheme extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        windowWidth: document.getElementById('root').clientWidth,//$("#root").width(),
        windowHeight: window.innerHeight,
        adjustWidth: 0,
        cardNumber: 0
    }
    componentDidMount() {
        this.onResponsiveTheme();
        window.addEventListener("resize", () => {
            this.setState({ windowWidth: document.getElementById('root').clientWidth, windowHeight: window.innerHeight }, () => this.onResponsiveTheme())
        });
    }
    onResponsiveTheme = () => {
        const { wall } = this.props;
        const { windowWidth } = this.state;
        const { adjustWidth, cardNumber } = THEME_WIDTH_ADJUSTMENT_NEW_LAYOUT(windowWidth, wall);
        this.setState({ adjustWidth, cardNumber })

    }
    render() {
        const { wall, appendData, webFilters, loader, postData, startEmbed, wallId } = this.props;
        const { windowHeight, windowWidth, adjustWidth, cardNumber } = this.state;
        let fixedHeightStyle = null; let storyFixedHeight = {};
        let numberOfCoumn = (isMobile || windowWidth < 768) ? parseInt(wall.ThemeRule.mobileColumn) : parseInt(wall.ThemeRule.numberOfCoumn)
        if (appendData && appendData.heightEvent && appendData.heightEvent.data.fixedHeight == "0") {
            fixedHeightStyle = { overflow: 'auto', height: `${appendData.heightEvent.data.iframeHeight}px` }
            storyFixedHeight = { height: `${appendData.heightEvent.data.iframeHeight}px` }
        }
        let isCenter = false;
        let centerTheme = customCenterHeight(windowHeight, wall.ThemeRule.multiRow == 2 ? 600 : 300);
        if (startEmbed && startEmbed.heightEvent && startEmbed.heightEvent.data) isCenter = false;
        else isCenter = true;

        return <Fragment>
            <div id="controllerContent" style={isCenter && wall.ThemeRule.multiRow == 1 ? { paddingTop: `${wall.ThemeRule.multiRow == 1 && parseInt(centerTheme) > 0 ? 220 : parseInt((centerTheme / 3)) > 0 ? (centerTheme / 3) : 0}px` } : {}}>
                <div id="wPostData">
                    {
                        wall.UserRule.on_site_upload && !(window.wallId) ? <SubmitPic wallId={wallId} wall={wall} /> : null
                    }

                    <div id="scrlBarComanW" className={`newlayout-scr ${windowWidth < 767 ? `isMobilePadding` : `isMobilePadding-0`} scrlBarComan themesWrapper themeStart${wall.Personalization.widgetTheme} themeType${wall.Personalization.themeType} ${wall.UserRule.branding_lite === 1 ? `theme${wall.Personalization.widgetTheme}_poweredByMain` : null}`}
                        style={{ ...storyFixedHeight, ...scrlBarComan(wall.BackgroundImage.path, wall.ThemeRule.transparent, wall.Personalization.widgetTheme, false, window.innerHeight, 0, `${CUSTOM_SERVER_PATH}${wall.BackgroundImage.path}${wall.BackgroundImage.newName}`, wall.ThemeRule.backgroundColor, fixedHeightStyle) }} >
                        {wall.CustomBanner.status === 1 && wall.CustomBanner.banner_position === 1 ? <ThemeBanner CustomBanner={wall.CustomBanner}
                            CustomBannerImage={wall.CustomBannerImage}
                            LogoImage={wall.LogoImage}
                        /> : null}
                        {
                            wall.BannerImage.status === 1 ? <OwnBanner BannerImage={wall.BannerImage} /> : null}
                        <div className={`themeWrap  `}>
                            {wall.UserRule.branding_lite === 1 ? <PoweredBY widgetTheme={wall.Personalization.widgetTheme} numberOfCoumn={numberOfCoumn} /> : null}
                            <div id="ePosts"
                                className={`themeStart${wall.Personalization.widgetTheme} theme${wall.Personalization.widgetTheme} owl-carousel owl-theme owl-loaded owl-drag owl-carousel owl-theme owl-loaded owl-drag ${postData.length < parseInt(cardNumber) ? `slickCloneHide` : ``}  ${numberOfCoumn > 3 ? `column-icon-responsive` : ``}`}
                                style={
                                    { height: 'inherit' }} >
                                <StoryThemeSlider cardNumber={parseInt(cardNumber)} adjustWidth={parseInt(adjustWidth)} windowWidth={windowWidth} numberOfCoumn={numberOfCoumn} />
                            </div>

                            <CustomPosts />
                        </div>  {
                            wall.CustomBanner.status === 1 && wall.CustomBanner.banner_position === 2 ? < ThemeBanner CustomBanner={wall.CustomBanner}
                                CustomBannerImage={wall.CustomBannerImage}
                                LogoImage={wall.LogoImage}
                            /> : null}

                    </div>

                </div>
            </div >
        </Fragment >

    }
}
const mapStateToProps = state => {
    const { wallId, webFilters, wall, postData, appendData, loader } = state
    return {
        wallId: wallId.wallID,
        webFilters: webFilters.webFilters,
        wall: wall.wallData,
        appendData: postData.appendData,
        loader: loader,
        postData: postData.postData,
        completeDataObject: postData.completeDataObject,
    }
}
export default connect(mapStateToProps)(StoryTheme);