import React, { Component } from 'react';
import { connect } from 'react-redux';
import { isMobile } from "react-device-detect";
import '../../../css/post-theme.css'
import { getDataNextSteps, preRenderDataUpdateToParent, showPopUP } from '../../../actions/themeActions'
import SlideData from './sliderData'
import { URLDATA, findFromArray } from '../../../utils'

const getFontSize = (screenSize) => {
    if (screenSize > 100 && screenSize < 300) return '10px'
    else if (screenSize > 300 && screenSize < 499) return '12px'
    else return '14px'

}

class PostThemeSlider extends Component {
    state = {
        count: 0, data: [], isArrowLoader: false
    }
    componentDidMount() {
        this.onLoadData(this.props);
    }
    onLoadData = (props) => {
        const { postData, completeDataObject } = props;
        let data = []
        postData && postData.postData && postData.postData.length && postData.postData.map((itemData, index) => {
            let item = completeDataObject[itemData];
            data.push(item);
        })
        this.setState({ data, isArrowLoader: false })
    }
    onUpdateData = () => {
        setTimeout(() => this.requestData(), 1000)
    }
    requestData = () => {
        const { appendData, wall, postData, wallId } = this.props;
        const tstamp = Math.floor(Date.now() / 1000);
        let postCount = wall.ThemeRule.numberOfPosts;
        if (appendData.networkID && appendData.networkID != 0) {
            const filteredArr = Object.keys(postData.preRender).filter(item => postData.preRender[item].network.id == appendData.networkID);
            if (filteredArr.length > 0) {
                this.props.preRenderDataUpdateToParent(filteredArr);
            }
            if (filteredArr.length < postCount) {
                this.props.getDataNextSteps(wallId, tstamp, postCount - filteredArr.length, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent, true);
            }
            this.props.getDataNextSteps(wallId, tstamp, postCount, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent);
            //this.slider.slickNext();

        } else {
            if (Object.keys(postData.preRender).length > 0) {
                this.props.preRenderDataUpdateToParent(postData.preRender);
                this.props.getDataNextSteps(wallId, tstamp, postCount, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent);
                //this.slider.slickNext();
            }
            else {
                this.props.getDataNextSteps(wallId, tstamp, postCount, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent);
                this.props.preRenderDataUpdateToParent(postData.preRender);
                this.props.getDataNextSteps(wallId, tstamp, postCount, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent);
                //this.slider.slickNext();
            }


        }
    }
    onLoadPopup = (itemIndex, item) => event => {
        const { wall, appendData, postData, languageSetting, completeDataObject } = this.props;
        if (isMobile && wall.Personalization.mobilePopup === 0) window.open(item.link, '_blank')
        else {
            let updatePostData = postData.postData.map(item => completeDataObject[item])
            let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
            let currentIndex = findFromArray(filteredPostData, item.id, itemIndex)
            if (appendData.heightEvent != null && wall.Personalization.postFeatured === 1) {
                var obj = {
                    type: 'showPopUp',
                    data: { type: 'post', card: item, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall },
                }
                appendData.heightEvent.source.postMessage(obj, appendData.heightEvent.origin);
            }
            else if (wall.Personalization.postFeatured === 2) window.open(item.link, '_blank')
            else {
                if (wall.Personalization.postFeatured === 1) this.props.showPopUP({ type: 'post', card: item, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall })
            }

        }
    }
    onChangeData = event => {
        if (event && event.item) {
            if (parseInt(event.item.index) == (parseInt(event.item.count) - 1)) {
                this.setState({ isArrowLoader: true }, () => setTimeout(() => this.onUpdateData(), 2000))

            }
        }
    }
    events = {
        onChanged: this.onChangeData
    };
    requestData = () => {
        const { appendData, wall, postData, wallId, hasMoreData } = this.props;
        const tstamp = Math.floor(Date.now() / 1000);
        let postCount = wall.ThemeRule.numberOfPosts;
        // appendData.networkID
        if (appendData.networkID && appendData.networkID != 0) {
            const filteredArr = Object.keys(postData.preRender).filter(
                (item) => postData.preRender[item].network.id == appendData.networkID
            );
            if (filteredArr.length > 0) {
                this.props.preRenderDataUpdateToParent(filteredArr);
            }
            if (filteredArr.length < postCount) {
                if (hasMoreData)
                    this.props.getDataNextSteps(
                        wallId,
                        tstamp,
                        postCount - filteredArr.length,
                        appendData.networkID,
                        appendData.after,
                        postData.preRender,
                        appendData.heightEvent,
                        true
                    );
            }
            if (hasMoreData)
                this.props.getDataNextSteps(
                    wallId,
                    tstamp,
                    postCount,
                    appendData.networkID,
                    appendData.after,
                    postData.preRender,
                    appendData.heightEvent
                );
            //this.slider.slickNext();
        } else {
            if (Object.keys(postData.preRender).length > 0) {
                this.props.preRenderDataUpdateToParent(postData.preRender);
                if (hasMoreData)
                    this.props.getDataNextSteps(
                        wallId,
                        tstamp,
                        postCount,
                        appendData.networkID,
                        appendData.after,
                        postData.preRender,
                        appendData.heightEvent
                    );
                //this.slider.slickNext();
            } else {
                if (hasMoreData)
                    this.props.getDataNextSteps(
                        wallId,
                        tstamp,
                        postCount,
                        appendData.networkID,
                        appendData.after,
                        postData.preRender,
                        appendData.heightEvent
                    );
                this.props.preRenderDataUpdateToParent(postData.preRender);
                if (hasMoreData)
                    this.props.getDataNextSteps(
                        wallId,
                        tstamp,
                        postCount,
                        appendData.networkID,
                        appendData.after,
                        postData.preRender,
                        appendData.heightEvent
                    );

                //this.slider.slickNext();
            }
        }
    };
    componentWillReceiveProps(nextProps) {
        setTimeout(() => this.onLoadData(nextProps), 3000);
    }

    render() {
        const { wall, loader, languageSetting, adjustWidth, wallId, startEmbed } = this.props;
        const { data, count, isArrowLoader } = this.state;
        return data && data.length ? <SlideData wallId={wallId} wall={wall} loader={loader} languageSetting={languageSetting} adjustWidth={adjustWidth} data={data} getFontSize={getFontSize} onLoadPopup={this.onLoadPopup} events={this.events} isArrowLoader={isArrowLoader} onUpdateData={this.onUpdateData} startEmbed={startEmbed} /> : null
    }

}
const mapStateToProps = state => {
    const { wallId, webFilters, wall, postData, appendData, loader, startEmbed } = state
    return {
        startEmbed: startEmbed,
        wallId: wallId.wallID,
        webFilters: webFilters.webFilters,
        wall: wall.wallData,
        languageSetting: state.languageSetting,
        appendData: postData.appendData,
        loader: loader,
        postData: postData,
        completeDataObject: postData.completeDataObject,
        networkId: postData.appendData.networkID,
        hasMoreData: state.postData.hasMoreData[0] ? state.postData.hasMoreData[0].hasMoreData == false ? false : state.postData.hasMoreData[state.postData.appendData.networkID] ? state.postData.hasMoreData[state.postData.appendData.networkID].hasMoreData : true : true
    }
}
const mapDispatchToProps = dispatch => {
    return {
        getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
        preRenderDataUpdateToParent: (postData) => dispatch(preRenderDataUpdateToParent(postData)),
        showPopUP: (data) => dispatch(showPopUP(data)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(PostThemeSlider);