import React, { Component, Fragment } from 'react';
import { SuspenseImg } from '../common/SuspenseImg';
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, CAMERA_ICON_UPDATE_WITH_STYLE, IS_SCREEN_SHOT } from '../../utils'
import { NO_CAMERA_IMAGE, INSTAGRAM_IMAGE_BLANK } from '../../constants'
import MultiImageIcon from './multiImageIcon'

const cardStyleEnable = (screenSize) => {
    if (screenSize < 150) return 1;
    else if (screenSize > 150 && screenSize < 200) return 2;
    else return 3;
}
const loadImage = async src =>
    new Promise((resolve, reject) => {
        const img = new Image();
        img.onload = () => resolve(img);
        img.onerror = (...args) => reject(args);
        img.src = src;
    });
const VideoIconComponent = ({ network }) => {

    return network.id === 7 ?
        <div class="tb_youtube_post_ico">
            <img className="tb_youtube_ico_svg" src={`https://cloud.taggbox.com/widget/widget-embed/youtube.png`} alt="youTube" />
        </div>
        :
        <div className="tb_video_post_ico">
            <svg xmlns="http://www.w3.org/2000/svg" width="23.872" height="13.05" viewBox="0 0 23.872 13.05">
                <g id="Group_29" data-name="Group 29" transform="translate(0 0)">
                    <path id="Path_35" data-name="Path 35" d="M25.492,40.55H12.378A1.881,1.881,0,0,1,10.5,38.672V29.378A1.881,1.881,0,0,1,12.378,27.5H25.46a1.881,1.881,0,0,1,1.878,1.878V38.64A1.862,1.862,0,0,1,25.492,40.55Z" transform="translate(-10.5 -27.5)" fill="#fff"></path>
                    <path id="Path_36" data-name="Path 36" d="M67.5,32.692v5.666l5.252,2.96a.317.317,0,0,0,.477-.286V30.019a.327.327,0,0,0-.477-.286Z" transform="translate(-49.357 -29)" fill="#fff"></path>
                </g>
            </svg>
        </div>

}

const WrapperMediaPost = ({ children, widgetTheme, height, videoStyle }) => {
    return (widgetTheme === 50 || widgetTheme === 4 || (videoStyle && Object.keys(videoStyle).length == 0)) ? children : height != `100` ? <div className="media__wrap__">{children}</div> : children
}
class MediaPost extends Component {

    state = {
        height: '100',
        width: '100',
        error: '',
    }
    componentWillMount() {
        this.onLoad(this.props)
    }
    onLoad = async (props) => {
        const { item } = props;
        if (item && Object.keys(item).length > 0 && [4, 5, 3].includes(item.type)) {
            if (item.mediaHeight != "" && item.mediaWidth != "") {
                this.setState({
                    height: (parseInt(item.mediaHeight) * 100) / parseInt(item.mediaWidth),
                    width: (parseInt(item.mediaWidth) * 100) / parseInt(item.mediaHeight)
                })
            }
            else {
                const image = await loadImage(item.postFileNew);
                this.setState({
                    height: (image.height * 100) / image.width,
                    width: (image.width * 100) / image.height
                })
            }

        }
    }
    render() {
        const { isHover, imgHeight, isFile, type, file, postFileNew, network, isEmbed, imgWidth, item, widgetTheme, newCardWidth, clickLoadPopUp, numberOfCoumn, wallId } = this.props;
        const MinHeight = widgetTheme === 3 && numberOfCoumn > 3 ? { minHeight: 'auto' } : { minHeight: 150 }
        const { height } = this.state;
        let videoStyle = (widgetTheme === 50 || widgetTheme === 4) ? {} : height != `100` ? {
            paddingBottom: `${height}%`, width: '100%'
        } : {}
        return <Fragment>
            {
                isFile ? <div className="image" style={videoStyle} onClick={isHover && (widgetTheme === 50 || widgetTheme === 4) ? clickLoadPopUp : clickLoadPopUp}>
                    <WrapperMediaPost widgetTheme={widgetTheme} height={height} videoStyle={videoStyle}>
                        <div className={`${network.id === 7 ? 'tb_youtube_icon_00' : ''} ${(type === 3 || type === 5) && (item.imageList && item.imageList.length > 0) ? 'tb_multiple_media_01' : 'tb_multiple_media_0'}`}>
                            {(type === 3 || type === 5) ?
                                <VideoIconComponent network={network} />
                                : null}
                            {(item.imageList && item.imageList.length > 0) ?
                                <MultiImageIcon widgetTheme={widgetTheme} />
                                : null}
                        </div>

                        {
                            widgetTheme === 4 || widgetTheme === 50 ? <React.Fragment>
                                {type === 1 ? <img style={{ transition: 'opacity 0.25s ease-in 0.5s' }} className="blur-bg"
                                    data-src={item.file} src={IS_SCREEN_SHOT ? item.optmizedImg : item.file} data-is-optmized="0"
                                    data-link={item.link} data-load="0" data-network={network.id} data-wall-id={wallId} data-item-id={item.id}
                                    data-filter-id={item.filterId}
                                    onLoad={(e) => CAMERA_ICON_UPDATE_WITH_STYLE(e)}
                                    onError={(item.stories == 0) ? (e) => {
                                        IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)
                                    } : null} />

                                    :
                                    <img className="blur-bg" data-src={item.postFileNew} src={IS_SCREEN_SHOT ? item.optmizedImg : item.postFileNew} data-is-optmized="0"
                                        data-link={item.link} data-load="0" data-network={network.id} data-wall-id={wallId} data-item-id={item.id}
                                        onLoad={(e) => CAMERA_ICON_UPDATE_WITH_STYLE(e)}
                                        data-filter-id={item.filterId}
                                        onError={(item.stories == 0) ? (e) => {
                                            IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)
                                        } : null} style={{ transition: 'opacity 0.25s ease-in 0.5s' }} />
                                }

                            </React.Fragment> : <React.Fragment>

                                    {type === 1 ?
                                        <img style={{ minHeight: 250, width: '100%', height: widgetTheme === 4 ? imgWidth : 'auto', transition: 'opacity 0.25s ease-in 0.5s' }}
                                            data-src={item.file} src={IS_SCREEN_SHOT ? item.optmizedImg : item.file} data-is-optmized="0"
                                            data-link={item.link} data-load="0" data-network={network.id} data-wall-id={wallId} data-item-id={item.id}
                                            data-filter-id={item.filterId}
                                            onLoad={(e) => CAMERA_ICON_UPDATE_WITH_STYLE(e)}

                                            onError={(item.stories == 0) ? (e) => {
                                                IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)
                                            } : null} />
                                        :
                                        <img alt="" style={{ minHeight: MinHeight, width: '100%', height: widgetTheme === 4 ? imgWidth : 'auto', transition: 'opacity 0.25s ease-in 0.5s' }} data-src={item.postFileNew} src={IS_SCREEN_SHOT ? item.optmizedImg : item.postFileNew} data-is-optmized="0"
                                            data-link={item.link} data-load="0" data-network={network.id} data-wall-id={wallId} data-item-id={item.id}
                                            onLoad={(e) => CAMERA_ICON_UPDATE_WITH_STYLE(e)}
                                            data-filter-id={item.filterId}
                                            onError={(item.stories == 0) ? (e) => {
                                                IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)
                                            } : null} />
                                    }
                                </React.Fragment>
                        }
                    </WrapperMediaPost>
                </div> : null
            }

            {
                (type === 3 || type === 5) && !isFile ? <div className="image" style={videoStyle} onClick={clickLoadPopUp}>
                    <WrapperMediaPost widgetTheme={widgetTheme}>
                        <img src={`https://cloud.taggbox.com/widget/widget-embed/blank.jpg`} alt="" className="img-responsive" />
                        <VideoIconComponent network={network} />

                    </WrapperMediaPost> </div> : null

            }
        </Fragment>
    }
}

export default MediaPost;