const mapStateToProps = (state) => {
  return {
    wallId: state.wallId.wallID,
    webFilters: state.webFilters.webFilters,
    wall: state.wall.wallData,
    postData: state.postData,
    languageSetting: state.languageSetting,
    startEmbed: state.startEmbed,
    // appendData: state.appendData.appendData,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    filteredDataApplyUpdatePostDataExtendWithNetwordID: (
      wallId,
      networkId,
      heightEvent
    ) =>
      dispatch(
        filteredDataApplyUpdatePostDataExtendWithNetwordID(
          wallId,
          networkId,
          heightEvent
        )
      ),

    filterPostDataAppendWebFilter: (
      wallID,
      timeStamp,
      postCount,
      networkId,
      after,
      postData,
      heightEvent,
      completeData
    ) =>
      dispatch(
        filterPostDataAppendWebFilter(
          wallID,
          timeStamp,
          postCount,
          networkId,
          after,
          postData,
          heightEvent,
          completeData
        )
      ),
    networkIdUpdate: (networkId) => dispatch(networkIdUpdate(networkId)),
    saveNetworkIdOnFilter: (networkId) =>
      dispatch(saveNetworkIdOnFilter(networkId)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(WebFilters);
