import React, { Component } from 'react';
import moment from 'moment';
import AuthorAvatar from '../../common/AuthorAvatar';
import { isValidAuthorImage } from '../../../utils';

class PostNetwork extends Component {
    state = { isAuthorImagevalid: true }
    componentDidMount() {
        const { item } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({
            isAuthorImagevalid: isAuthorImagevalid
        });
    }
    render() {
        const { network, item, widgetTheme } = this.props;
        return <h4>
            <div className={`postNetwork ${(widgetTheme == 49 && item.type == 1) ? `onlytext-network` : ``}`} data-network={network.name} data-network-color={network.color}>
                <i className={`fa ${network.icon}`} style={{ color: item.font.iconColor }} >
                </i>
            </div>
            <div className="authorDetails" style={{ display: (item.instaHash === 0) ? 'none' : 'block' }}>
                {/* <img style={{ display: (item.postAuthor === 0) ? 'none' : '' }} src={item.author.picture}
                    onError={(e) => {
                        e.target.onerror = null; e.target.src = item.author.errorPic;
                    }}
                    alt="postAuthor" /> */}
                {this.state.isAuthorImagevalid && <img style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block' }} src={item.author.picture}
                    onError={(e) => {
                        this.setState({
                            isAuthorImagevalid: false
                        });
                        e.target.onerror = null;
                        e.target.src = item.author.errorPic
                        console.clear()
                    }} alt="postAuthor" />}


                {!this.state.isAuthorImagevalid && <AuthorAvatar style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'flex' }} username={item.author.name} color={item.network.color} />}
                <div className="postAuthorName" style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : '', color: item.font.authorColor }}>
                    {item.author.name}
                </div>

                <div className="authrHandleTime">
                    <span className="authrHandle" style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : '', color: item.font.authorColor }} target="_blank">
                        {/* {item.author.username.length>0?`@`:``}{item.author.username} */}
                        {item.author.username ? item.author.username.length > 0 ? `@` : `` : ''}{item.author.username}
                    </span>
                    <div className="sepratedot" style={{ display: (item.postAuthor === 0 || item.timePost === 0 || item.author.isInstaUser) ? 'none' : '', color: item.font.authorColor }}></div>
                    <span className="timePost" style={{ display: (item.timePost === 0 || item.author.isInstaUser) ? 'none' : '', color: item.font.authorColor }} data-livestamp={item.createdAt}>
                        {moment(new Date(item.createdAt * 1000)).fromNow()}
                    </span>
                </div>
                <div className="clearfix"></div>
            </div>
        </h4>
    }
}
export default PostNetwork;