import React, { Component } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import { checkStringInNotNull } from '../../themes/customFunction'
import { convertHtmlStringToRender } from '../../themes/customFunction'
import ContentPostConversion from '../contentPostConversion'
import AuthorAvatar from '../../common/AuthorAvatar';
import ErrorBoundary from '../../errorBoundary'
import { isValidAuthorImage, IS_YOU_TUBE_POST_HOVER_CONTENT } from '../../../utils';
const isMinimumPost = { transform: 'translate(-50%, -50%)', top: '50%', left: '50%' }
class GalleryPhotoContentPost extends Component {
    state = { isAuthorImageValid: true }
    componentDidMount() {
        const { item } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({
            isAuthorImagevalid: isAuthorImagevalid
        });
    }
    render() {
        const { item, network, widgetTheme, imgWidth, wall, clickLoadPopUp, personalization } = this.props;
        let firefoxAgent = navigator.userAgent.indexOf("Firefox") > -1;
        return <React.Fragment>
            <span onClick={clickLoadPopUp}>
                <div className="image_overlay_black"></div>
                <div className="postNetwork" data-network={network.name} data-network-color={network.color}>
                    <i className={`fa ${network.icon}`} style={{ color: item.font.iconColor }} >
                    </i>
                </div>

                {IS_YOU_TUBE_POST_HOVER_CONTENT(widgetTheme, network.id) && <div className={firefoxAgent ? `postContentCard galleryThemeOverlay` : `postContentCard`}>

                    <h4>
                        <div className="authorDetails" style={parseInt(wall.Personalization.minimumPostWidth) < 201 ? { ...isMinimumPost, display: (item.instaHash === 0) ? 'none' : '' } : { display: (item.instaHash === 0) ? 'none' : '' }}>
                            {/* <img style={{ display: (item.postAuthor === 0) ? 'none' : '' }} src={item.author.picture}
                            onError={(e) => {
                                e.target.onerror = null; e.target.src = item.author.errorPic;
                            }}
                            alt="postAuthor" /> */}
                            {this.state.isAuthorImagevalid && <img style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block' }} src={item.author.picture}
                                onError={(e) => {
                                    this.setState({
                                        isAuthorImagevalid: false
                                    });
                                    e.target.onerror = null;
                                    e.target.src = item.author.errorPic
                                    console.clear()
                                }} alt="postAuthor" />}
                            {!this.state.isAuthorImagevalid && <AuthorAvatar style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'flex' }} username={item.author.name} color={item.network.color} />}
                            <div className="postAuthorName" style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }}>
                                {item.author.name}
                                {/* <br/>{item.id} */}
                            </div>

                            <div className="authrHandleTime">
                                <span className="authrHandle" style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor, textOverflow: 'ellipsis', whiteSpace: 'nowrap', maxWidth: parseInt(wall.Personalization.minimumPostWidth) < 180 ? '60.5px' : '100px' }} target="_blank">
                                    {/* {item.author.username.length>0?`@`:``}{item.author.username} */}
                                    {item.author.username ? item.author.username.length > 0 ? `@` : `` : ''}{item.author.username}
                                </span>
                                <div className="sepratedot" style={{ display: (item.postAuthor === 0 || item.timePost === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }}></div>
                                <span className="timePost" style={{ display: (item.timePost === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }} data-livestamp={item.createdAt}>
                                    {moment(new Date(item.createdAt * 1000)).fromNow()}
                                </span>
                            </div>
                            <div className="clearfix"></div>
                        </div>
                    </h4>


                    <div className={`postedTxt ${personalization.textDecorate == 1 ? item.textDecoClass : ``} setFont  sGFfonte-${wall.ThemeRule.font_varient}`} style={{ fontFamily: wall.ThemeRule.css_font, display: (item.hideContent === 1 && item.type !== 1) ? 'none' : 'inline-grid', fontSize: item.font.fontsize, color: item.font.fontColor }}>
                        {checkStringInNotNull(item.contentTitle) ? <h1>{convertHtmlStringToRender(item.contentTitle)}</h1> : null}
                        {/* <p> {
                    wall.Personalization.hashtag_highlight !== 0 ?
                        wall.Personalization.hashtag_all === 1 ? reactStringReplace(item.content, /#(\w+)/g, (match, i) => (
                            <span key={match + i} style={{ color: wall.Personalization.hashtag_color, fontWeight: "bold" }}>#{match}</span>
                        ))
                            : wall.Personalization.hashtag_feed === 1 ? reactStringReplace(item.content, item.hash.hashString, (match, i) => (
                                <span key={match + i} style={{ color: wall.Personalization.hashtag_color, fontWeight: "bold" }}>{match}</span>
                            )) : item.content
                        : item.content
                }</p> */}
                        {parseInt(wall.Personalization.minimumPostWidth) > 201 ? <ErrorBoundary><ContentPostConversion item={item} network={network} widgetTheme={widgetTheme} imgWidth={imgWidth} /> </ErrorBoundary> : null}
                    </div>

                    {/* <div className="shareWrapper" data-color="" style={{ display: (item.share.status === 0 || item.network.id === 7) ? 'none' : 'block' }}>
                    <a target="_blank">
                        <i className="fa fa-share icon" aria-hidden="true" style={{ lineHeight: '30px' }}>
                        </i>
                    </a>

                    <a href={item.share.facebook} className="fbShare" target="_blank">
                        <i className="fa fa-facebook-f icon" aria-hidden="true" style={{ color: 'rgb(58, 87, 149)' }}>
                        </i>
                    </a>
                    <a href={item.share.twitter} className="twShare" target="_blank">
                        <i className="fa fa-twitter" aria-hidden="true">
                        </i>
                    </a>

                    <a href={item.share.linkedin} className="liShare" target="_blank">
                        <i className="fa fa-linkedin" aria-hidden="true">
                        </i>
                    </a>
                </div>
 */}

                </div>}
            </span>
        </React.Fragment>
    }
}

const mapStateToProps = state => {
    return {
        wall: state.wall.wallData
    }
}

export default connect(mapStateToProps)(GalleryPhotoContentPost);