import React, { Component, Suspense } from "react";
import { connect } from "react-redux";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { isMobile } from "react-device-detect";
import "../../../css/theme-story.css";
import MultiImageIcon from '../../cardPost/multiImageIcon'
import { URLDATA, IMG_LOAD_AFTER_COMPLETE_LAYOUT, CAMERA_ICON_UPDATE_WITH_STYLE, IS_SCREEN_SHOT, findFromArray } from '../../../utils'
import { INSTAGRAM_IMAGE_BLANK, NO_CAMERA_IMAGE } from '../../../constants'
import {
  getDataNextSteps,
  preRenderDataUpdateToParent,
  themePostTracking, showPopUP
} from "../../../actions/themeActions";

const styles = {
  postImg: {
    backgroundSize: "cover!important",
    backgroundPosition: "center center!important",
    opacity: 1,
    height: "inherit",
    width: "100%",
  },
  displayInline: {
    display: "inline-block",
  },
};
const SampleNextArrow = (props) => {
  const { className, style, onClick, count, hasMoreData, windowWidth, adjustWidth } = props;
  if (onClick == null && hasMoreData) props.onUpdateData();
  const fixedColumnStyle = isMobile ? { height: 30, width: 30, padding: 5, top: adjustWidth < 100 ? adjustWidth : adjustWidth } : {}
  return (
    <div className="owl-nav">
      <div
        className={`tryHorizontalStyle ${onClick == null && hasMoreData
          ? "tryHorizontalStyle-next-loader owl-next-right-arrow"
          : "tryHorizontalStyle-next owl-next"
          } ${className}`}
        style={{ ...style }}
        onClick={onClick}
      />
    </div>
  );
};

const SamplePrevArrow = (props) => {
  const { className, style, onClick, adjustWidth } = props;
  const fixedColumnStyle = isMobile ? { height: 30, width: 30, padding: 5, top: adjustWidth < 100 ? adjustWidth : adjustWidth } : {}
  return (
    <div className="owl-nav">
      <div
        className={`owl-pre tryHorizontalStyle tryHorizontalStyle-pre ${className} owl-pre-left-arrow`}
        style={{ ...style, display: "block", zIndex: 9 }}
        onClick={onClick}
      />
    </div>
  );
};

const VideoIconComponent = ({ network }) => {
  return network.id === 7 ?
    <div class="tb_youtube_post_ico">
      <img className="tb_youtube_ico_svg" src={`https://cloud.taggbox.com/widget/widget-embed/youtube.png`} alt="youTube" />
    </div>
    :
    <div className="tb_video_post_ico">
      <svg xmlns="http://www.w3.org/2000/svg" width="23.872" height="13.05" viewBox="0 0 23.872 13.05">
        <g id="Group_29" data-name="Group 29" transform="translate(0 0)">
          <path id="Path_35" data-name="Path 35" d="M25.492,40.55H12.378A1.881,1.881,0,0,1,10.5,38.672V29.378A1.881,1.881,0,0,1,12.378,27.5H25.46a1.881,1.881,0,0,1,1.878,1.878V38.64A1.862,1.862,0,0,1,25.492,40.55Z" transform="translate(-10.5 -27.5)" fill="#fff"></path>
          <path id="Path_36" data-name="Path 36" d="M67.5,32.692v5.666l5.252,2.96a.317.317,0,0,0,.477-.286V30.019a.327.327,0,0,0-.477-.286Z" transform="translate(-49.357 -29)" fill="#fff"></path>
        </g>
      </svg>
    </div>
}
class StoryThemeSlider extends Component {
  onUpdateData = () => setTimeout(() => this.requestData(), 1000);
  requestData = () => {
    const { appendData, wall, postData, wallId, hasMoreData } = this.props;
    const tstamp = Math.floor(Date.now() / 1000);
    let postCount = wall.ThemeRule.numberOfPosts;
    if (appendData.networkID && appendData.networkID != 0) {
      const filteredArr = Object.keys(postData.preRender).filter(
        (item) => postData.preRender[item].network.id == appendData.networkID
      );
      if (filteredArr.length > 0) {
        this.props.preRenderDataUpdateToParent(filteredArr);
      }
      if (filteredArr.length < postCount) {
        if (hasMoreData)
          this.props.getDataNextSteps(
            wallId,
            tstamp,
            postCount - filteredArr.length,
            appendData.networkID,
            appendData.after,
            postData.preRender,
            appendData.heightEvent,
            true
          );
      }
      if (hasMoreData)
        this.props.getDataNextSteps(
          wallId,
          tstamp,
          postCount,
          appendData.networkID,
          appendData.after,
          postData.preRender,
          appendData.heightEvent
        );
      //this.slider.slickNext();
    } else {
      if (Object.keys(postData.preRender).length > 0) {
        this.props.preRenderDataUpdateToParent(postData.preRender);
        if (hasMoreData)
          this.props.getDataNextSteps(
            wallId,
            tstamp,
            postCount,
            appendData.networkID,
            appendData.after,
            postData.preRender,
            appendData.heightEvent
          );
        //this.slider.slickNext();
      } else {
        if (hasMoreData)
          this.props.getDataNextSteps(
            wallId,
            tstamp,
            postCount,
            appendData.networkID,
            appendData.after,
            postData.preRender,
            appendData.heightEvent
          );
        this.props.preRenderDataUpdateToParent(postData.preRender);
        if (hasMoreData)
          this.props.getDataNextSteps(
            wallId,
            tstamp,
            postCount,
            appendData.networkID,
            appendData.after,
            postData.preRender,
            appendData.heightEvent
          );

        //this.slider.slickNext();
      }
    }
  };

  onLoadPopup = (itemIndex, item) => event => {
    const { wall, appendData, postData, languageSetting, completeDataObject, wallId } = this.props;
    if (isMobile && wall.Personalization.mobilePopup === 0) window.open(item.link, '_blank')
    else {
      let updatePostData = postData.postData.map(item => completeDataObject[item])
      let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
      let currentIndex = findFromArray(filteredPostData, item.id, itemIndex)
      if (appendData.heightEvent != null && wall.Personalization.postFeatured === 1) {
        var obj = {
          type: 'showPopUp',
          data: { type: 'post', card: item, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall },
        }
        appendData.heightEvent.source.postMessage(obj, appendData.heightEvent.origin);
      }
      else if (wall.Personalization.postFeatured === 2) window.open(item.link, '_blank')
      else {
        if (wall.Personalization.postFeatured === 1) this.props.showPopUP({ type: 'post', card: item, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall })
      }

    }
    themePostTracking({ action: 2, wall: parseInt(wallId), feed: item.feedId, post: item.referenceId })
  }


  render() {
    const {
      wallId,
      wall,
      postData,
      loader,
      completeDataObject,
      hasMoreData, cardNumber, adjustWidth, windowWidth, numberOfCoumn
    } = this.props;
    const infinite = !hasMoreData ? { infinite: true } : { infinite: false }

    var settings = {
      className: "center",
      infinite: true,
      //infinite: , //hasMoreData ? false : true,
      //centerPadding: "6  0px",
      // slidesToShow: loader.loader.gridColumn == false ? 4 : loader.loader.gridColumn,
      slidesToShow: numberOfCoumn == 0 ? cardNumber : numberOfCoumn,
      swipeToSlide: true,
      autoplay: wall.ThemeRule.slidePost == 1 ? true : false,
      speed: wall.ThemeRule.slidePost == 1 ? (1000) : 500,
      autoplaySpeed: wall.ThemeRule.slidePost == 1 ? (parseInt(wall.ThemeRule.slideDuration) * 1000) : 5000,
      nextArrow: (
        <SampleNextArrow windowWidth={windowWidth}
          onUpdateData={this.onUpdateData}
          hasMoreData={hasMoreData}
        />
      ),
      prevArrow: <SamplePrevArrow windowWidth={windowWidth} adjustWidth={adjustWidth} />,
      responsive: [
        {
          breakpoint: 1300,
          settings: {
            // slidesToShow: loader.loader.gridColumn == false ? 3 : loader.loader.gridColumn,
            slidesToShow: numberOfCoumn == 0 ? cardNumber : numberOfCoumn,
            initialSlide: 0,
            infinite: true,
            //infinite: hasMoreData ? false : true,
            autoplay: wall.ThemeRule.slidePost == 1 ? true : false,
            speed: wall.ThemeRule.slidePost == 1 ? (1000) : 500,
            autoplaySpeed: wall.ThemeRule.slidePost == 1 ? (parseInt(wall.ThemeRule.slideDuration) * 1000) : 5000,
            nextArrow: (
              <SampleNextArrow windowWidth={windowWidth}
                onUpdateData={this.onUpdateData}
                hasMoreData={hasMoreData}
              />
            ),
            prevArrow: <SamplePrevArrow windowWidth={windowWidth} adjustWidth={adjustWidth} />,
          },
        },
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: numberOfCoumn == 0 ? cardNumber : numberOfCoumn,
            infinite: true,
            /// infinite: hasMoreData ? false : true,
            autoplay: wall.ThemeRule.slidePost == 1 ? true : false,
            speed: wall.ThemeRule.slidePost == 1 ? (1000) : 500,
            autoplaySpeed: wall.ThemeRule.slidePost == 1 ? (parseInt(wall.ThemeRule.slideDuration) * 1000) : 500,
            nextArrow: (
              <SampleNextArrow windowWidth={windowWidth}
                onUpdateData={this.onUpdateData}
                hasMoreData={hasMoreData}
              />
            ),
            prevArrow: <SamplePrevArrow windowWidth={windowWidth} adjustWidth={adjustWidth} />,
          },
        },
        {
          breakpoint: 700,
          settings: {
            slidesToShow: numberOfCoumn == 0 ? cardNumber : numberOfCoumn,
            infinite: true,

            //infinite: hasMoreData ? false : true,
            autoplay: wall.ThemeRule.slidePost == 1 ? true : false,
            speed: wall.ThemeRule.slidePost == 1 ? (1000) : 500,
            autoplaySpeed: wall.ThemeRule.slidePost == 1 ? (parseInt(wall.ThemeRule.slideDuration) * 1000) : 500,
            nextArrow: (
              <SampleNextArrow windowWidth={windowWidth}
                onUpdateData={this.onUpdateData}
                hasMoreData={hasMoreData}
              />
            ),
            prevArrow: <SamplePrevArrow windowWidth={windowWidth} adjustWidth={adjustWidth} />,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: numberOfCoumn == 0 ? cardNumber : numberOfCoumn,
            infinite: true,
            //infinite: hasMoreData ? false : true,
            autoplay: wall.ThemeRule.slidePost == 1 ? true : false,
            speed: wall.ThemeRule.slidePost == 1 ? (1000) : 500,
            autoplaySpeed: wall.ThemeRule.slidePost == 1 ? (parseInt(wall.ThemeRule.slideDuration) * 1000) : 500,
            nextArrow: (
              <SampleNextArrow windowWidth={windowWidth}
                onUpdateData={this.onUpdateData}
                hasMoreData={hasMoreData}
              />
            ),
            prevArrow: <SamplePrevArrow windowWidth={windowWidth} adjustWidth={adjustWidth} />,
          },
        },
      ],
    };

    return (
      <div className={(postData && postData.postData && postData.postData.length >= 4 ? `` : `postcount`)}>
        <Slider ref={(c) => (this.slider = c)} {...settings}>
          {!loader.webFilterLoader &&
            postData &&
            postData.postData &&
            postData.postData.length &&
            postData.postData.map((itemData, index) => {
              let item = completeDataObject[itemData];
              if (IS_SCREEN_SHOT && postData.postData && ((postData.postData.length - 1) == index)) {
                setTimeout(() => {
                  let dataUpdateImage = document.querySelectorAll("img[data-is-optmized='0']");
                  if (dataUpdateImage && dataUpdateImage.length > 0) {
                    dataUpdateImage.forEach((item) => {
                      item.setAttribute("src", item.getAttribute("data-src"))
                      item.setAttribute("data-is-optmized", "1")
                    })
                  }
                }, 1300)
              }
              return (
                <Suspense fallback={null} key={index}>
                  <div style={{ padding: wall.Personalization.padding / 2 }}>
                    <div
                      id={`postId${item.id}`}
                      key={index}
                      className={`feedId${item.feedId
                        } owl-lazy postItem item flatCard ${item.type == 1 ? "onlyTextCard" : ""
                        }`}
                      style={{ height: adjustWidth, width: (wall.Personalization.padding / 2) == 0 ? 'auto' : adjustWidth }}
                      data-post-id={item.id}
                      data-highlight={item.highlight}
                      data-pin={item.pin}
                      data-created={item.createdAt}
                    >
                      <div
                        className="post newlayout"
                        style={{ backgroundColor: item.font.cardColor }}
                      >
                        {item.file != null && item.file !== "" ? (
                          <div className="image">


                            <div className={`${item.network.id === 7 ? 'tb_youtube_icon_00' : ''} ${(item.type == 3 || item.type == 5) && (item.imageList && item.imageList.length > 0) ? 'tb_multiple_media_01' : 'tb_multiple_media_0'}`}>
                              {(item.type == 3 || item.type == 5) ? (
                                <VideoIconComponent network={item.network} />
                              ) : null}
                              {(item.imageList && item.imageList.length > 0) ? <MultiImageIcon widgetTheme={wall.Personalization.widgetTheme} /> : null}
                            </div>

                            {item.type == 1 ? (
                              <img
                                className="postImg completeLazyLoad"
                                data-link={item.link} data-load="0" data-network={item.network.id}
                                onLoad={(e) => CAMERA_ICON_UPDATE_WITH_STYLE(e)}
                                data-src={item.file} src={IS_SCREEN_SHOT ? item.optmizedImg : item.file} data-is-optmized="0"
                                data-wall-id={wallId} data-item-id={item.id}
                                data-filter-id={item.filterId}
                                style={{
                                  // backgroundImage: `url(${item.file})`,
                                  transition: `all 0s ease-in-out 2s`,
                                }}
                                onError={(item.stories == 0) ? (e) => {
                                  IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)
                                } : null}
                              />
                            ) : (
                                <img
                                  className="postImg completeLazyLoad"
                                  data-src={item.postFileNew} src={IS_SCREEN_SHOT ? item.optmizedImg : item.postFileNew} data-is-optmized="0"
                                  data-link={item.link} data-load="0" data-network={item.network.id}
                                  data-wall-id={wallId} data-item-id={item.id}
                                  data-filter-id={item.filterId}
                                  style={{
                                    // backgroundImage: `url(${item.postFileNew})`,
                                    transition: `all 0s ease-in-out 2s`,
                                  }}
                                  onLoad={(e) => CAMERA_ICON_UPDATE_WITH_STYLE(e)}
                                  onError={(item.stories == 0) ? (e) => {
                                    IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)
                                  } : null}
                                />
                              )}

                          </div>
                        ) : (item.type == 3 || item.type == 5) && !item.file ? (
                          <div className="image">
                            <div
                              className="postImg"
                              style={{ backgroundImage: `url(https://cloud.taggbox.com/widget/widget-embed/blank.jpg)` }}
                            >
                              <VideoIconComponent network={item.network} />
                            </div>
                          </div>
                        ) : null}


                        <div
                          className="postContentCard"
                          onClick={this.onLoadPopup(index, item)}
                        >
                          {item.content ? (
                            <div
                              className={`postedTxt setFont sGFfonte-${wall.ThemeRule.font_varient}`}
                              style={{
                                fontFamily: wall.ThemeRule.css_font,
                                display:
                                  item.hideContent == 1 && item.type != 1
                                    ? "none"
                                    : "",
                                fontSize: item.font.fontsize,
                                color: item.font.fontColor,
                              }}
                            >


                            </div>
                          ) : null}
                          <div
                            className="postNetwork"
                            data-network={item.network.name}
                            data-network-color={item.network.color}
                          >
                            <i
                              className={`fa ${item.network.icon == 'fa-google' ? 'fa-google-new' : item.network.icon}`}
                              style={{ color: item.font.iconColor }}
                            ></i>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div> </Suspense>
              );
            })}
        </Slider>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  const {
    wallId,
    webFilters,
    wall,
    postData,

    appendData,
    loader,
  } = state;
  return {
    wallId: wallId.wallID,
    webFilters: webFilters.webFilters,
    wall: wall.wallData,
    languageSetting: state.languageSetting,
    appendData: postData.appendData,
    loader: loader,
    postData: postData,
    completeDataObject: postData.completeDataObject,
    networkId: postData.appendData.networkID,
    hasMoreData: state.postData.hasMoreData[0] ? state.postData.hasMoreData[0].hasMoreData == false ? false : state.postData.hasMoreData[state.postData.appendData.networkID] ? state.postData.hasMoreData[state.postData.appendData.networkID].hasMoreData : true : true
  }
}
const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
    preRenderDataUpdateToParent: (postData) => dispatch(preRenderDataUpdateToParent(postData)),
    showPopUP: (data) => dispatch(showPopUP(data)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(StoryThemeSlider);
