import React, { Component } from 'react';
import moment from 'moment'
import { Helmet } from "react-helmet";
import { getThemeImageFromWallID, CloudUrl } from '../../utils'
import { hashtagCampaignImage } from '../../actions/themeActions'



export default class PageHeadSection extends Component {
    state = {
        hashtag_campaign_image: ""
    }
    componentDidMount() {
        const isHastag = window.location.href.includes("hashtag_campaign") ? true : false
        const { wallId, wallData } = this.props;
        if (isHastag) {
            hashtagCampaignImage({
                wallId: wallId,
                domainName: `https://widget.taggbox.com/${wallId}?screenshot=true`,
                screenshotSize: ["1200x624"]
            }).then(({ data }) => {
                const { responseCode, responseData } = data;
                if (responseCode == 200 && responseData.length > 0 && responseData[0] && responseData[0].s3Url) {
                    const currentTime = moment().milliseconds() + 1000 * (moment().seconds() + 60 * (moment().minutes() + 60 * moment().hours()));
                    this.setState({ hashtag_campaign_image: `${responseData[0].s3Url}?v=${currentTime}` })
                }
                else {
                    const shareData = getThemeImageFromWallID(wallData.Personalization.widgetTheme)
                    this.setState({ hashtag_campaign_image: shareData })
                }
            })
                .catch((error) => {
                    const shareData = getThemeImageFromWallID(wallData.Personalization.widgetTheme)
                    this.setState({ hashtag_campaign_image: shareData })
                })
        }
    }
    render() {
        const { PageTitle, PageMetaData, PageStyle, wallId, widgetName, widgetOGImage, wallData } = this.props;
        const { hashtag_campaign_image } = this.state;
        const titleMeta = `${widgetName} - Taggbox Widget`;
        const isHastag = window.location.href.includes("hashtag_campaign") ? true : false
        return isHastag ? <Helmet>
            {PageMetaData != null ? <meta {...PageMetaData} /> : null}
            {PageTitle != null ? <title>{titleMeta}</title> : null}
            {PageStyle != null ? <style type="text/css">{PageStyle}</style> : null}

            {/* <!-- Open Graph / Facebook --> */}

            <meta property="og:type" content="website" />
            <meta property="og:url" content={`https://widget.taggbox.com/${wallId}`} />
            <meta property="og:title" content={titleMeta} />
            <meta property="og:description" content={`${widgetName} hashtag campaign created using Taggbox Widget.`} />
            <meta property="og:image" content={hashtag_campaign_image} />


            <meta property="type" content="website" />
            <meta property="url" content={`https://widget.taggbox.com/${wallId}`} />
            <meta property="title" content={titleMeta} />
            <meta property="description" content={`${widgetName} hashtag campaign created using Taggbox Widget.`} />
            <meta property="image" content={hashtag_campaign_image} />

            {<style>
                {`@font-face {
                    font-family: 'TaggboxSocialFonts';
                    src:  url('${CloudUrl}/web-fonts/TaggboxIcon.eot?jkob7x');
                    src:  url('${CloudUrl}/web-fonts/TaggboxIcon.eot?jkob7x#iefix') format('embedded-opentype'),
                        url('${CloudUrl}/web-fonts/TaggboxIcon.ttf?jkob7x') format('truetype'),
                        url('${CloudUrl}/web-fonts/TaggboxIcon.woff?jkob7x') format('woff'),
                        url('${CloudUrl}/web-fonts/TaggboxIcon.svg?jkob7x#TaggboxSocialFonts') format('svg');
                    font-weight: normal;
                    font-style: normal;
                    font-display: block;
                }`}
            </style>
            }

            {
                wallData.ThemeRule && Object.keys(wallData.ThemeRule).length > 0 && wallData.ThemeRule.link_font && wallData.ThemeRule.link_font != "" ? <link href={`https://fonts.googleapis.com/css?family=${wallData.ThemeRule.link_font}:${wallData.ThemeRule.font_varient}`} rel="stylesheet" /> : null
            }
            {
                wallData.CustomBanner && Object.keys(wallData.CustomBanner).length > 0 && wallData.CustomBanner.subtitle_link_font != "" && wallData.CustomBanner.subtitle_link_font ? <link href={`https://fonts.googleapis.com/css?family=${wallData.CustomBanner.subtitle_link_font}:${wallData.CustomBanner.subtitle_font_varient}`} rel="stylesheet" /> : null
            }
            {
                wallData.CustomBanner && Object.keys(wallData.CustomBanner).length > 0 && wallData.CustomBanner.title_link_font != "" && wallData.CustomBanner.title_link_font ? <link href={`https://fonts.googleapis.com/css?family=${wallData.CustomBanner.title_link_font}:${wallData.CustomBanner.title_font_varient}`} rel="stylesheet" /> : null
            }
            {
                (wallData.Wall && wallData.Wall.google_tacking_status) ? <script async src="https://www.googletagmanager.com/gtag/js"></script> : null
            }

            {
                (wallData.Wall && wallData.Wall.google_tacking_status) ? <script async>
                    {`window.dataLayer = window.dataLayer || [];
                    function gtag() {dataLayer.push(arguments); }
                    gtag("js", new Date());
                    window.gaTokenClient = '${wallData.Wall.google_tacking_code}';
                    gtag("event", "page_view", {
                        "send_to": window.gaTokenClient,
                    "anonymize_ip": true,
                    });`}

                </script> : null
            }
        </Helmet> : <Helmet>
                {PageMetaData != null ? <meta {...PageMetaData} /> : null}
                {PageTitle != null ? <title>{titleMeta}</title> : null}
                {PageStyle != null ? <style type="text/css">{PageStyle}</style> : null}

                {/* new meta tags */}
                <meta name="description" content={`${widgetName} featuring Social Feed using Taggbox Widget. It helps brands to curate & embed social feeds, user reviews, UGC etc. on websites to boost sales, traffic, & engagement.`} />
                {/* <!-- Open Graph / Facebook --> */}

                <meta property="og:type" content="website" />
                <meta property="og:url" content={`https://widget.taggbox.com/${wallId}`} />
                <meta property="og:title" content={titleMeta} />
                <meta property="og:description" content={`${widgetName} featuring Social Feed using Taggbox Widget. It helps brands to curate & embed social feeds, user reviews, UGC etc. on websites to boost sales, traffic, & engagement.`} />
                <meta property="og:image" content={widgetOGImage} />

                {/* <!-- Twitter --> */}
                <meta property="twitter:card" content={widgetOGImage} />
                <meta property="twitter:url" content={`https://widget.taggbox.com/${wallId}`} />
                <meta property="twitter:title" content={titleMeta} />
                <meta property="twitter:description" content={`${widgetName} featuring Social Feed using Taggbox Widget. It helps brands to curate & embed social feeds, user reviews, UGC etc. on websites to boost sales, traffic, & engagement.`} />
                <meta property="twitter:image" content={widgetOGImage} />
                {<style>
                {`@font-face {
                    font-family: 'TaggboxSocialFonts';
                    src:  url('${CloudUrl}/web-fonts/TaggboxIcon.eot?jkob7x');
                    src:  url('${CloudUrl}/web-fonts/TaggboxIcon.eot?jkob7x#iefix') format('embedded-opentype'),
                        url('${CloudUrl}/web-fonts/TaggboxIcon.ttf?jkob7x') format('truetype'),
                        url('${CloudUrl}/web-fonts/TaggboxIcon.woff?jkob7x') format('woff'),
                        url('${CloudUrl}/web-fonts/TaggboxIcon.svg?jkob7x#TaggboxSocialFonts') format('svg');
                    font-weight: normal;
                    font-style: normal;
                    font-display: block;
                }`}
                </style>
                }
                {
                    wallData.ThemeRule && Object.keys(wallData.ThemeRule).length > 0 && wallData.ThemeRule.link_font && wallData.ThemeRule.link_font != "" ? <link href={`https://fonts.googleapis.com/css?family=${wallData.ThemeRule.link_font}:${wallData.ThemeRule.font_varient}`} rel="stylesheet" /> : null
                }
                {
                    wallData.CustomBanner && Object.keys(wallData.CustomBanner).length > 0 && wallData.CustomBanner.subtitle_link_font != "" && wallData.CustomBanner.subtitle_link_font ? <link href={`https://fonts.googleapis.com/css?family=${wallData.CustomBanner.subtitle_link_font}:${wallData.CustomBanner.subtitle_font_varient}`} rel="stylesheet" /> : null
                }
                {
                    wallData.CustomBanner && Object.keys(wallData.CustomBanner).length > 0 && wallData.CustomBanner.title_link_font != "" && wallData.CustomBanner.title_link_font ? <link href={`https://fonts.googleapis.com/css?family=${wallData.CustomBanner.title_link_font}:${wallData.CustomBanner.title_font_varient}`} rel="stylesheet" /> : null
                }
                {
                    (wallData.Wall && wallData.Wall.google_tacking_status) ? <script async src="https://www.googletagmanager.com/gtag/js"></script> : null
                }
                {
                    (wallData.Wall && wallData.Wall.google_tacking_status) ? <script async>
                        {`window.dataLayer = window.dataLayer || [];
                    function gtag() {dataLayer.push(arguments); }
                    gtag("js", new Date());
                    window.gaTokenClient = '${wallData.Wall.google_tacking_code}';
                    gtag("event", "page_view", {
                        "send_to": window.gaTokenClient,
                    "anonymize_ip": true,
                    });`}

                    </script> : null
                }

            </Helmet>
    }
}