import { ONSITE_TOCKEN_UPDATE } from './reducersKeys'
const initialState = { csrf_token: null }
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case ONSITE_TOCKEN_UPDATE: {
            return { ...state, csrf_token: action.payload }
        }
        default:
            return { ...state }
    }

}