import React, { Component, Suspense, Fragment } from 'react';
import { connect } from 'react-redux';
import Scrollbar from 'smooth-scrollbar';
import Masonry from 'react-masonry-component';
import WebFilters from '../../webFilters'
import { scrlBarComan } from '../customFunction'
import CardPost from '../../cardPost'
import OwnBanner from '../../ownBanner'
import ThemeBanner from '../../themeBanner'
import ShowMore from '../../showMore'
import PoweredBY from '../../poweredByComponent'
import { CUSTOM_SERVER_PATH } from '../../../constants'
import '../../../css/widget-theme.css'
import { IS_EMBED } from '../../../actions/api'
import { THEME_WIDTH_ADJUSTMENT_WIDGET_THEME, IS_SCREEN_SHOT } from '../../../utils'
import SubmitPic from '../../submitPic'

class ModernCardTheme extends Component {
    constructor(props) {
        super(props);
        this.myRef = React.createRef()
    }
    state = {
        windowWidth: window.innerWidth,
        windowHeight: window.innerHeight,
    }
    componentDidMount() {
        window.addEventListener("resize", () => {
            this.setState({ windowWidth: window.innerWidth, windowHeight: window.innerHeight })
        });
        const { postData, loadMoreRequest } = this.props;
        if (postData.length) this.setState({ initialCount: postData.length })

        var ScrollbarOptions = {
            damping: 0.1,
            thumbMinSize: 10
        };
        const Scroll = Scrollbar.init(document.querySelector('.scrlBarComan'), ScrollbarOptions);
        Scroll.addListener((s) => {
            let documentHeight = document.body.scrollHeight;
            let load = (parseInt(documentHeight) / 2) > 250 ? (parseInt(documentHeight) / 2) : 250;
            if ((s.offset.y) >= (s.limit.y - load)) {
                loadMoreRequest().then((scrollResponse) => {

                })
            }
        })
    }
    render() {
        const { wall, postData, loader, appendData, completeDataObject, webFilters, startEmbed, wallId } = this.props;
        const { windowWidth, windowHeight } = this.state;
        const { adjustWidth } = THEME_WIDTH_ADJUSTMENT_WIDGET_THEME(windowWidth, wall);
        let widgetEditorStyle = { height: 500 }; let widgetClass = window.innerWidth > adjustWidth ? "col-md-4 offset-md-4" : "";
        let fixedHeightStyle = { overflow: 'auto!important' };
        if (appendData && appendData.heightEvent && appendData.heightEvent.data.fixedHeight == "0") {
            fixedHeightStyle = { overflow: 'auto!important', msOverflowStyle: 'none', scrollbarWidth: 'none', height: `${appendData.heightEvent.data.iframeHeight}px` }
        }
        if (startEmbed && startEmbed.heightEvent && startEmbed.heightEvent.data) {
            widgetEditorStyle = {}
            widgetClass = "";
        }
        else {
            widgetEditorStyle = { marginTop: window.innerWidth > 767 ? 100 : 0, height: 500, maxWidth: window.innerWidth > 767 ? 380 : '100%' };
            widgetClass = "col-md-4 offset-md-4"
        }

        //className={`poweredBy49 poweredBy49tag `}
        return <Fragment>
            {
                wall.UserRule.on_site_upload && !(window.wallId) ? <SubmitPic wallId={wallId} wall={wall} /> : null
            }
            <div id="wPostData" className={`embedPostFull  themesWrapper themeStart49 themeType${wall.Personalization.themeType}`}
                style={fixedHeightStyle}>
                {
                    wall.BannerImage.status === 1 ? <OwnBanner BannerImage={wall.BannerImage}
                    /> : null}
                <div id="scrlBarComanW" className={`scrlBarComan themesWrapper themeStart${wall.Personalization.widgetTheme} themeType${wall.Personalization.themeType}   ${widgetClass}`}
                    style={{ ...scrlBarComan(wall.BackgroundImage.path, wall.ThemeRule.transparent, wall.Personalization.widgetTheme, false, window.innerHeight, 0, `${CUSTOM_SERVER_PATH}${wall.BackgroundImage.path}${wall.BackgroundImage.newName}`, wall.ThemeRule.backgroundColor, fixedHeightStyle), ...widgetEditorStyle }} >
                    {wall.CustomBanner.status === 1 && wall.CustomBanner.banner_position === 1 ? <ThemeBanner CustomBanner={wall.CustomBanner}
                        CustomBannerImage={wall.CustomBannerImage}
                        LogoImage={wall.LogoImage}
                    /> : null}



                    <WebFilters appendData={appendData} />
                    <div className="themeWrap">
                        <Masonry
                            id="ePosts"
                            className={`isotope theme${wall.Personalization.widgetTheme} single_widget_theme`}
                            elementType={'div'}
                            options={{
                                transitionDuration: 0,
                                percentPosition: true,
                                itemSelector: '.postItem',
                                columnWidth: adjustWidth,
                            }}
                            disableImagesLoaded={false}
                            updateOnEachImageLoad={false}
                            imagesLoadedOptions={{}}
                            enableResizableChildren={true}

                        > {!loader.webFilterLoader && postData && postData.length ? postData.map((item, index) => {
                            if (IS_SCREEN_SHOT && postData && ((postData.length - 1) == index)) {
                                setTimeout(() => {
                                    let dataUpdateImage = document.querySelectorAll("img[data-is-optmized='0']");
                                    if (dataUpdateImage && dataUpdateImage.length > 0) {
                                        dataUpdateImage.forEach((item) => {
                                            item.setAttribute("src", item.getAttribute("data-src"))
                                            item.setAttribute("data-is-optmized", "1")
                                        })
                                    }
                                }, 1300)
                            }
                            return <Suspense fallback={null} key={index}><CardPost itemIndex={index}
                                key={index}
                                item={completeDataObject[item]}
                                minimumWidth={wall.Personalization.minimumPostWidth}
                                windowWidth={windowWidth}
                                windowHeight={windowHeight}
                                adjustWidth={adjustWidth}
                                initialAnimateCssStatus={parseInt(index) < parseInt(this.state.initialCount) ? true : false}
                            /></Suspense>
                        }) : null
                            } </Masonry>
                    </div>
                    {this.props.hasMoreData === false ? <ShowMore appendData={appendData} /> : null}
                    {
                        wall.CustomBanner.status === 1 && wall.CustomBanner.banner_position === 2 ? < ThemeBanner CustomBanner={wall.CustomBanner}
                            CustomBannerImage={wall.CustomBannerImage}
                            LogoImage={wall.LogoImage}
                        /> : null}

                </div>

            </div>
        </Fragment >
    }
}
const mapStateToProps = state => {
    const { wallId, webFilters, wall, postData, appendData, loader, startEmbed } = state
    return {
        wallId: wallId.wallID,
        webFilters: webFilters.webFilters,
        wall: wall.wallData,
        appendData: postData.appendData,
        loader: loader,
        postData: postData.postData,
        completeDataObject: postData.completeDataObject,
        startEmbed: startEmbed,
        hasMoreData: postData.hasMoreData[0] ? postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true
    }
}
export default connect(mapStateToProps)(ModernCardTheme);