import React, { Component, Suspense, Fragment } from "react";
import { connect } from "react-redux";
import Masonry from "react-masonry-component";
import $ from 'jquery';
import '../../../css/classic-photo.css'
import WebFilters from "../../webFilters";
import { scrlBarComan, themeLeft } from "../customFunction";
import CardPost from "../../cardPost";
import OwnBanner from "../../ownBanner";
import ThemeBanner from "../../themeBanner";
import ShowMore from "../../showMore";
import PoweredBY from "../../poweredByComponent";
import { CUSTOM_SERVER_PATH } from "../../../constants";
import { THEME_WIDTH_ADJUSTMENT_CLASSIC_PHOTO_CARD, IS_SCREEN_SHOT } from '../../../utils'
import SubmitPic from '../../submitPic'
class ClassicPhotoTheme extends Component {
  state = {
    //windowWidth: window.innerWidth,
    windowWidth: $("#root").width(),
    windowHeight: window.innerHeight,
    initialCount: 0,
    adjustWidth: 0,
    themeOpacity: 0
  };
  componentDidMount() {
    this.onResponsiveTheme();
    window.addEventListener("resize", () => {
      this.setState({
        windowWidth: $("#root").width(),
        windowHeight: window.innerHeight,
      }, () => this.onResponsiveTheme());
    });
    const { appendData, postData, loadMoreRequest } = this.props;
    if (postData.length) this.setState({ initialCount: postData.length });
    if (
      appendData &&
      appendData.heightEvent &&
      appendData.heightEvent.data.fixedHeight == "0"
    ) {
      document
        .getElementById("scrlBarComanW")
        .addEventListener("scroll", (event) => {
          let fixedHeightScrollBar = document.getElementById("scrlBarComanW");
          let documentHeight = document.body.scrollHeight;
          if (
            documentHeight + fixedHeightScrollBar.scrollTop >=
            fixedHeightScrollBar.scrollHeight - 150
          ) {
            loadMoreRequest().then((scrollResponse) => { });
          }
        });
    }
  }
  onResponsiveTheme = () => {
    const { wall, wallId } = this.props;
    const { windowWidth } = this.state;
    const { adjustWidth } = THEME_WIDTH_ADJUSTMENT_CLASSIC_PHOTO_CARD(windowWidth, wall);
    this.setState({ adjustWidth })
  }
  render() {
    const {
      wall,
      postData,
      loader,
      appendData,
      completeDataObject,
      webFilters, wallId
    } = this.props;
    const { windowWidth, windowHeight, adjustWidth, themeOpacity } = this.state;


    let fixedHeightStyle = null;
    if (
      appendData &&
      appendData.heightEvent &&
      appendData.heightEvent.data.fixedHeight == "0"
    ) {
      fixedHeightStyle = {
        overflow: "auto",
        msOverflowStyle: "none",
        scrollbarWidth: "none",
        height: `${appendData.heightEvent.data.iframeHeight}px`,
      };
    }
    return <Fragment>
      {
        wall.UserRule.on_site_upload && !(window.wallId) ? <SubmitPic wallId={wallId} wall={wall} /> : null
      }
      <div id="wPostData" style={fixedHeightStyle} className={themeOpacity == 0 ? `theme-rendering-opacity` : `theme-rendering-opacity-t`} >

        {wall.CustomBanner.status === 1 &&
          wall.CustomBanner.banner_position === 1 ? (
            <ThemeBanner
              CustomBanner={wall.CustomBanner}
              CustomBannerImage={wall.CustomBannerImage}
              LogoImage={wall.LogoImage}
            />
          ) : null}
        <div
          id="scrlBarComanW"
          className={`scrlBarComan themesWrapper themeStart${
            wall.Personalization.widgetTheme
            } themeType${wall.Personalization.themeType}`}
          style={scrlBarComan(
            wall.BackgroundImage.path,
            wall.ThemeRule.transparent,
            wall.Personalization.widgetTheme,
            false,
            window.innerHeight,
            0,
            `${CUSTOM_SERVER_PATH}${wall.BackgroundImage.path}${wall.BackgroundImage.newName}`,
            wall.ThemeRule.backgroundColor,
            fixedHeightStyle
          )}
        >
          {wall.BannerImage.status === 1 ? (
            <OwnBanner BannerImage={wall.BannerImage} />
          ) : null}
          <WebFilters appendData={appendData} />
          <div className="themeWrap" style={{ left: themeLeft() }}>

            <Masonry
              id="ePosts"
              className={`isotope theme${wall.Personalization.widgetTheme}`}
              elementType={"div"}

              options={{
                transitionDuration: 0,
                percentPosition: true,
                itemSelector: ".postItem",
                columnWidth: adjustWidth,
                resize: true
              }}
              disableImagesLoaded={false}
              updateOnEachImageLoad={false}
              imagesLoadedOptions={{}}
              style={{ width: "100%!important" }}
              enableResizableChildren={true}
            >
              {" "}
              {!loader.webFilterLoader && postData && postData.length
                ? postData.map((item, index) => {
                  if (index == (postData.length - 1)) {
                    if (themeOpacity == 0) setTimeout(() => this.setState({ themeOpacity: 1 }), 100)
                  }
                  if (IS_SCREEN_SHOT && postData && ((postData.length - 1) == index)) {
                    setTimeout(() => {
                      let dataUpdateImage = document.querySelectorAll("img[data-is-optmized='0']");
                      if (dataUpdateImage && dataUpdateImage.length > 0) {
                        dataUpdateImage.forEach((item) => {
                          item.setAttribute("src", item.getAttribute("data-src"))
                          item.setAttribute("data-is-optmized", "1")
                        })
                      }
                    }, 1300)
                  }
                  return <Suspense fallback={null} key={index}>
                    <CardPost
                      itemIndex={index}
                      key={index}
                      item={completeDataObject[item]}
                      minimumWidth={wall.Personalization.minimumPostWidth}
                      windowWidth={windowWidth}
                      windowHeight={windowHeight}
                      adjustWidth={adjustWidth}
                      initialAnimateCssStatus={
                        parseInt(index) < parseInt(this.state.initialCount)
                          ? true
                          : false
                      }
                    />
                  </Suspense>
                })
                : null}{" "}
            </Masonry>{" "}

          </div>{" "}
          <ShowMore appendData={appendData} />{" "}
          {wall.CustomBanner.status === 1 &&
            wall.CustomBanner.banner_position === 2 ? (
              <ThemeBanner
                CustomBanner={wall.CustomBanner}
                CustomBannerImage={wall.CustomBannerImage}
                LogoImage={wall.LogoImage}
              />
            ) : null}
        </div>
      </div>
    </Fragment >

  }
}
const mapStateToProps = (state) => {
  const {
    wallId,
    webFilters,
    wall,
    postData,

    appendData,
    loader,
  } = state;
  return {
    wallId: wallId.wallID,
    webFilters: webFilters.webFilters,
    wall: wall.wallData,
    appendData: postData.appendData,
    loader: loader,
    postData: postData.postData,
    completeDataObject: postData.completeDataObject,
  };
};

export default connect(mapStateToProps)(ClassicPhotoTheme);
