import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import './css/users.css'
//import './css/all-fonts.css'
import './css/custom-google-fonts.css'
import './css/social-icon.css'
import './css/walls-common.css';
import './css/no-post.css';
import { heightEvent, getDataNextSteps, preRenderDataUpdateToParent, setUrlAccessed, getThemeDataWithWallID, onSiteTokenUpdate } from './actions/themeActions'
import ErrorExtends from './components/themes/error'
import PlanLimit from './components/themes/error/planLimit'
import Theme from './components/themes';
import { IS_EMBED } from './actions/api';
import { WINDOW_ON_LOAD, ON_GET_WALL_ID, checkRestangularNullData, checkWidgetThemeAutoScrollEnable } from './utils'
//import isSocket from './utils/socket'

class App extends PureComponent {
    constructor(props) {
        super(props);
        this.onWallLoad();
        this.state = { isAutoScrollEnable: false }
    }
    onWallLoad = () => {
        window.addEventListener("message", this.onMessageReceived);

        WINDOW_ON_LOAD();
    }
    componentDidMount() {
        setTimeout(() => {
            //Commented as not needed in embeds - need to check better way
            if (!IS_EMBED) {
                const renderWallID = ON_GET_WALL_ID()
                //isSocket.sendData("taggbox_themes_render", { isSocket: 1, id: renderWallID, wallID: renderWallID })
                this.props.getThemeDataWithWallID(renderWallID, null)
                window.addEventListener("scroll", this.AutoLoadScrollData);
            }
        }, 200);
    }
    AutoLoadScrollData = event => {
        const { isAutoScrollEnable } = this.state;
        if (!isAutoScrollEnable) this.setState({ isAutoScrollEnable: true }, () => this.onAutoScrollQueryRequest().then((scrollResponse) => this.setState({ isAutoScrollEnable: false })))
    }
    onAutoScrollQueryRequest = async () => {
        const { wall, wallId, loader } = this.props;
        if (wall && wall.Personalization.autoScrollStatus === 1 && checkRestangularNullData(this.props.postData.hasMoreData[this.props.postData.currentFilterNetworkId == undefined ? 0 : this.props.postData.currentFilterNetworkId]) && checkWidgetThemeAutoScrollEnable(wall.Personalization.widgetTheme)) {
            const tstamp = Math.floor(Date.now() / 1000);
            let postCount = wall.ThemeRule.numberOfPosts;
            if (this.props.appendData.networkID && this.props.appendData.networkID != 0) {
                const filteredArr = Object.keys(this.props.postData.preRender).filter(item => this.props.postData.preRender[item].network.id == this.props.appendData.networkID);
                if (filteredArr.length > 0) {
                    this.props.preRenderDataUpdateToParent([]);
                }
                if (filteredArr.length < postCount) {
                    await this.props.getDataNextSteps(wallId, tstamp, postCount - filteredArr.length, this.props.appendData.networkID, this.props.appendData.after, this.props.postData.preRender, this.props.appendData.heightEvent, true);
                }
                await this.props.getDataNextSteps(wallId, tstamp, postCount, this.props.appendData.networkID, this.props.appendData.after, this.props.postData.preRender, this.props.appendData.heightEvent);
            } else {
                if (Object.keys(this.props.postData.preRender).length > 0) {
                    this.props.preRenderDataUpdateToParent(this.props.postData.preRender);
                    this.props.getDataNextSteps(wallId, tstamp, postCount, this.props.appendData.networkID, this.props.appendData.after, this.props.postData.preRender, this.props.appendData.heightEvent);
                }
                else {
                    await this.props.getDataNextSteps(wallId, tstamp, postCount, this.props.appendData.networkID, this.props.appendData.after, this.props.postData.preRender, this.props.appendData.heightEvent, true);
                    this.props.getDataNextSteps(wallId, tstamp, postCount, this.props.appendData.networkID, this.props.appendData.after, this.props.postData.preRender, this.props.appendData.heightEvent);
                }
            }
        }
    }

    onMessageReceived = event => {
        if (event && typeof (event) === 'object' && event.data.hasOwnProperty("type"))
            if (event.data.type === "startEmbed") {
                const renderWallID = ON_GET_WALL_ID();
                //isSocket.sendData("taggbox_themes_render", { isSocket: 1, id: renderWallID, wallID: renderWallID })
                this.props.heightEvent(event, renderWallID);
                this.props.getThemeDataWithWallID(renderWallID, this.props.appendData.heightEvent)
                setInterval(() => {
                    const { wall, postData, startEmbed, errorWithMessage } = this.props;
                    let referersData = {}; let scrollHeight = document.body.scrollHeight < 150 ? 150 : document.body.scrollHeight;
                    if (wall && wall != null && postData && postData.appendData && startEmbed.heightEvent && startEmbed.heightEvent.data) {
                        referersData = { refererlink: wall.User.referePageLink, referer: wall.User.referePage }
                        if (startEmbed.heightEvent.data.fixedHeight == "0") {
                            if (startEmbed.heightEvent != null) {
                                startEmbed.heightEvent.source.postMessage({ type: 'getHeight', data: { height: startEmbed.heightEvent.data.iframeHeight, iframe: this.props.startEmbed.heightEvent.data.id, ...referersData } }, "*");
                            }
                            else {
                                window.top.postMessage({ type: 'getHeight', data: { height: startEmbed.heightEvent.data.iframeHeight, iframe: this.props.startEmbed.heightEvent.data.id, ...referersData } }, "*");
                            }
                        }
                        else {
                            if (startEmbed.heightEvent != null) {
                                startEmbed.heightEvent.source.postMessage({ type: 'getHeight', data: { height: !scrollHeight ? 150 : scrollHeight, iframe: this.props.startEmbed.heightEvent.data.id, ...referersData } }, "*")
                            }
                            else {
                                window.top.postMessage({ type: 'getHeight', data: { height: !scrollHeight ? 150 : scrollHeight, iframe: this.props.startEmbed.heightEvent.data.id, ...referersData } }, "*")
                            }



                        }
                    }
                    else {
                        if (errorWithMessage.errorWithMessage != null) {
                            if (errorWithMessage.errorWithMessage && errorWithMessage.errorWithMessage.wall && errorWithMessage.errorWithMessage.wall.User) {
                                const { referePage, referePageLink } = errorWithMessage.errorWithMessage.wall.User;
                                referersData = { refererlink: referePageLink, referer: referePage }
                            }
                            window.top.postMessage({ type: 'getHeight', data: { height: !scrollHeight ? 150 : scrollHeight, iframe: this.props.startEmbed.heightEvent.data.id, ...referersData } }, "*");
                        }
                    }

                }, 5);
            } else if (event.data.type === "loadMore") {
                const { appendData } = this.props;
                this.onAutoScrollQueryRequest().then((scrollResponse) => {
                    if (appendData.heightEvent != null) {
                        appendData.heightEvent.source.postMessage({ type: 'loadComplete', loadstatus: true }, appendData.heightEvent.origin)
                    }

                })
            }
            else if (event.data.type === "token_private_onsite") {
                this.props.onSiteTokenUpdate(event.data.onsite_token)
            }
    }
    render() {
        const { loader, appendData, errorWithMessage } = this.props;
        return loader && loader.loader != null && errorWithMessage.errorWithMessage == null && !errorWithMessage.planLimit ? <Theme appendData={appendData} loadMoreRequest={this.onAutoScrollQueryRequest} /> : errorWithMessage.planLimit ? <PlanLimit innerHeight={window.innerHeight} /> : errorWithMessage.errorWithMessage != null ? <ErrorExtends innerHeight={window.innerHeight} /> : null
    }
}
const mapStateToProps = state => {
    return {
        startEmbed: state.startEmbed,
        loader: state.loader,
        wallId: state.wallId.wallID,
        wall: state.wall.wallData,
        postData: state.postData,
        appendData: state.postData.appendData,
        errorWithMessage: state.error
    }
}
const mapDispatchToProps = dispatch => {
    return {
        heightEvent: (event, wallID) => dispatch(heightEvent(event, wallID)),
        getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInState) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInState)),
        preRenderDataUpdateToParent: (postData) => dispatch(preRenderDataUpdateToParent(postData)),
        setUrlAccessed: (url) => dispatch(setUrlAccessed(url)),
        getThemeDataWithWallID: (Id, heightEvent) => dispatch(getThemeDataWithWallID(Id, heightEvent)),
        onSiteTokenUpdate: (token) => dispatch(onSiteTokenUpdate(token)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(App);