import React, { Component } from 'react';
import { isMobile } from "react-device-detect";
import { URLDATA, IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO } from '../../../../utils'
import MultiImageIcon from '../../../cardPost/multiImageIcon'
import ReactPlayer from 'react-player'
import Iframe from 'react-iframe';
import TaggboxAds from '../../../taggboxAds'
import { CLOUD_URL, PRICE_PAGE, BRANDING_CARDS_LINK } from './../../../../constants'

const VideoComponent = ({ data, WallId }) => {
    return (data.type === 3 || data.type === 5) && <>
        {
            (data.link.indexOf("youtube") >= 0 || data.link.indexOf("youtu.be") >= 0) &&

            <ReactPlayer url={`${data.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
                muted
                config={{
                    file: {
                        attributes: {
                            autoPlay: true,
                            muted: true
                        }
                    }
                }} />
        }


        {
            (data.link.indexOf("vimeo") >= 0) &&
            <ReactPlayer url={`${data.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
                muted
                config={{
                    file: {
                        attributes: {
                            autoPlay: true,
                            muted: false
                        }
                    }
                }} />
        }
        {
            ((data.network.id && data.network.id == 30) && data.link.indexOf("vk") >= 0) &&
            <Iframe
                className="tb-detail-image-iframe"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                src={`${data.mediaUrl}&autoplay=1`}
                allowFullScreen="true"
                style={{ width: "100%!important" }}
                data-filter-id={data.filterId}
                data-stories={data.stories}
                data-type="video"
                data-network={data.networkId}
                data-link={data.link}
                data-data-id={data.id}
                data-load={0}
            ></Iframe>

        }

        {

            (data.link.indexOf("soundcloud") >= 0) &&
            <Iframe
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                src={`${data.mediaUrl}`}
                allowFullScreen="true"
                style={{ width: "100%!important" }}
            ></Iframe>

        }

        {
            (data.link.indexOf("linkedin") >= 0 || data.link.indexOf("twitter") >= 0 || data.link.indexOf("instagram") >= 0 || data.link.indexOf("tumblr") >= 0 || data.link.indexOf("instagram") >= 0) && <video
                src={`${data.mediaUrl}`}
                autoPlay={true}
                muted={true}
                onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                data-type="video"
                data-stories={data.stories}
                data-filter-id={data.filterId}
                data-network={data.networkId}
                data-postid={data.id}
                data-link={data.link}
                data-load={0}
                data-wall-id={WallId} data-item-id={data.id}
            ></video>
        }
    </>

}
class StoryCard extends Component {

    render() {
        const { item, windowHeight, windowWidth, ThemeRule, Personalization, wallId, sliderActiveFocus, startEmbed } = this.props;
        return (
            <div id={`post_ID-${item.id}`} className="tb_st_card_container">
                <div className="tb_st_card_wrapper" style={{ backgroundColor: ThemeRule.cardColor }}>
                    <div className="tb_st_card_wrapper_00">
                        <div className="tb_st_card_content_0">
                            <div className="tb_st_media_img">
                                {item.isPost ? <TaggboxAds link={`${PRICE_PAGE}?utm_source=${startEmbed.heightEvent.origin ? startEmbed.heightEvent.origin : "https://widget.taggbox.com/"}&${BRANDING_CARDS_LINK}`} imgPath={`${CLOUD_URL}/widget-embed/branding/br_card-01.svg`} alt="Taggbox Branding" /> : <>
                                    {(item.imageList && item.imageList.length > 0) ? <MultiImageIcon widgetTheme={Personalization.widgetTheme} /> : null}
                                    {(item.type === 2 || item.type === 4) ?
                                        <img className="tb_st_img00" src={item.file} data-link={item.link} data-load="0" data-network={item.network.id} data-item-id={item.id}
                                            data-filter-id={item.filterId}
                                            data-wall-id={wallId}
                                            alt="" onError={(item.stories == 0) ? (e) => {
                                                IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)
                                            } : null} />
                                        : sliderActiveFocus && item.network.id!=28 ? <VideoComponent data={item} WallId={wallId} />
                                            : <img className="tb_st_img00" src={item.file} data-link={item.link} data-load="0" data-network={item.network.id} data-item-id={item.id}
                                                data-wall-id={wallId}
                                                alt="" onError={(item.stories == 0) ? (e) => {
                                                    IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)
                                                } : null} />

                                    }

                                    { (sliderActiveFocus && item.network.id==28) || !sliderActiveFocus ? (
                                        <div className="tb_st_play_btn">
                                            {item.network.id == 7 ? (
                                                <svg xmlns="http://www.w3.org/2000/svg" width="167" height="167" viewBox="0 0 167 167">
                                                    <g id="Group_6566" data-name="Group 6566" transform="translate(7430 20108)">
                                                        <path id="Subtraction_4" data-name="Subtraction 4" d="M79.5,109.716c-.5,0-49.832-.038-62.112-3.294A19.848,19.848,0,0,1,3.313,92.6C.038,80.5,0,55.113,0,54.858S.038,29.14,3.313,17.081A19.932,19.932,0,0,1,17.393,3.254C29.673.037,79.008,0,79.5,0S129.34.037,141.62,3.254a20.083,20.083,0,0,1,14.121,13.827c3.148,12.092,3.312,37.522,3.313,37.777s-.038,25.678-3.313,37.737a20.039,20.039,0,0,1-14.121,13.827C129.341,109.679,80,109.716,79.5,109.716ZM63.643,31.761V78.2l41.495-23.343Z" transform="translate(-7426 -20080.068)" fill="#FF0000" />
                                                    </g>
                                                </svg>
                                            ) : (
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="90" height="90" viewBox="0 0 90 90">
                                                        <path id="tb_st_play_btn_Path_1" fill="#fff" data-name="Path 1" d="M50,5A45,45,0,1,0,95,50,45.046,45.046,0,0,0,50,5Zm0,6A39,39,0,1,1,11,50,38.955,38.955,0,0,1,50,11ZM38.781,28.938A3.014,3.014,0,0,0,36.063,32V68q-.008.156,0,.313a3.062,3.062,0,0,0,1.588,2.421,2.846,2.846,0,0,0,2.818-.077l15-9,15-9a3.119,3.119,0,0,0,0-5.313l-15-9-15-9a2.854,2.854,0,0,0-1.687-.406Zm3.156,8.344,10.594,6.344L63.094,50,52.531,56.375,41.938,62.719V37.281Z" transform="translate(-5 -5)" />
                                                    </svg>
                                                )}
                                        </div>
                                    ) : null}
                                </>}
                            </div>
                            {item.isPost ? null : <div className="tb_st_content_wrap">
                                {(Personalization.postAuthor) ?
                                    <div className="tb_st_author_img">
                                        {(item.postAuthor != 0 || !item.author.isInstaUser) ? <div className="tb_st_author_img_00" style={{ backgroundImage: `url(${item.author.picture.replace(/\s/g, '')})` }}></div> : ''}
                                    </div> : ''
                                }
                                <div className="tb_st_author_info">
                                    <div className={`tb_st_social_icon fa ${item.network.icon}`} style={(ThemeRule.iconType == 1) ? { color: ThemeRule.iconColor } : (ThemeRule.iconType == 505) ? { color: item.font.iconColor } : ''}></div>
                                    <div className="tb_st_author_001">
                                        {(item.hash.hashString) ? <div className={`tb_st_hashtag sGFfonte-${ThemeRule.font_varient}`} style={{ color: ThemeRule.fontColor, fontFamily: ThemeRule.css_font }}>{item.hash.hashString}</div> : ''}
                                        {(item.author.username && Personalization.postAuthor) ? <div className="tb_st_author_name" style={{ color: ThemeRule.authorColor }}>{item.author.username}</div> : ""}
                                    </div>
                                </div>
                            </div>}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default StoryCard;