import React, { PureComponent, Fragment } from "react";
import { connect } from "react-redux";
import { isMobile, isTablet } from "react-device-detect";
import $ from 'jquery';
import CardPostFile from './cardPostFile'
import ShareActions from './shareActions'
import { renderScreenSize, PRICE_PAGE, BRANDING_CARDS_LINK, CLOUD_URL } from '../../constants'
import ShareWrapper from './themeContentPost/shareWrapper'
import { themePostTracking, showPopUP } from '../../actions/themeActions'
import { URLDATA, findFromArray } from '../../utils'
import TaggboxAds from '../taggboxAds'
class CardPost extends PureComponent {
  state = {
    isHover: false
  }


  onLoadPopup = event => {

    const { item, wall, appendData, postData, itemIndex, languageSetting, completeDataObject, wallId } = this.props;

    if (isMobile && wall.Personalization.mobilePopup === 0) window.open(item.link, '_blank')
    else {
      let updatePostData = postData.map(item => completeDataObject[item])
      let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
      let currentIndex = findFromArray(filteredPostData, item.id, itemIndex)
      if (appendData.heightEvent != null && wall.Personalization.postFeatured === 1) {

        var obj = {
          type: 'showPopUp',
          data: { type: 'post', card: item, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall },
        }
        appendData.heightEvent.source.postMessage(obj, appendData.heightEvent.origin);
      }
      else if (wall.Personalization.postFeatured === 2) window.open(item.link, '_blank')
      else {
        if (wall.Personalization.postFeatured === 1) this.props.showPopUP({ type: 'post', card: item, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall })
      }

    }

    themePostTracking({ action: 2, wall: wallId, feed: item.feedId, post: item.referenceId })
  }

  onUpdateHover = event => {
    const { isHover } = this.state;
    this.setState({ isHover: !isHover })
  }

  render() {
    const {
      startEmbed,
      wallId,
      itemIndex,
      wall,
      item,
      completeDataObject,
      adjustWidth,
      initialAnimateCssStatus,
      windowWidth, keyId,
      initialCount
    } = this.props;
    //const { adjustWidth } = this.onLoadFunction();
    // console.warn("adjustWidth",adjustWidth)
    //${initialAnimateCssStatus ? `try_animate` : `try_animate_Mv`}


    let numberOfCoumn = (wall.Personalization.widgetTheme === 3 || wall.Personalization.widgetTheme === 4) ? (isMobile || windowWidth < 768) ? parseInt(wall.ThemeRule.mobileColumn) : parseInt(wall.ThemeRule.numberOfCoumn) : parseInt(wall.ThemeRule.numberOfCoumn)

    const newCardWidth = renderScreenSize(
      wall.Personalization.widgetTheme,
      item.highlight,
      isMobile,
      isTablet,
      adjustWidth,
      item.cardWidth,
      wall, numberOfCoumn
    );

    const social_action = wall.Personalization.widgetTheme === 3 ? numberOfCoumn > 2 ? false : true : true
    if (parseInt(initialCount) + 1 == parseInt(itemIndex)) {
      $(".monumentalst").focus();
    }

    return <>{
      <div

        id={`postId${item.id}`}
        className={`feedId${
          item.feedId
          } postItem item flatCard col-lg-2 col-sm-6 col-md-6 col-xs-12 ${
          item.network.class
          } ${item.type === 1 ? `onlyTextCard` : ``} try_animate ${numberOfCoumn == 2 && wall.Personalization.widgetTheme === 3 ? `slick-slide-numberOfCoumn` : ``} `}
        data-post-id={item.id}
        data-created={item.createdAt}
        data-highlight={item.highlight}
        data-pin={item.pin}
        style={{
          padding: wall.Personalization.padding / 2,
          margin: 0,
          height:
            wall.Personalization.widgetTheme === 4 ||
              wall.Personalization.widgetTheme === 50
              ? (wall.Personalization.widgetTheme === 50 || (wall.Personalization.widgetTheme === 4 && parseInt(numberOfCoumn) == 0)) &&
                (isMobile &&
                  !isTablet)
                ? ""
                : newCardWidth
              : "auto",
          width:
            (isMobile && !isTablet)
              ? (wall.Personalization.widgetTheme === 4 ||
                wall.Personalization.widgetTheme === 50) &&
                parseInt(wall.Personalization.minimumPostWidth) < 175
                ? newCardWidth
                : wall.Personalization.widgetTheme === 49
                  ? "100%"
                  : "100%"
              : wall.Personalization.widgetTheme === 49 &&
                windowWidth > newCardWidth
                ? "100%"
                : newCardWidth,
          //maxWidth: item.highlight==1?newCardWidth:"auto",
          maxWidth: (isMobile && !isTablet) ? (((wall.Personalization.widgetTheme === 4 || (wall.Personalization.widgetTheme === 3 && parseInt(numberOfCoumn) > 0)) && parseInt(numberOfCoumn) > 0)) ? newCardWidth : '100%' : (wall.Personalization.widgetTheme === 49) ? (windowWidth > newCardWidth) ? "100%" : newCardWidth : newCardWidth,

          //maxWidth: item.highlight == 1 ? (item.cardWidth * 2) : item.cardWidth,
          minWidth: "auto",
        }}
      >

        <div

          className={`post ${parseInt(initialCount) + 1 == parseInt(itemIndex) ? 'monumentalst' : ``}`}
          tabIndex={parseInt(initialCount) < parseInt(itemIndex) ? '1' : ``}
          style={{ backgroundColor: item.font.cardColor }}
          onMouseOver={this.onUpdateHover}
          onMouseOut={this.onUpdateHover}
        >
          <div className="postContent">
            {item.isPost ? <TaggboxAds link={`${PRICE_PAGE}?utm_source=${startEmbed.heightEvent.origin ? startEmbed.heightEvent.origin : "https://widget.taggbox.com/"}&${BRANDING_CARDS_LINK}`} imgPath={`${CLOUD_URL}/widget-embed/branding/br_card-01.svg`} alt="Taggbox Branding" /> : <CardPostFile
              isHover={this.state.isHover}
              newCardWidth={newCardWidth}
              item={item}
              imgWidth={
                adjustWidth >= item.cardWidth ? adjustWidth : item.cardWidth
              }
              widgetTheme={wall.Personalization.widgetTheme}
              clickLoadPopUp={this.onLoadPopup}
              numberOfCoumn={numberOfCoumn}
              personalization={wall.Personalization}
              wall={wall}
              wallId={wallId}
            />
            }

          </div>

          {!(item.isPost) && item.socialAction && (social_action) ? (
            <ShareActions
              item={item}
              widgetTheme={wall.Personalization.widgetTheme}
            />
          ) : null}

          {!(item.isPost) && wall.Personalization.widgetTheme === 49 ? (
            <ShareWrapper item={item} network={item.network} />
          ) : null}
        </div>
      </div>
    } </>
  }
}

const mapDispatchToProps = dispatch => {
  return {
    showPopUP: (data) => dispatch(showPopUP(data)),
  }
}

const mapStateToProps = (state) => {
  return {
    wall: state.wall.wallData,
    postData: state.postData.postData,
    completeDataObject: state.postData.completeDataObject,
    // appendData: state.appendData.appendData, // #TEST REMOVED
    appendData: state.postData.appendData,
    languageSetting: state.languageSetting,
    wallId: state.wallId.wallID,
    startEmbed: state.startEmbed,
    onSite: state.onSite
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(CardPost);
