import React, { Component, Fragment } from 'react';
const networkName = (network) => {
    if (network.includes("twitter")) return "fa-twitter";
    else if (network.includes("instagram")) return "fa-instagram";
    else if (network.includes("facebook")) return "fa-facebook";
    else if (network.includes("google")) return "fa-google";
    else if (network.includes("linkedin")) return "fa-linkedin";
    else if (network.includes("rss")) return "fa-rss";
    else if (network.includes("youtube")) return "fa-youtube-play";
    else if (network.includes("vimeo")) return "fa-vimeo";
    else if (network.includes("tumblr")) return "fa-tumblr";
    else if (network.includes("flickr")) return "fa-flickr";
    else if (network.includes("yelp")) return "fa-yelp";
    else if (network.includes("slack")) return "fa-slack";
    else if (network.includes("spotify")) return "fa-spotify";
    else if (network.includes("soundcloud")) return "fa-soundcloud";
    else if (network.includes("workplace")) return "icon-taggbox-workplace-2";
    else if (network.includes("airbnb")) return "fa-airbnb";
    else if (network.includes("giphy")) return "fa-giphy";
    else return "";
}

class SocialNetworkIcon extends Component {
    render() {
        const { network, size, iconColor } = this.props;
        return <i className={`fa ${networkName(network)} ${size}`} aria-hidden="true" style={{ color: iconColor }} ></i>
    }

}
export default SocialNetworkIcon