import { SHOW_POP_UP, CLOSE_POP_UP } from './reducersKeys'
const initialState = { isShowPopUp: false, data: null }
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case SHOW_POP_UP: {
            return { ...state, isShowPopUp: true, data: action.payload }
        }
        case CLOSE_POP_UP: {
            return { ...state, isShowPopUp: false, data: null }
        }
        default:
            return { ...state }
    }

}