import { GET_LOADER_SUCCESS, GET_LOADER_ERROR, GET_THEME_LOADER_SUCCESS, GET_THEME_SHOW_MORE_LOADER_SUCCESS, GET_WEB_FILTER_LOADER_SUCCESS, TAGS_LOADER } from './reducersKeys'
const initialState = { loader: null, themeLoader: true, isShowMoreLoader: false, webFilterLoader: false, isLoaderEnable: false, tagsLoader: false }
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case GET_LOADER_SUCCESS: {
            const { loader_type } = action.payload;
            return { ...state, loader: action.payload, isLoaderEnable: loader_type === 404 ? false : true }
        }
        case GET_THEME_LOADER_SUCCESS: {
            return { ...state, themeLoader: action.payload }
        }
        case GET_THEME_SHOW_MORE_LOADER_SUCCESS: {
            return { ...state, isShowMoreLoader: action.payload }
        }
        case GET_WEB_FILTER_LOADER_SUCCESS: {
            return { ...state, webFilterLoader: action.payload }
        }
        case GET_LOADER_ERROR: {
            return { ...state, loader: null, themeLoader: true, isShowMoreLoader: false }
        }
        case TAGS_LOADER: {
            return { ...state, tagsLoader: action.payload }
        }
        default:
            return { ...state }
    }

}