import React, { Component } from 'react';
import { themePostTracking } from '../../../actions/themeActions'
class ShareWrapper extends Component {
    componentDidMount() {
        if (window.twttr) window.twttr.ready((twttr) => {
            window.twttr.events.bind('tweet', this.onTwitterClick);
        });
    }
    onShareFacebook = event => {
        const { item, wallId } = this.props;
        if (window.FB) window.FB.ui({
            display: 'popup',
            method: 'share',
            href: item.shareDetail.shareUrl,
        }, (response) => {
            if (response && !response.error_message) {
                themePostTracking({
                    type: 2,
                    action: 2,
                    wall: wallId,
                    feed: item.feedId,
                    post: item.referenceId,
                })
            }
        });
    }
    onTwitterClick = event => {
        const { item, wallId } = this.props;
        if (event) {
            themePostTracking({
                type: 2,
                action: 2,
                wall: wallId,
                feed: item.feedId,
                post: item.referenceId,
            })
        }
    }
    linkedinShareCount = event => {
        const { item, wallId } = this.props;
        themePostTracking({
            type: 2,
            action: 2,
            wall: wallId,
            feed: item.feedId,
            post: item.referenceId,
        })

    }
    render() {
        const { network, item } = this.props;
        return <div className="shareWrapper" data-color="" style={{ display: (item.share.status === 0 || network.id === 7) ? 'none' : '' }}>
            <a onClick={this.onShareFacebook} className="fbShare socialIcon_foot" target="_blank">
                <i className="fa fa-facebook icon facebook_icon" aria-hidden="true">
                </i>
            </a>

            {/* {network.id !== 1 && item.type !== 1 ?
                        <a href={item.share.facebook} className="fbShare socialIcon_foot" target="_blank">
                            <i className="fa fa-facebook icon facebook_icon" aria-hidden="true">
                            </i>
                        </a>
                        : null} */}
            <a href={item.share.twitter} className="twShare socialIcon_foot" target="_blank">
                <i className="fa fa-twitter" aria-hidden="true">
                </i>
            </a>

            <a onClick={this.linkedinShareCount} href={item.share.linkedin} className="liShare socialIcon_foot" target="_blank">
                <i className="fa fa-linkedin" aria-hidden="true">
                </i>
            </a>
        </div>
    }
}
export default ShareWrapper;