
import axios from 'axios';
import store from '../store';
import queryString from 'query-string'
import { API_URL, IS_EMBED } from './api'

const requestInterceptor = (config) => {
  let wallURL = {}
  const appState = store.getState();
  const wallId = appState.wallId.wallID;
  if (IS_EMBED || (appState.startEmbed.heightEvent && appState.startEmbed.heightEvent.data)) {
    const { tags } = appState.startEmbed.heightEvent.data;
    const embedUrl = appState.startEmbed.heightEvent.data.url
    //wallURL = { url: embedUrl, wall: wallId }
    if (tags) {
      wallURL = { tag: tags.includes("true") ? 1 : 0 }
    }
  }
  else {
    const parsed = queryString.parse(window.location.search);
    if (parsed.tags) {
      const tags = parsed.tags;
      wallURL = {
        tag: tags.includes("true") ? 1 : 0
        //url: window.location, wall: wallId
        // , editor: 1
      }
    }
  }


  return {
    ...config,
    headers: {
      'authorization': 'Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9eyJpZCI6MTIzNDUsInUiOiJnZWlyOTE4MjkzIiwiaWF0IjoxNjM3NjU2OTYwLCJleHAiOjE2NDU0MzI5NjB9W08RIyF40I9wIDxTaAZ1Zlg=',
      // ...wallURL,
      ...config.headers,
    },
  }
};

class HttpClient {
  constructor() {
    const options = { baseURL: API_URL };
    const instance = axios.create(options);
    instance.interceptors.request.use(requestInterceptor);
    axios.interceptors.response.use(
      function (response) {
        return response;
      },
      function (error) {
        return Promise.reject(error);
      }
    );
    return instance;
  }
}
export default HttpClient;
