class StoryThemeSlider extends Component {
  state = {
    activeSlide: 0,
    activeSlide2: 0
  };
  onLoadPopup = (itemIndex, item) => (event) => {
    const {
      wall,
      appendData,
      postData,
      languageSetting,
      completeDataObject,
      wallId,
    } = this.props;

    if (isMobile && wall.Personalization.mobilePopup === 0) window.open(item.link, '_blank')
    else {
      let updatePostData = postData.postData.map(item => completeDataObject[item])
      let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
      let currentIndex = findFromArray(filteredPostData, item.id, itemIndex)
      if (appendData.heightEvent != null && wall.Personalization.postFeatured === 1) {

        var obj = {
          type: 'showPopUp',
          data: { type: 'post', card: item, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall },
        }
        appendData.heightEvent.source.postMessage(obj, appendData.heightEvent.origin);
      }
      else if (wall.Personalization.postFeatured === 2) window.open(item.link, '_blank')
      else {
        if (wall.Personalization.postFeatured === 1) this.props.showPopUP({ type: 'post', card: item, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall })
      }
      themePostTracking({
        action: 2,
        // wall: parseInt(wallId),
        wall: wallId,
        feed: item.feedId,
        post: item.referenceId,
      });
    }
  };


  render() {
    const {
      wall,
      postData,
      loader,
      completeDataObject,
      hasMoreData, wallId, numberOfCoumn, adjustWidth, windowHeight, windowWidth, percentTime, startEmbed
    } = this.props;

    let postCount = postData.postData.length;
    let postCountLess = postCount - 1;


    let items = (postCount >= 11) ? 11 : (postCountLess % 2) ? postCountLess : postCountLess - 1;
    if (windowWidth > 1600) {
      items = (postCount > 9) ? 9 : (postCountLess % 2) ? postCountLess : postCountLess - 1;
    } else if (windowWidth > 1200) {
      items = (postCount > 7) ? 7 : (postCountLess % 2) ? postCountLess : postCountLess - 1;
    } else if (windowWidth > 1024) {
      items = (postCount > 5) ? 5 : (postCountLess % 2) ? postCountLess : postCountLess - 1;
    } else if (windowWidth > 767) {
      items = (postCount > 3) ? 3 : (postCountLess % 2) ? postCountLess : postCountLess - 1;
    } else if (windowWidth > 480) {
      items = (postCount > 3) ? 3 : (postCountLess % 2) ? postCountLess : postCountLess - 1;
    } else {
      items = (postCount >= 1) ? 1 : (postCountLess % 2) ? postCountLess : postCountLess - 1;
    }

    const settings = {
      className: "tb_st_center tb_st_multi_slide",
      dots: false,
      nav: true,
      infinite: true,
      speed: 500,
      slidesToShow: (items < 0) ? 1 : items,
      autoplay: true,
      slidesToScroll: 1,
      centerMode: true,
      centerPadding: '0',
      nextArrow: <SampleNextArrow classNameCustom={'tb_st_slider_next_arrow'} windowWidth={windowWidth} items={items} />,
      prevArrow: <SamplePrevArrow classNameCustom={'tb_st_slider_prev_arrow'} windowWidth={windowWidth} items={items} />,
      beforeChange: (current, next) => this.setState({ activeSlide: next }),
      afterChange: current => this.setState({ activeSlide2: current }),
      responsive: [
        {
          breakpoint: 480,
          settings: {
            className: "tb_st_center tb_st_single_slide",
          }
        }
      ]
    };
    return (
      <div className="tb_st_slider_container">
        <Slider ref={(c) => (this.slider = c)} {...settings}>
          {!loader.webFilterLoader &&
            postData &&
            postData.postData &&
            postData.postData.length &&
            postData.postData.map((itemData, index) => {
              let item = completeDataObject[itemData];
              return <div key={index} onClick={item.isPost ? null : this.onLoadPopup(index, item)}><StoryCard wallId={wallId} item={item} ThemeRule={wall.ThemeRule} Personalization={wall.Personalization} windowHeight={windowHeight} windowWidth={windowWidth} sliderActiveFocus={(index === this.state.activeSlide) ? true : false} startEmbed={startEmbed} /></div>
            })}
        </Slider>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  const {
    wallId,
    webFilters,
    wall,
    postData,
    startEmbed,
    appendData,
    loader,
  } = state;
  return {
    startEmbed: startEmbed,
    wallId: wallId.wallID,
    webFilters: webFilters.webFilters,
    wall: wall.wallData,
    languageSetting: state.languageSetting,
    appendData: postData.appendData,
    loader: loader,
    postData: postData,
    completeDataObject: postData.completeDataObject,
    networkId: postData.appendData.networkID,
    hasMoreData: state.postData.hasMoreData[0] ? state.postData.hasMoreData[0].hasMoreData == false ? false : state.postData.hasMoreData[state.postData.appendData.networkID] ? state.postData.hasMoreData[state.postData.appendData.networkID].hasMoreData : true : true
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
    preRenderDataUpdateToParent: (postData) => dispatch(preRenderDataUpdateToParent(postData)),
    showPopUP: (data) => dispatch(showPopUP(data)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(StoryThemeSlider);