import React, { Component } from 'react';
import moment from 'moment';
import { checkStringInNotNull } from '../../themes/customFunction'
import { convertHtmlStringToRender } from '../../themes/customFunction'
import ContentPostConversion from '../contentPostConversion'
import { CLOUD_URL } from '../../../constants'
import { isValidAuthorImage } from '../../../utils';
import AuthorAvatar from '../../common/AuthorAvatar';
import { themePostTracking } from '../../../actions/themeActions'
import ErrorBoundary from '../../errorBoundary'
class ModernCardContentPost extends Component {
    state = { isAuthorImagevalid: true }
    componentDidMount() {
        const { item } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({
            isAuthorImagevalid: isAuthorImagevalid
        });
        if (window.twttr) window.twttr.ready((twttr) => {
            window.twttr.events.bind('tweet', this.onTwitterClick);
        });
    }
    onCtaClick = event => {
        const { wall, item } = this.props;
        themePostTracking({
            type: 1,
            action: 2,
            wall: wall.Wall.id,
            feed: item.feedId,
            post: item.referenceId,
        })
    }
    onShareFacebook = event => {
        const { item, wallId } = this.props;
        if (window.FB) window.FB.ui({
            display: 'popup',
            method: 'share',
            href: item.shareDetail.shareUrl,
        }, (response) => {
            if (response && !response.error_message) {
                themePostTracking({
                    type: 2,
                    action: 2,
                    wall: wallId,
                    feed: item.feedId,
                    post: item.referenceId,
                })
            }
        });
    }
    onTwitterClick = event => {
        const { item, wallId } = this.props;
        if (event) {
            themePostTracking({
                type: 2,
                action: 2,
                wall: wallId,
                feed: item.feedId,
                post: item.referenceId,
            })
        }
    }
    linkedinShareCount = event => {
        const { item, wallId } = this.props;
        themePostTracking({
            type: 2,
            action: 2,
            wall: wallId,
            feed: item.feedId,
            post: item.referenceId,
        })

    }
    render() {
        const { wall, item, isRating, rating, network, isCta, cta, clickLoadPopUp, personalization } = this.props;
        return <div className="postContentCard">
            {isRating ? <div className="rating">
                <img src={`${CLOUD_URL}/widget-embed/rating/${network.id}/${rating}.png`} alt="rating" />
            </div> : null}


            {isCta ? <span onClick={this.onCtaClick}> {convertHtmlStringToRender(cta)}</span> : null}


            <div onClick={clickLoadPopUp} className={`postedTxt ${personalization.textDecorate == 1 ? item.textDecoClass : ""} setFont  sGFfonte-${wall.ThemeRule.font_varient}`} style={{
                fontFamily: wall.ThemeRule.css_font, display: (item.hideContent === 1 && item.type !== 1) ? 'none' : 'inline-grid', fontSize: item.font.fontsize,
                // color: item.font.fontColor
                color: item.font.cardColor === "#000000" && item.font.fontColor === "#000000" ? "#ffffff" :
                    item.font.cardColor === "#ffffff" && item.font.fontColor === "#ffffff" ? "#000000"
                        : item.font.fontColor
            }}>
                {checkStringInNotNull(item.contentTitle) ? <h1>{convertHtmlStringToRender(item.contentTitle)}</h1> : null}
                {/* <p> {
                    item.personalization.hashtag_highlight !== 0 ?
                        item.personalization.hashtag_all === 1 ? reactStringReplace(item.content, /#(\w+)/g, (match, i) => (
                            <span key={match + i} style={{ color: item.personalization.hashtag_color, fontWeight: "bold" }}>#{match}</span>
                        ))
                            : item.personalization.hashtag_feed === 1 ? reactStringReplace(item.content, item.hash.hashString, (match, i) => (
                                <span key={match + i} style={{ color: item.personalization.hashtag_color, fontWeight: "bold" }}>{match}</span>
                            )) : item.content
                        : item.content
                }</p> */}
                <ErrorBoundary><ContentPostConversion item={item} network={network} /></ErrorBoundary>
            </div>

            <h4 onClick={clickLoadPopUp} style={item.hideContent == 1 ? { paddingTop: 10 } : null}>
                <div className="postNetwork" data-network={network.name} data-network-color={network.color}>
                    <i className={`fa ${network.icon == 'fa-google' ? 'fa-google-new' : network.icon}`} style={{ color: item.font.iconColor }} >
                    </i>
                </div>


                <div className="authorDetails" style={{ display: (item.instaHash === 0) ? 'none' : 'block' }}>
                    {this.state.isAuthorImagevalid && <img style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block' }} src={item.author.picture}
                        onError={(e) => {
                            this.setState({
                                isAuthorImagevalid: false
                            });
                            e.target.onerror = null;
                            e.target.src = item.author.errorPic
                            console.clear()
                        }} alt="postAuthor" />}
                    {!this.state.isAuthorImagevalid && <AuthorAvatar style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'flex' }} username={item.author.name} color={item.network.color} />}
                    <div className="postAuthorName" style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }}>
                        {item.author.name}
                    </div>

                    <div className="authrHandleTime">
                        <span className="authrHandle" style={{ display: (item.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }} target="_blank">
                            {item.author.username ? item.author.username.length > 0 ? `@` : `` : ''}{item.author.username}
                        </span>
                        <div className="sepratedot" style={{ display: (item.postAuthor === 0 || item.timePost === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }}></div>
                        <span className="timePost" style={{ display: (item.timePost === 0 || item.author.isInstaUser) ? 'none' : 'block', color: item.font.authorColor }} data-livestamp={item.createdAt}>
                            {moment(new Date(item.createdAt * 1000)).fromNow()}
                        </span>
                    </div>
                    <div className="clearfix"></div>
                </div>

            </h4>
            {!(item.share.status === 0 || network.id === 7) ?
                <div className="shareWrapper" data-color="">
                    <a target="_blank">
                        <i className="fa fa-share icon" aria-hidden="true" style={{ lineHeight: '30px' }}>
                        </i>
                    </a>

                    <a onClick={this.onShareFacebook} className="fbShare">
                        <i className="fa fa-facebook-f icon" aria-hidden="true" style={{ color: 'rgb(58, 87, 149)' }}>
                        </i>
                    </a>
                    <a href={item.share.twitter} className="twShare" target="_blank">
                        <i className="fa fa-twitter" aria-hidden="true">
                        </i>
                    </a>

                    <a onClick={this.linkedinShareCount} href={item.share.linkedin} className="liShare" target="_blank">
                        <i className="fa fa-linkedin" aria-hidden="true">
                        </i>
                    </a>
                </div>
                : null
            }

        </div>
    }
}

export default ModernCardContentPost;