const getDataFromRequestAPI = (dispatch, Id) => {

  const TEST_URL_LOADED = USE_NODE_APIS
  if (!isRequestComplete) {
    isRequestComplete = true;
    new HttpClient()
      .get(`${TEST_URL_LOADED ? GET_WIDGET_DATA_API_PATH1 : GET_WIDGET_DATA_API_PATH}${Id}`, HeadersData())
      .then((response) => {
        getDataFromSocketAndAPI(dispatch, response.data)
      })
      .catch((error) => {
        const { response } = error;
        if (response && response.status && response.status == 404) {
          if (parseInt(response.status) == 404) {
            dispatch({
              type: ERROR_RESPONSE_WITH_MESSAGE,
              payload: response.data,
            });
            //throw new Error(response.data.message);
          }
        }
        dispatch({ type: GET_LOADER_ERROR });
        setTimeout(() => dispatch({ type: TAGS_LOADER, payload: false }), 2000)
      });
  }
}
const getDataFromSocketAndAPI = (dispatch, response, heightEvent) => {
  //console.log("test", response, response && Object.keys(response).length > 0)
  if (response && Object.keys(response).length > 0) {
    const {
      planLimit,
      after,
      customPost,
      postData,
      webFilters,
      error_code,
      post_message,
      older_days,
      older_post,
      visitorLimit,
      wall, response_code,
      loader_type, loader_color1, loader_color2,
      onsite_token
    } = response;
    //console.log("postData", response)
    if (parseInt(response_code) == 404) {
      dispatch({
        type: ERROR_RESPONSE_WITH_MESSAGE,
        payload: response,
      });
      throw new Error(response.message);
    }
    else if (visitorLimit === 0) {
      if (planLimit) dispatch({ type: PLAN_LIMIT, payload: planLimit });
      if (wall && wall.UserRule && wall.UserRule.branding_lite == 1 && ![55].includes(wall.Personalization.widgetTheme) && wall.Wall.owner != "23542") {
        dispatch({ type: UPDATE_FREE_ADS_STATUS, payload: true });
      }


      dispatch({ type: GET_LOADER_SUCCESS, payload: { loader_type, loader_color1, loader_color2, wall } });
      dispatch({ type: GET_WALL_SUCCESS, payload: wall });
      dispatch({ type: GET_WALL_ID_SUCCESS, payload: wall.Wall.id });
      dispatch({ type: WALL_ID_FETCHED, payload: wall.Wall.id });
      dispatch({
        type: GET_THEME_ERROR_SUCCESS,
        payload: {
          error_code: error_code,
          post_message: post_message,
          older_days: older_days,
          older_post: older_post,
        },
      });

      dispatch({
        type: GET_LANGUAGE_SUCCESS,
        payload: languageSettings(
          wall.Personalization.w_language,
          wall.Personalization.custom_lan_data
        ),
      });
      if (wall && IS_EMBED && wall.Wall) SOCKET_CONNECT_WITH_ACTIVE_USER(wall.Wall.id); // socket for active users in backend
      dispatch({ type: GET_CUSTOM_POST_DATA_SUCCESS, payload: customPost }); // 2
      dispatch({ type: GET_POST_DATA_SUCCESS, payload: postData, table_name: (wall && wall.UserDetail && wall.UserDetail.db_table) ? wall.UserDetail.db_table : "" }); // 7

      dispatch({
        type: ONSITE_TOCKEN_UPDATE,
        payload: onsite_token,
      });

      dispatch({
        type: GET_THEME_WEB_FILTERS_SUCCESS,
        payload: convertObjectToArray(webFilters),
      }); // 1
      dispatch({
        type: GET_APPEND_DATA_SUCCESS,
        payload: { after: after, networkID: 0, heightEvent: heightEvent },
      });
      setTimeout(() => {
        dispatch({ type: GET_THEME_LOADER_SUCCESS, payload: false });
      }, 100);
    } else {
      dispatch({
        type: ERROR_RESPONSE_WITH_MESSAGE,
        // payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED" },
        payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response },
      });
      throw new Error("");
    }
    setTimeout(() => dispatch({ type: TAGS_LOADER, payload: false }), 2000)
  }
}


