export const heightEvent = (event, WALL_ID) => {
  return (dispatch) => {
    dispatch({ type: START_EMBED_IFRAME, payload: event });
    dispatch({ type: EMBED_URL_FETCHED, payload: event });
    dispatch({ type: GET_IFRAME_EVENT_SUCCESS, payload: event });
    if (event && event.data && event.data.url && WALL_ID) {
      urlTracking(event.data.url, WALL_ID);
    }
  };
};

const urlTracking = (URL, WALL_ID) => {
  new HttpClient()
    .post(`${GET_LOADER_API_PATH}`, {}, {
      headers: {
        url: URL, wall: WALL_ID,
        source: 0
      }
    })
    .then((response) => {
    })
    .catch((error) => {
      console.error("url-tracking", error)
    });
}

export const wallThemeUpdate = (themeID, wallData) => {
  return (dispatch) => {
    try {
      wallData.Personalization.widgetTheme = themeID;
      dispatch({ type: WALL_THEME_ID_UPDATE, payload: wallData });
    } catch (ex) {
      dispatch({ type: WALL_THEME_ID_UPDATE, payload: wallData });
    }
  };
};

export const filterPostDataAppendWebFilter = (
  wallID,
  timeStamp,
  postCount,
  networkId,
  after,
  postData,
  heightEvent,
  complete_Data
) => {
  return (dispatch) => {
    dispatch({ type: GET_NETWORK_ID_APPEND_DATA_SUCCESS, payload: networkId });
    dispatch({ type: GET_WEB_FILTER_LOADER_SUCCESS, payload: true });
    const wall = store.getState().wall.wallData;
    const bestFit = (wall.Personalization.widgetTheme === 4 && wall.Personalization.fitRow == 1);
    // const TEST_URL_LOADED = ["463", "499", "470", "452"].includes(wallID);
    const TEST_URL_LOADED = USE_NODE_APIS //SAMPLE_WALLS.includes(wallID);


    new HttpClient()
      .get(`${TEST_URL_LOADED ? GET_DATA_POST_APPEND1 : GET_DATA_POST_APPEND}${wallID}/${timeStamp}/${postCount}/${networkId}/1/${after}`, HeadersData())
      // .get(`${GET_DATA_POST_APPEND1}${wallID}/${timeStamp}/${postCount}/${networkId}/1/${after}`)
      .then((response) => {
        const newData = response.data.data;

        const selectedNetworkId = store.getState().postData
          .currentFilterNetworkId;

        if (selectedNetworkId && selectedNetworkId == networkId) {
          dispatch({
            type: GET_NETWORK_ID_APPEND_DATA_SUCCESS,
            payload: networkId,
          });

          dispatch({
            type: GET_FILTERED_COMPLETE_POST_DATA_SUCCESS,
            payload: newData,
            filterNetworkId: networkId,
          });

          dispatch({
            type: GET_APPEND_DATA_SUCCESS,
            payload: {
              after: response.data.after,
              networkID: networkId,
              heightEvent: heightEvent,
            },
            filterNetworkId: networkId,
          });
          dispatch({
            type: SET_HAS_MORE_DATA,
            payload: {
              [networkId]: {
                hasMoreData: bestFit && Object.keys(response.data.data).length < postCount ? false : response.data.hasPost == 1 ? true : false,//Object.keys(newData).length < postCount ? false : true,
                after: response.data.after,
              },
            },
          }); // HasMoreData ?

          dispatch({ type: GET_WEB_FILTER_LOADER_SUCCESS, payload: false });
        } else if (!selectedNetworkId) {
          dispatch({
            type: GET_NETWORK_ID_APPEND_DATA_SUCCESS,
            payload: networkId,
          });

          dispatch({
            type: GET_FILTERED_COMPLETE_POST_DATA_SUCCESS,
            payload: newData,
            filterNetworkId: networkId,
          });
          dispatch({
            type: SET_HAS_MORE_DATA,
            payload: {
              [networkId]: {
                hasMoreData: bestFit && Object.keys(response.data.data).length < postCount ? false : response.data.hasPost == 1 ? true : false,//Object.keys(newData).length < postCount ? false : true,
                after: response.data.after,
              },
            },
          }); // HasMoreData ?
          dispatch({
            type: GET_APPEND_DATA_SUCCESS,
            payload: {
              after: response.data.after,
              networkID: networkId,
              heightEvent: heightEvent,
            },
            filterNetworkId: networkId,
          });
          // dispatch({ type: GET_BUTTON_NAME_UPDATE_SUCCESS, payload: newData.length < postCount ? (languageSetting.no_more) : languageSetting.buttonName });
          dispatch({ type: GET_WEB_FILTER_LOADER_SUCCESS, payload: false });
        }
      })
      .catch((error) => { });
    // }, 50000)
  };
};

