const getShowMoreDataFromAPISocket = (dispatch, response, wall, networkId, bestFit, postCount, heightEvent) => {
  if (response && Object.keys(response).length > 0) {
    dispatch({
      type: GET_POST_DATA_SUCCESS,
      payload: response.data,
      filterNetworkId: networkId, table_name: (wall && wall.UserDetail && wall.UserDetail.db_table) ? wall.UserDetail.db_table : ""
    });



    dispatch({
      type: GET_APPEND_DATA_SUCCESS,
      payload: {
        after: response.after,
        networkID: networkId,
        heightEvent: heightEvent,
      },
      filterNetworkId: networkId,
    });
    dispatch({
      type: SET_HAS_MORE_DATA,
      payload: {
        [networkId]: {
          hasMoreData: bestFit && Object.keys(response.data).length < postCount ? false : response.hasPost == 1 ? true : false,//newDataLength < postCount ? false : true,
          after: response.after,
        },
      },
    });
    dispatch({
      type: GET_THEME_SHOW_MORE_LOADER_SUCCESS,
      payload: false,
    });
  }
}

