import React, { Component, Fragment } from 'react';
import $ from 'jquery';
import OwlCarousel from 'react-owl-carousel2';
import 'react-owl-carousel2/lib/styles.css';
import TaggboxAds from '../../taggboxAds'
import { CLOUD_URL, PRICE_PAGE, BRANDING_CARDS_LINK } from './../../../constants';
import { IS_SCREEN_SHOT } from '../../../utils'


import PostItems from './post'
class SliderData extends Component {
    isArrowLoader = false;
    shouldComponentUpdate(nextProps, nextState) {
        this.isArrowLoader = false;
        return this.props.data.length != nextProps.data.length;
    }
    componentDidMount() {
        $('.testimonials .owl-carousel').find('.owl-nav').removeClass('disabled');
        $('.testimonials .owl-carousel').on('changed.owl.carousel', function (event) {
            $(this).find('.owl-nav').removeClass('disabled');
        });

        $(".share-icon").click(function (event) {
            $(".popover-content").hide(event);
            $(this).next().toggle();
            event.stopPropagation();
            event.stopImmediatePropagation();
        });
        $('.testimonials').click(function (e) {
            if (!$(e.target).closest('.share-icon').length) {
                $(".popover-content").hide();
            }
        });
    }

    onChangeData = event => {
        if (event && event.item) {
            if (parseInt(event.item.index) == (parseInt(event.item.count) - 1)) {
                this.isArrowLoader = true;
                setTimeout(() => this.props.onUpdateData(), 1000)

            }
        }
    }

    render() {
        const { wallId, wall, languageSetting, adjustWidth, data, getFontSize, onLoadPopup, events, startEmbed } = this.props;

        let options = {
            items: 1,
            itemClass: "owl-item center",
            loadedClass: "owl-carousel custome_slide",
            navText: [`<button type='button' role='presentation' class='owl-prev'><span aria-label='Previous'></span></button>`, `<button type='button' role='presentation' class='owl-next'><span aria-label='Next'></span></button>`],
            margin: 0,
            center: true,
            loop: false,
            nav: true,
            autoplay: wall.ThemeRule.slidePost == 1 ? true : false,
            autoplayTimeout: wall.ThemeRule.slidePost == 1 ? (parseInt(wall.ThemeRule.slideDuration) * 1000) : 5000,
            dots: false,
            rewind: false,
            lazyLoad: false,
            freeDrag: true,
        };
        const reduceFontSize = adjustWidth.height ? parseInt(adjustWidth.height.replace("px", "")) : null
        let isDefaultFont = getFontSize(reduceFontSize);

        return <section className="testimonials">
            <div className="inner-testimonials">

                <OwlCarousel id="slide-testimonal" ref="car" options={options} events={events} >
                    {
                        data && data.length && data.map((item, index) => {
                            if (isDefaultFont == "") isDefaultFont = item.font.fontsize;
                            const fonrSize = (reduceFontSize && reduceFontSize < 500) ? isDefaultFont : item.font.fontsize
                            let fontSizeTrim = fonrSize ? String(fonrSize).replace("px", "") : "0";
                            const videoIconSize = (reduceFontSize && reduceFontSize < 500) ? '10px' ? 25 : '12px' ? 40 : '14px' ? 60 : 60 : 60;

                            if (IS_SCREEN_SHOT && data && ((data.length - 1) == index)) {
                                setTimeout(() => {
                                    let dataUpdateImage = document.querySelectorAll("img[data-is-optmized='0']");
                                    if (dataUpdateImage && dataUpdateImage.length > 0) {
                                        dataUpdateImage.forEach((item) => {
                                            item.setAttribute("src", item.getAttribute("data-src"))
                                            item.setAttribute("data-is-optmized", "1")
                                        })
                                    }
                                }, 1300)
                            }


                            return item.type != 1 && <Fragment key={index}>
                                {item.isPost ? <TaggboxAds link={`${PRICE_PAGE}?utm_source=${startEmbed.heightEvent.origin ? startEmbed.heightEvent.origin : "https://widget.taggbox.com/"}&${BRANDING_CARDS_LINK}`} imgPath={`${CLOUD_URL}/widget-embed/branding/br_card-01.svg`} alt="Taggbox Branding" /> : <PostItems wallId={wallId} languageSetting={languageSetting} isDefaultFont={isDefaultFont} fontSizeTrim={fontSizeTrim} index={index} videoIconSize={videoIconSize} onLoadPopup={onLoadPopup} reduceFontSize={reduceFontSize} fonrSize={fonrSize} item={item} wall={wall} adjustWidth={adjustWidth} />}

                            </Fragment>
                        })
                    }
                </OwlCarousel>
            </div>

        </section>
    }
}
export default SliderData;