import React, { Component } from 'react';
import ErrorBoundary from '../errorBoundary'
import ModernCardContentPost from './themeContentPost/modernCardContentPost'
import ClassicCardContentPost from './themeContentPost/classicCardContentPost'
import NewsCardContentPost from './themeContentPost/newsCardContentPost'
import ClassicPhotoCardContentPost from './themeContentPost/classicPhotoCardContentPost'
import SquarePhotoContentPost from './themeContentPost/squarePhotoCardPost'
import GalleryPhotoContentPost from './themeContentPost/galleryPhotoPost'
import WidgetThemeCardContentPost from './themeContentPost/widgetThemeCardContentPost'

class ContentPost extends Component {

    render() {
        const { widgetTheme } = this.props;
        return widgetTheme === 20 ? <ErrorBoundary><ModernCardContentPost {...this.props} /></ErrorBoundary>
            : widgetTheme === 5 ? <ErrorBoundary><ClassicCardContentPost {...this.props} /></ErrorBoundary>
                : widgetTheme === 3 ? <ErrorBoundary><ClassicPhotoCardContentPost {...this.props} /></ErrorBoundary >
                    : widgetTheme === 4 ? <ErrorBoundary><SquarePhotoContentPost {...this.props} /></ErrorBoundary >
                        : widgetTheme === 50 ? <ErrorBoundary><GalleryPhotoContentPost {...this.props} /></ErrorBoundary >
                            : widgetTheme === 19 ? <ErrorBoundary><NewsCardContentPost {...this.props} /></ErrorBoundary >
                                : widgetTheme === 49 ? <ErrorBoundary><WidgetThemeCardContentPost {...this.props} /></ErrorBoundary >
                                    : null
    }
}

export default ContentPost;