import React, { Component, Fragment } from 'react';
import { checkStringInNotNull, convertHtmlStringToRender } from '../themes/customFunction'
import MediaPost from './mediaPost'
import ContentPost from './contentPost'
import PostNetwork from './themeContentPost/postNetwork'

class CardPostFile extends Component {
    state = {
        height: 300,
    }

    render() {
        const { item, imgWidth, widgetTheme, newCardWidth, clickLoadPopUp, numberOfCoumn, personalization, wall, wallId, isHover } = this.props;
        const { height } = this.state;

        return widgetTheme === 5 ? <Fragment>
            <ContentPost wall={wall} item={item} isRating={checkStringInNotNull(item.rating)} rating={item.rating} network={item.network} isCta={checkStringInNotNull(item.cta)} cta={item.cta} widgetTheme={widgetTheme} imgWidth={imgWidth} newCardWidth={newCardWidth} clickLoadPopUp={clickLoadPopUp} numberOfCoumn={numberOfCoumn} personalization={personalization} wallId={wallId} isHover={isHover} />
            <MediaPost newCardWidth={newCardWidth} widgetTheme={widgetTheme} imgHeight={height} imgWidth={imgWidth} file={item.file} postFileNew={item.postFileNew} type={item.type} item={item} isFile={checkStringInNotNull(item.file)} personalization={item.personalization} isEmbed={checkStringInNotNull(item.embed)} network={item.network} clickLoadPopUp={clickLoadPopUp} numberOfCoumn={numberOfCoumn} wallId={wallId} isHover={isHover} />
            {checkStringInNotNull(item.cta) ? convertHtmlStringToRender(item.cta) : null}

        </Fragment> : <React.Fragment>
                {widgetTheme === 49 ? <PostNetwork network={item.network} item={item} widgetTheme={widgetTheme} /> : null}
                <MediaPost newCardWidth={newCardWidth} widgetTheme={widgetTheme} imgHeight={height} imgWidth={imgWidth} file={item.file} postFileNew={item.postFileNew} type={item.type} item={item} isFile={checkStringInNotNull(item.file)} personalization={item.personalization} isEmbed={checkStringInNotNull(item.embed)} network={item.network} clickLoadPopUp={clickLoadPopUp} numberOfCoumn={numberOfCoumn} wallId={wallId} isHover={isHover} />
                <ContentPost wall={wall} newCardWidth={newCardWidth} item={item} isRating={checkStringInNotNull(item.rating)} rating={item.rating} network={item.network} isCta={checkStringInNotNull(item.cta)} cta={item.cta} widgetTheme={widgetTheme} imgWidth={imgWidth} clickLoadPopUp={clickLoadPopUp} numberOfCoumn={numberOfCoumn} personalization={personalization} wallId={wallId} isHover={isHover} />
            </React.Fragment>
    }
}

export default CardPostFile;