import React, { Component } from 'react';
import { CUSTOM_SERVER_PATH } from '../../constants'
import '../../css/banner.css';
class ThemeBanner extends Component {
    render() {

        const { BannerImage } = this.props;

        return BannerImage.status === 1 ? <div className="bannerTop" id="wBannerImage" style={{ zIndex: 9 }}>
            <img src={`${CUSTOM_SERVER_PATH}${BannerImage.path}${BannerImage.newName}`} className="img-responsive" />
        </div> : null

    }
}
export default ThemeBanner;