import React from 'react';
import ReactHtmlParser from 'react-html-parser';
const MiddleBanner = ({ CustomBanner, wall }) => <div className="headerBannerMiddle">
    <div className="headBannerHeading">
        <span className={`setFont sGFfonte-${CustomBanner.subtitle_font_varient}`} style={{ fontFamily: CustomBanner.subtitle_css_font, fontSize: CustomBanner.subTitle_font_size, color: CustomBanner.subTitle_font_color }}>{ReactHtmlParser(CustomBanner.subTitle)}</span>
        <h1 className={`setFont sGFfonte-${CustomBanner.title_font_varient}`} style={{ fontFamily: CustomBanner.title_css_font, fontSize: CustomBanner.title_font_size, color: CustomBanner.title_font_color }}>{ReactHtmlParser(CustomBanner.title)}</h1>
    </div>
</div>

export default MiddleBanner;