export const randomNumber = (min, max, freeAdsPosition) => {
    try {
        var position = freeAdsPosition; let count = 3;
        for (var i = 0; i < 10; i++) {
            count = parseInt(count) + 9 + parseInt(i);
            if (!position.includes(count)) position.push(count);
        }
        return position;
    }
    catch (ex) {
        return freeAdsPosition
    }
}

export const IS_SCREEN_SHOT = window.location.href.includes("screenshot") ? false : true

export const bytesToSize = (bytes) => {
    var sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
    if (bytes == 0) return '0 Byte';
    var i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)));
    return { size: Math.round(bytes / Math.pow(1024, i), 2), sizeText: sizes[i] }

}
export const URL_DESTRUCTURE = (UPDATED_URL) => queryString.parse(UPDATED_URL)
export const HeadersData = () => {
    const appState = store.getState();
    var checkTags = {}
    if ((appState.startEmbed.heightEvent && appState.startEmbed.heightEvent.data)) {
        const { tags } = appState.startEmbed.heightEvent.data;
        if (tags) checkTags = { tag: tags.includes("true") ? 1 : 0 }
    }
    else {
        const parsed = queryString.parse(window.location.search);
        if (parsed.tags) {
            const tags = parsed.tags;
            checkTags = { tag: tags.includes("true") ? 1 : 0 }
        }
    }
    return { headers: checkTags }
}

