import React, { Component } from 'react';
import { connect } from 'react-redux';
import SubmitPic from '../../submitPic'
import ModalPopUp from '../../modalPopUp'
import Error from './error'
import { ERROR_BACKGROUND_IMG, noPostErrorDisplayCss } from '../../../constants'
class ErrorExtends extends Component {
    render() {
        const { error, wall, innerHeight, errorWithMessage, startEmbed, modalPop } = this.props;
        let editor = window.location.pathname.includes('/editor');
        return <>
            {modalPop.isShowPopUp && <ModalPopUp data={modalPop.data} />}
            <div className="col-md-12 text-center noPostSec" id="noPosts"
                style={{
                    backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')'
                    , height: window.innerHeight
                }}>
                <div className="noPostContent">
                    {
                        wall && Object.keys(wall).length > 0 && wall.UserRule.on_site_upload && !(window.wallId) ? <div className='submit_pic_btn__00'>
                            <SubmitPic wallId={wall.Wall.id} wall={wall} />
                        </div> : null
                    }

                    {errorWithMessage != null ?
                        <div className="noPostMsgBlock">
                            {<a className="product-logo" target="_blank" href={`https://taggbox.com/widget/?utm_source=Not_active_logo&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=widget_not_active`}>
                                <img src="https://cloud.taggbox.com/widget/widget-embed/Taggbox_logo.svg" alt="logo" /> <span className="tb-pro-name">Widget</span></a>}

                            {(errorWithMessage.type !== 'VISITOR_LIMIT_EXCEEDED') && <><h2>{errorWithMessage.message}</h2>
                                <p>Oops! Seems like something went wrong. If you are the owner of this Taggbox account, please click below link to access your account.</p>
                                {<a className="btn btn-lg btn-danger" target="_blank" href={`https://app.taggbox.com/widget/accounts/login?utm_source=Access_now_button&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=widget_not_active`}>Access Now</a>}</>}
                            {(errorWithMessage.type === 'VISITOR_LIMIT_EXCEEDED') && <><h2>Page Views Limit Exceeded!</h2>
                                <p>Oops! Seems Page Views Limit for the plan exceeded. If you are the owner of this Taggbox account, please click below link to access your account.</p>
                                {<a className="btn btn-lg btn-danger" target="_blank" href={`https://app.taggbox.com/widget/accounts/login?utm_source=Upgrade_now_button&utm_medium=${startEmbed.heightEvent.origin}&utm_campaign=page_views_exceeded`}>Upgrade Now</a>}</>}
                        </div>
                        : <React.Fragment>
                            {wall.Personalization.demoWall === 1 && editor ? <Error innerHeight={innerHeight} /> :

                                <div className="noPostMsgBlock" >
                                    <Error innerHeight={innerHeight} />
                                </div>
                            }

                        </React.Fragment>
                    }
                </div> </div>
        </>

    }
}

const mapStateToProps = state => {
    return {
        wall: state.wall.wallData,
        error: state.error.themeError,
        errorWithMessage: state.error.errorWithMessage,
        startEmbed: state.startEmbed,
        modalPop: state.modalPop
    }
}
export default connect(mapStateToProps)(ErrorExtends);