import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import ThemeBanner from '../../themeBanner'
import PoweredBY from '../../poweredByComponent'
import OwnBanner from '../../ownBanner'
import PostThemeSlider from './slider'
import { scrlBarComan } from '../customFunction'
import { CUSTOM_SERVER_PATH } from '../../../constants'
import { IS_EMBED } from '../../../actions/api';
import { THEME_WIDTH_ADJUSTMENT_POST_THEME } from '../../../utils'
import SubmitPic from '../../submitPic'
class PostTheme extends Component {
    constructor(props) {
        super(props);
        this.myRef = React.createRef()
    }
    state = {
        windowWidth: window.innerWidth,
        windowHeight: window.innerHeight,
        initialCount: 0
    }
    componentDidMount() {
        window.addEventListener("resize", () => {
            this.setState({ windowWidth: window.innerWidth, windowHeight: window.innerHeight })
        });
        const { postData } = this.props;
        if (postData.length) this.setState({ initialCount: postData.length })
    }

    render() {
        const { wall, appendData, postData, loader, wallId } = this.props;
        const { windowWidth, windowHeight } = this.state;
        let adjustWidth = THEME_WIDTH_ADJUSTMENT_POST_THEME(windowWidth);

        let fixedHeightStyle = null;
        if (appendData && appendData.heightEvent && appendData.heightEvent.data.fixedHeight == "0") {
            fixedHeightStyle = { overflow: 'auto', height: `${appendData.heightEvent.data.iframeHeight}px` }
            adjustWidth = { height: `${appendData.heightEvent.data.iframeHeight}px`, width: `${appendData.heightEvent.data.iframeHeight}px` }
        }

        return <Fragment>
            {
                wall.UserRule.on_site_upload && !(window.wallId) ? <SubmitPic wallId={wallId} wall={wall} /> : null
            }
            <div id="controllerContent" style={!IS_EMBED ? { marginTop: ((windowHeight / 2) > 150 ? 150 : (windowHeight / 2)) } : {}}>
                <div style={{ maxWidth: adjustWidth.height, margin: '0 auto', ...scrlBarComan(wall.BackgroundImage.path, wall.ThemeRule.transparent, wall.Personalization.widgetTheme, false, window.innerHeight, 0, `${CUSTOM_SERVER_PATH}${wall.BackgroundImage.path}${wall.BackgroundImage.newName}`, wall.ThemeRule.backgroundColor, fixedHeightStyle) }}>
                    {
                        wall.BannerImage.status === 1 ? <OwnBanner BannerImage={wall.BannerImage}
                        /> : null}
                    {wall.CustomBanner.status === 1 && wall.CustomBanner.banner_position === 1 ? <ThemeBanner CustomBanner={wall.CustomBanner}
                        CustomBannerImage={wall.CustomBannerImage}
                        LogoImage={wall.LogoImage}
                    /> : null}
                    <div className={`themeWrap`} style={{ maxHeight: adjustWidth.height }}>
                        {
                            !loader.webFilterLoader && postData && postData.length ? <PostThemeSlider adjustWidth={adjustWidth} wallId={wallId} /> : null
                        }

                    </div>
                </div>

            </div>
        </Fragment >

    }
}
const mapStateToProps = state => {
    const { wallId, webFilters, wall, postData, appendData, loader } = state
    return {
        wallId: wallId.wallID,
        webFilters: webFilters.webFilters,
        wall: wall.wallData,
        appendData: postData.appendData,
        loader: loader,
        postData: postData.postData,
        completeDataObject: postData.completeDataObject
    }
}
export default connect(mapStateToProps)(PostTheme);