import React, { Component } from 'react';
import { connect } from 'react-redux';
import $ from 'jquery';
import { getDataNextSteps, preRenderDataUpdateToParent } from '../actions/themeActions'
import LoaderComponent from './loaderComponent'
import { loaderTopHeightSetting } from '../constants'
class ShowMore extends Component {
    state = { scrollPosition: 0 }
    componentDidMount() {
        //if (document.getElementById("appendBtn")) document.getElementById("appendBtn").focus();
    }
    onClickToDataAppend = event => {
        let scrlBarComanW = document.querySelector("#scrlBarComanW");
        let scrlBarComanWGET = document.getElementById("scrlBarComanW");
        const { appendData } = this.props;
        if (scrlBarComanW != null && appendData && appendData.heightEvent && appendData.heightEvent.data.fixedHeight == "0") {
            this.setState({ scrollPosition: (scrlBarComanWGET.scrollHeight - 200) }, () => this.onClickToUpdateFix())
        }
        else this.onClickToUpdateFix();
    }

    onClickToUpdateFix = () => {
        const { appendData, wall, postData, wallId, fitRowCount } = this.props;
        const tstamp = Math.floor(Date.now() / 1000);
        let postCount = wall.Personalization.widgetTheme === 4 && fitRowCount > wall.ThemeRule.numberOfPosts ? fitRowCount : wall.ThemeRule.numberOfPosts;

        $(".post").removeClass("monumentalst");
        $(".post").removeAttr("tabindex");
        $(".monumentalst").removeAttr("tabindex");
        //$("#appendBtn").attr("tabindex", "-1");

        if (appendData.networkID && appendData.networkID != 0) {

            const filteredArr = Object.keys(postData.preRender).filter(item => postData.preRender[item].network.id == appendData.networkID);
            if (filteredArr.length > 0) {
                this.props.preRenderDataUpdateToParent(filteredArr);
            }
            if (filteredArr.length < postCount) {
                this.props.getDataNextSteps(wallId, tstamp, postCount - filteredArr.length, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent, true);
            }
            this.props.getDataNextSteps(wallId, tstamp, postCount, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent);
            if (appendData && appendData.heightEvent && appendData.heightEvent.data.fixedHeight == "0") {
                setTimeout(() => {
                    let elmnt = document.getElementById("scrlBarComanW");
                    elmnt.scrollTop = this.state.scrollPosition;
                }, 200);
            }
        } else {
            if (Object.keys(postData.preRender).length > 0) {
                this.props.preRenderDataUpdateToParent(postData.preRender);
                this.props.getDataNextSteps(wallId, tstamp, postCount, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent);
            }
            else {
                this.props.getDataNextSteps(wallId, tstamp, postCount, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent);
                this.props.preRenderDataUpdateToParent(postData.preRender);
                this.props.getDataNextSteps(wallId, tstamp, postCount, appendData.networkID, appendData.after, postData.preRender, appendData.heightEvent);
            }
            if (appendData && appendData.heightEvent && appendData.heightEvent.data.fixedHeight == "0")
                setTimeout(() => {
                    let elmnt = document.getElementById("scrlBarComanW");
                    elmnt.scrollTop = this.state.scrollPosition;
                }, 100);
        }

    }

    render() {
        const { languageSetting, loader, wall } = this.props;
        let loaderTopStyle = 0;
        if (wall && wall != null) loaderTopStyle = loaderTopHeightSetting()
        return loader.webFilterLoader && loader.isLoaderEnable ? <LoaderComponent loader={loader.loader} wall={wall} loaderTopStyle={loaderTopStyle} isLoading={true} /> : this.props.hasMoreData ? <div className={`col-md-12 text-center loadMoreWrapper loadmorebtn${wall.Personalization.widgetTheme} loadmoreThemetype${wall.Personalization.themeType}  sGFfonte-${wall.ThemeRule.font_varient}`} id="eMorePosts" style={{ fontFamily: wall.ThemeRule.css_font, display: wall.Personalization.loadMoreStatus === 1 || this.props.hasMoreData === false ? 'block' : 'none' }}>
            <div className="appendLoader" style={{ display: !loader.webFilterLoader ? 'none' : !loader.isLoaderEnable ? 'none' : 'block', marginBottom: '30px' }}>
                <img width="30px" src="https://cloud.taggbox.com/widget/widget-embed/loader.svg" alt="loader" />
            </div>
            <button tabindex="1" id="appendBtn" aria-label={this.props.hasMoreData ? languageSetting.buttonName.toLowerCase() == 'show more' ? 'load more posts' : languageSetting.buttonName : languageSetting.no_more}
                aria-labelledby={this.props.hasMoreData ? languageSetting.buttonName.toLowerCase() == 'show more' ? 'load more posts' : languageSetting.buttonName : languageSetting.no_more}

                className={`loadMoreBtn ladda-button sGFfonte-${wall.ThemeRule.font_varient}`} data-style="zoom-in" data-spinner-color="#1f1b1b" style={{
                    fontFamily: wall.ThemeRule.css_font,
                    cursor: "pointer",
                    border: '1px solid rgb(31, 27, 27)', display: 'inline', marginBottom: '20px', backgroundColor: wall.ThemeRule.cardColor, borderColor: wall.ThemeRule.fontColor === "#ffffff" ? 'black' : wall.ThemeRule.fontColor,
                    color: wall.ThemeRule.fontColor === "#ffffff" ? 'black' : wall.ThemeRule.fontColor
                }} onClick={this.props.hasMoreData ? this.onClickToDataAppend : null} data-offset="20" data-time="1596187609" data-wall="36512" data-lastid="">{loader.isShowMoreLoader ? <i className="fa fa-spinner fa-spin" aria-hidden="true"></i> :
                    this.props.hasMoreData ? languageSetting.buttonName : languageSetting.no_more
                }</button>
        </div> : null

    }
}


const mapStateToProps = state => {
    return {
        wallId: state.wallId.wallID,
        webFilters: state.webFilters.webFilters,
        wall: state.wall.wallData,
        postData: state.postData,
        languageSetting: state.languageSetting,
        networkId: state.postData.appendData.networkID,
        loader: state.loader,
        hasMoreData: (Object.keys(state.postData.hasMoreData).length && state.postData.appendData.networkID) ? state.postData.hasMoreData[state.postData.appendData.networkID] ? state.postData.hasMoreData[state.postData.appendData.networkID].hasMoreData : true : state.postData.hasMoreData.hasOwnProperty(0) ? state.postData.hasMoreData[0].hasMoreData : true

    }
}
const mapDispatchToProps = dispatch => {
    return {
        getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
        preRenderDataUpdateToParent: (postData) => dispatch(preRenderDataUpdateToParent(postData))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ShowMore);